
import { EventType, SoundType } from "../../configs/EnumType";
import D3Tween from "../../extensionTools/D3Tween";
import CarControlComponent from "../../extensionTools/mods_Nimo/ctrl/CarControlComponent";
import Data from "../../extensionTools/mods_Nimo/Data";
import FxController from "../../extensionTools/mods_Nimo/FxController";
import NimoForFrame from "../../extensionTools/mods_Nimo/NimoForFrame";
import { Nimo_EventType } from "../../extensionTools/mods_Nimo/Nimo_EventType";
import PopupTextTool from "../../extensionTools/PopupTextTool/PopupTextTool";
import GameMgr from "../../managers/GameMgr";
import Game from "../../uiScenes/Game";
import MachineComponent_Player from "../Machine/MachineComponent_Player";
import emojiCoolButton from "../UI/emojiCoolButton";
import emojiCryButton from "../UI/emojiCryButton";
import emojiCryLaughButton from "../UI/emojiCryLaughButton";
import SpeedButton from "../UI/SpeedButton";
import SpeedUI from "../UI/SpeedUI";
import CarComponent, { emojiType } from "./CarComponent";
import CarHitCheck_Player from "./HitCheck/CarHitCheck_Player";




export default class CarComponent_Player extends CarComponent {

    setMaxSpeed = 4.5;

    getControlComp() {
        this.controlComp = CarControlComponent.instance;

    }
    getColliderComp() {
        this.colliderComp = this.user.addComponent(CarHitCheck_Player);

    }
    speedEffect: Laya.ShuriKenParticle3D;
    specialInit() {
        GameMgr.Event.on(Nimo_EventType.GameSpeed1On, this, this.SpeedUp)
        GameMgr.Event.on(Nimo_EventType.GameEmojiCool, this, this.showCoolFace);
        GameMgr.Event.on(Nimo_EventType.GameEmojiCry, this, this.showCryFace);
        GameMgr.Event.on(Nimo_EventType.GameEmojiCryLaugh, this, this.showCryLaughFace);
        this.speedUpStart = this.setMaxSpeed
        this.speedEffect = Data.mainCam.getChildByName("SpeedLine") as Laya.ShuriKenParticle3D;
        // this.speedEffect.active = true;
        this.speedEffect.particleSystem.startSizeConstantMax = 0;
    }
    CarStartSpecial() {
        let carControl: Laya.Image = CarControlComponent.instance.owner as Laya.Image;
        let speed: Laya.Image = SpeedButton.instance.owner as Laya.Image;
        let speedUI: Laya.Image = SpeedUI.instance.owner as Laya.Image;

        carControl.set_visible(false);
        speed.set_visible(false);
        speedUI.set_visible(false);
        let emojiCool: Laya.Image = emojiCoolButton.instance.owner as Laya.Image;
        let emojiCry: Laya.Image = emojiCryButton.instance.owner as Laya.Image;
        let emojiLaughCry: Laya.Image = emojiCryLaughButton.instance.owner as Laya.Image;
        emojiCool.set_visible(false);
        emojiCry.set_visible(false);
        emojiLaughCry.set_visible(false);
        Data.sound.stopLoop(SoundType.CarRuning);
        Data.sound.stopLoop(SoundType.CarEnding)
        GameMgr.Event.event(EventType.AirUI_On);
    }
    gameSpecialEnd() {

        Data.sound.play(SoundType.CarFly);
    }
    specialUpdate() {
        if (this.tracker.speedFactor > this.setMaxSpeed) {
            this.tracker.speedFactor = this.setMaxSpeed;
        }

        let axis = this.controlComp.axis

        if (this.isSpeed && !this.isEnding) {
            axis = 1;
            this.controlComp.axis = 1;
        }
        if (axis > 0) {
            Data.sound.stopLoop(SoundType.CarEnding);
            Data.sound.playLoop(SoundType.CarRuning);
            Data.sound.setSoundVolume(SoundType.CarRuning, axis)
        } else if (axis < 0) {
            Data.sound.stopLoop(SoundType.CarRuning);
            Data.sound.playLoop(SoundType.CarEnding);
            Data.sound.setSoundVolume(SoundType.CarRuning, -axis)
        } else {
            Data.sound.stopLoop(SoundType.CarRuning);
            Data.sound.stopLoop(SoundType.CarEnding)
        }

        let cam: Laya.Camera = Data.mainCam.getChildByName("Main Camera") as Laya.Camera;
        let targetFov = Data.minCamFov + this.tracker.speedFactor * Data.camFovSpeedAxis;
        let nowFov = cam.fieldOfView
        cam.fieldOfView = Laya.MathUtil.lerp(nowFov, targetFov, Data.camLerpAxis);
        //转速表
        GameMgr.Event.event(Nimo_EventType.GameCarSpeedChange, this.tracker.speedFactor)
    }
    CarSpecialChange() {
        this.user.addComponent(MachineComponent_Player);
        this.user.getComponent(MachineComponent_Player).playerId = this.playerId;
        this.user.getComponent(MachineComponent_Player).init(this.skinId);
        Data.sound.stopLoop(SoundType.CarRuning);
        Data.sound.stopLoop(SoundType.CarEnding)
        //速度特效
        Data.mainCam.getChildByName("SpeedLine").active = false;
        NimoForFrame.isPlayerCar = false;
        //摧毁原生广告
        Game.instance.nativeAd.destroy();
    }
    time = 3000;
    cdTime = 8000;
    isSpeed = false;
    speedUpStart;
    SpeedUp() {
        SpeedButton.instance.unActive(this.cdTime);
        if (this.isSpeed)
            return;
        let hitParent = this.carPushEffect;
        FxController.playEffectDestroy("PushEffect", hitParent, this.time) as Laya.ShuriKenParticle3D;
        //速度特效
        this.speedEffect.particleSystem.startSizeConstantMax = 0;
        this.isSpeed = true;
        let tween = new D3Tween();
        let startSpeed = this.speedUpStart;
        let self = this;
        let speedUp = 2;
        let addSpeedUp = Data.carSpeedUpSpeed - Data.carBaseAddSpeed;
        tween.updateTweenCallback = (progress) => {
            self.setMaxSpeed = startSpeed + speedUp * progress;
            self.carAddAxis = Data.carBaseAddSpeed + addSpeedUp * progress;
            self.speedEffect.particleSystem.startSizeConstantMax = progress * 0.35;
        }
        tween.toTween(this.user, {}, this.time * 0.2, () => {
            self.setMaxSpeed = startSpeed + speedUp;
            self.carAddAxis = Data.carSpeedUpSpeed;
            self.speedEffect.particleSystem.startSizeConstantMax = 0.35
            tween.updateTweenCallback = null;
        }).then({}, this.time * 0.6, () => {
            tween.updateTweenCallback = (progress) => {
                self.setMaxSpeed = startSpeed + speedUp - speedUp * progress;
                self.carAddAxis = Data.carSpeedUpSpeed - addSpeedUp * progress;
                self.speedEffect.particleSystem.startSizeConstantMax = 0.35 - 0.35 * progress
            }
        }).then({}, this.time * 0.2, () => {
        })
        Laya.timer.once(this.time, this, this.SpeedEnd);
        //进入CD
    }
    SpeedEnd() {
        //速度特效
        this.speedEffect.particleSystem.startSizeConstantMax = 0;
        this.isSpeed = false;
        this.setMaxSpeed = this.speedUpStart;
    }
    HitSpecial() {
        GameMgr.Platform.vibrateShort();
        Data.camShake.vibrateMoreSoft(Data.mainCam)
        // PopupTextTool.PopText(-100, this.hitTarget.user.transform.position, new Laya.Vector2(0, -100));
    }

    showCoolFace() {
        this.emojiPlay(emojiType.Cool);
    }
    showCryFace() {
        this.emojiPlay(emojiType.Cry);
    }
    showCryLaughFace() {
        this.emojiPlay(emojiType.LaughCry);
    }
}