import { EventType } from "../configs/EnumType";
import { StorageData } from "../configs/StorageData";
import GameMgr from "./GameMgr";

export default class StorageMgr {
    /**缓存数据 */
    storageData: StorageData = new StorageData;
    /**缓存键名 */
    storageName: StorageData = new StorageData;

    constructor() {
        for (let key in this.storageData) {
            let jsonData = this.readStorage(key);
            if (!jsonData) {
                this.writeStorage(key, this.storageName[key]);
                GameMgr.ConsoleLog("初始化缓存数据", key, this.storageName[key]);
            }
            else {
                this.storageData[key] = JSON.parse(jsonData);
                GameMgr.ConsoleLog("加载缓存数据", key, this.storageData[key]);
            }
            this.storageName[key] = key; //数据键名更新
        }
        this.refreshData();
    }


    /**写入缓存 */
    writeStorage(key: any, value: any) {
        Laya.LocalStorage.setItem(key, JSON.stringify(value));
        this.storageData[key] = value;
    }

    /**读取缓存 */
    readStorage(key: any) {
        return Laya.LocalStorage.getItem(key);
    }

    /**移除缓存 */
    removeStorage(key: string) {
        Laya.LocalStorage.removeItem(key);
    }

    /**更新每日数据 */
    private refreshData() {
        var gameTime = Math.floor((this.storageData.freshTime) / 86400000);
        var curTime = Math.floor(new Date().getTime() / 86400000);
        if (gameTime != curTime) { //TODON 测试签到 -86400000
            GameMgr.ConsoleLog("更新每日数据");
            //更新当日游戏次数
            this.writeStorage(this.storageName.playCount, 0);
            //更新刷新时间
            this.writeStorage(this.storageName.freshTime, new Date().getTime());
        }
    }


    /*********** 常用缓存数据 ***********/
    /**重置签到 */
    initSign() {
        this.writeStorage(this.storageName.isSign, false);
        if (this.storageData.signDay > 7) {
            this.writeStorage(this.storageName.signDay, 1);
            this.writeStorage(this.storageName.signWeek, this.storageData.signWeek + 1);
        }
    }

    /**
     * 签到
     * @param callback 回调
     */
    sign(callback?: Function) {
        this.writeStorage(this.storageName.isSign, true);
        this.writeStorage(this.storageName.signDay, this.storageData.signDay + 1);
        callback && callback();
    }

    /**通关 */
    passLevel() {
        this.writeStorage(this.storageName.level, this.storageData.level + 1);
    }

    /**
     * 金币更新
     * @param value 更新值
     * @param callback 回调
     */
    addCoin(value, callback?: Function) {
        this.writeStorage(this.storageName.coin, this.storageData.coin + value);
        GameMgr.ConsoleLog("金币数量", value);
        GameMgr.Event.event(EventType.Update_Coin_Event, { coin: this.storageData.coin });
        callback && callback(this.storageData.coin);
    }

    //type 0 机甲类型
    /**解锁新皮肤 */
    unlockSkin(id, type = 0) {
        if (!this.hasSkin(id, type)) {
            this.storageData.skinList[type].push(id);
            this.writeStorage(this.storageName.skinList, this.storageData.skinList);
            this.changeSkin(id);
        }
    }
    /**是否拥有此皮肤 */
    hasSkin(id, type = 0): boolean {
        return this.storageData.skinList[type].indexOf(id) != -1;
    }
    /**读取现在皮肤 */
    readNowSkin(type = 0) {
        return this.storageData.skinId[type];
    }
    /**更换皮肤 */
    changeSkin(id, type = 0, callback?: Function) {
        this.storageData.skinId[type] = id;
        this.writeStorage(this.storageName.skinId, this.storageData.skinId);
        GameMgr.Event.event(EventType.Update_Skin_Event);
        callback && callback();
    }
}