import { PlatformType } from "../../../configs/EnumType";
import GameMgr from "../../../managers/GameMgr";
import qy_oppo from "./qy_oppo";
import qy_tt from "./qy_tt";
import qy_vivo from "./qy_vivo";
import qy_wx from "./qy_wx";

export default class qyMgr {
    static init() {
        switch (GameMgr.GlData.platform) {
            case PlatformType.WX:
                qy_wx.init();
                GameMgr.GlData.isSubPackage = true;
                break;

            case PlatformType.TickTok:
                qy_tt.init();
                GameMgr.GlData.isSubPackage = true;
                break;

            case PlatformType.OPPO:
                qy_oppo.init();
                GameMgr.GlData.isSubPackage = false;
                break;

            case PlatformType.VIVO:
                qy_vivo.init();
                GameMgr.GlData.isSubPackage = true;
                break;

            case PlatformType.PC:
                qy_tt.init();
                GameMgr.GlData.isSubPackage = false;
                break;
        }
    }
}

/**轻游后台配置参数 */
export class DataConfig {
    /**原生广告本地刷新时间-秒 */
    nativeAdRefreshTime: number = 30;
    /**初次进游戏延迟显示原生广告时间-秒 */
    showNativeDelayTime: number = 60;
    /**是否显示游戏中原生广告 */
    isShowGameNativeAd: boolean = false;
    /**是否显示插屏原生广告 */
    isShowInterNativeAd: boolean = false;
    /**是否开启调试 */
    isDebug: boolean = false;
    /**游戏中原生广告关闭后延迟重新展示时间 */
    nativeAdGameReActiveTime = 5;
    /**版本控制 -1.0.0 */
    v100: any = {}
}
