import { PlatformType } from "../../configs/EnumType";
import GameMgr from "../../managers/GameMgr";
import ExtensionTool from "../ExtensionTool";
import Data from "../mods_Nimo/Data";
import Tools from "../mods_Nimo/Tools";
import BitMapFntTool from "./BitMapFntTool";

/* 
飘字工具
*/
export default class PopupTextTool {
    public static MoveTime = 1500;
    public static ScaleTime = 400;
    public static MoveOffSet = 800;
    public static EaseType = Laya.Ease.quartInOut;
    public static AlphaSpeed = 0.02;
    public static ScaleEaseType = Laya.Ease.cubicOut;

    //创建
    public static CreateText(pos: Laya.Vector2, str: string, fontSize: number, color: string, font?, scale?) {
        var text = new Laya.Text();
        text.text = str;
        text.x = pos.x;
        text.y = pos.y;
        text.fontSize = fontSize;
        text.color = color;
        text.bold = true;
        if (font)
            text.font = font;
        if (scale)
            text.scaleX = scale;
        if (scale)
            text.scaleY = scale;
        Laya.Tween.to(text, { y: pos.y - PopupTextTool.MoveOffSet, update: new Laya.Handler(this, PopupTextTool.OnMovingUpdate, [text]) }, PopupTextTool.MoveTime, PopupTextTool.EaseType, Laya.Handler.create(this, PopupTextTool.OnFinishMove, [text]), 0);
        Laya.stage.addChild(text);
        Laya.timer.once(this.MoveTime, this, () => { text.destroy() });
    }

    //创建
    public static ScaleText(pos: Laya.Vector2, str: string, fontSize: number, color: string, font?, isleft?) {
        var text = new Laya.Text();
        text.text = str;
        text.x = pos.x;
        text.y = pos.y;
        text.pivotX = 0.5;
        text.pivotY = 0.5;
        text.fontSize = fontSize;
        text.color = color;
        text.font = font;
        //text.scaleX = 0;
        //text.scaleY = 0;

        //if (isleft)
        Laya.Tween.to(text, { y: pos.y + PopupTextTool.MoveOffSet * 40, update: new Laya.Handler(this, null, [text]) }, PopupTextTool.MoveTime * 2, PopupTextTool.EaseType, null, 0);
        // else
        //     Laya.Tween.to(text, { x: pos.y - PopupTextTool.MoveOffSet*40, update: new Laya.Handler(this, null, [text]) }, PopupTextTool.MoveTime*2, PopupTextTool.EaseType, null, 0);

        Laya.Tween.to(text, { scaleX: 1.4, scaleY: 1.4, update: new Laya.Handler(this, null, [text]) }, PopupTextTool.ScaleTime * 1.5, PopupTextTool.ScaleEaseType, Laya.Handler.create(this, PopupTextTool.OnFinishMove, [text]), 0);
        Laya.stage.addChild(text);
    }


    //移动过程帧循环
    private static OnMovingUpdate(txt: Laya.Text) {
        txt.alpha -= PopupTextTool.AlphaSpeed;
    }

    //移动完成回调
    private static OnFinishMove(txt: Laya.Text) {
        txt.removeSelf();
    }

    public static PopText(value: number, typeId, pos: Laya.Vector3, offset: Laya.Vector2 = new Laya.Vector2()) {
        let cam = Data.mainCam.getChildAt(0) as Laya.Camera;
        var outPopPos = new Laya.Vector4();
        cam.viewport.project(pos, cam.projectionViewMatrix, outPopPos);
        // if (GameMgr.GlData.platform == PlatformType.OPPO) {
        //     offset.x -= 100;
        // }
        var popPos = new Laya.Vector2((outPopPos.x + offset.x) / Laya.stage.clientScaleX, (outPopPos.y + offset.y) / Laya.stage.clientScaleY);
        // var popPos = new Laya.Vector2((outPopPos.x - Laya.stage.x + offset.x), outPopPos.y - Laya.stage.y + offset.y);
        let scale = 0.8;
        if (typeId == 1) {
            scale = 1;
        }
        PopupTextTool.CreateText(popPos, value.toString(), 75, "#ffffff", BitMapFntTool.fntName[typeId], scale);
        // else
        //     PopupTextTool.CreateText(popPos, Randvalue.toString(), 100, "#ffffff", BitMapFntTool.fntName[1], 0.8);
    }

}