    /**游戏加载--进入加载页调用 */
    static loadGame(cb?) {
        var closeVideo = () => {
            // console.log("关闭首次进入视频")
            this.showReMen(cb);

            if (this.loadScene) {
                this.loadScene.destroy();
                this.loadScene = null;
            }
        }

        if (this.canTrapAll) {
            FdAd.showVideoAd(closeVideo, closeVideo);
        }
        else {
            closeVideo();
        }
    }

    /**热门推荐 */
    static showReMen(cb?) {
        if (this.canTrapRemen) {
            Laya.Scene.open(SceneType.Remen, false, null, Laya.Handler.create(this, s => {
                Laya.stage.addChild(s);
                s.size(Laya.stage.width, Laya.stage.height);

                if (!FdAd.showGridAD()) {
                    FdAd.initGridAD(() => { FdAd.showGridAD(); }, (Laya.Browser.clientWidth) * 0.5 - 200, 0);
                }

                this.bannerShowHide();
                FdAd.bannerIndex = 0;

                let btnContinue: Laya.Button = s.btnContinue;
                btnContinue.offAllCaller(this);
                btnContinue.on(Laya.Event.CLICK, this, () => {
                    Laya.timer.clearAll(this);
                    Laya.Scene.close(SceneType.Remen);
                    FdAd.hideGridAD();
                    FdAd.showVideoAd();
                    this.showBox2(cb, true);
                    this.refreshBanner();
                });
            }));
        }
        else {
            cb && cb();
        }
    }

    /**banner闪烁 */
    static bannerShowHide() {
        FdAd.hideNextBanner();
        Laya.timer.once(1000, this, () => {
            FdAd.showNextBanner();
            Laya.timer.once(800, this, () => {
                this.bannerShowHide();
            });
        });
    }

