    //网络请求
    public static httpRequest(url: string, params: any, type: string = 'get', completeHandler?: Function, errorHandler?: Function) {
        var xhr: Laya.HttpRequest = new Laya.HttpRequest();
        xhr.http.timeout = 5000;//设置超时时间；
        xhr.once(Laya.Event.COMPLETE, this, completeHandler);
        // xhr.once(Laya.Event.ERROR, this, this.httpRequest, [url, params, type, completeHandler]);
        xhr.once(Laya.Event.ERROR, this, errorHandler, [url, params, type, completeHandler]);
        if (type == "get") {
            xhr.send(url + '?' + params, "", type, "text");
        } else if (type == "post") {
            xhr.send(url, JSON.stringify(params), type, "text");
        }
    }

    static getConfigByKey(key) {
        if (Laya.Browser.onWeiXin) {
            return Laya.Browser.window.wxsdk.conf[key];
        }
        else {
            return true;
        }
    }

    static get canTrapAll() {
        return this.allowScene && !this.getConfigByKey("isCloseAll");
    }

    static get canTrapVirtualWx() {
        return this.canTrapAll && this.getConfigByKey("showVitualWx");
    }

    static get canTrapBox() {
        return this.canTrapAll && this.getConfigByKey("showBox");
    }

    static get canTrapRemen() {
        return this.canTrapAll && this.getConfigByKey("showRemen");
    }

