export default class TraceCircuit extends Laya.Script3D {
    private smoothRoute: boolean = true;
    private numPoints: number;
    private points: Laya.Vector3[];

    /**点距离 */
    private distances: number[];
    public accumulateDistance: number;
    public substeps: number = 100;

    private _length: number;
    /**跑道长度 */
    get length() {
        return this._length;
    }
    set length(value) {
        this._length = value;
    }

    allPointVec3: Laya.Vector3[];

    p0n: number = 0;
    p1n: number = 0;
    p2n: number = 0;
    p3n: number = 0;

    i: number = 0;

    p0: Laya.Vector3 = new Laya.Vector3();
    p1: Laya.Vector3 = new Laya.Vector3();
    p2: Laya.Vector3 = new Laya.Vector3();
    p3: Laya.Vector3 = new Laya.Vector3();

    v3Add(a: Laya.Vector3, b: Laya.Vector3): Laya.Vector3 {
        var v = new Laya.Vector3();
        Laya.Vector3.add(a, b, v);

        return v;
    }

    v3Subtract(a: Laya.Vector3, b: Laya.Vector3): Laya.Vector3 {
        var v = new Laya.Vector3();
        Laya.Vector3.subtract(a, b, v);

        return v;
    }

    v3Scale(a: Laya.Vector3, b: number): Laya.Vector3 {
        var v = new Laya.Vector3();
        Laya.Vector3.scale(a, b, v);

        return v;
    }

    cachePositionsAndDistances() {
        this.points = [];
        this.distances = [];
        var accumulateDistance = 0;

        for (var i = 0; i < this.allPointVec3.length; i++) {//+1
            var t1 = this.allPointVec3[i % this.allPointVec3.length];
            var t2 = this.allPointVec3[(i + 1) % this.allPointVec3.length];

            if (t1 && t2) {
                var p1 = t1
                var p2 = t2

                this.points[i] = this.allPointVec3[i % this.allPointVec3.length];
                this.distances[i] = accumulateDistance;

                var p = new Laya.Vector3();
                Laya.Vector3.subtract(p1, p2, p);

                if (i < this.allPointVec3.length - 1) {
                    accumulateDistance += Laya.Vector3.scalarLength(p);
                }
            }
        }

        this.accumulateDistance = accumulateDistance; //实际距离 = 跑道总距离 - 单节长度
    }

    getUpDirection(dist): Laya.Vector3 {
        return new Laya.Vector3(0, 1, 0);
    }


    getRoutePoint(dist) {
        var p1 = this.getRountePosition(dist);
        var p2 = this.getRountePosition(dist + 0.1); //+ 0.1

        var delta = new Laya.Vector3();
        var v = this.v3Subtract(p1, p2);
        Laya.Vector3.normalize(v, delta);
        return new RoutePoint(p1, delta);
    }



    getRountePosition(dist): Laya.Vector3 {
        var point = 0;
        this.length = this.distances[this.distances.length - 1];

        dist %= this.length;

        while (this.distances[point] < dist) {
            ++point;
        }

        this.p1n = (point - 1 + this.numPoints) % this.numPoints;
        this.p2n = point;

        this.i = this.inverseLerp(this.distances[this.p1n], this.distances[this.p2n], dist);

        if (this.smoothRoute) {
            this.p0n = (point - 2 + this.numPoints) % this.numPoints;
            this.p3n = (point + 1) % this.numPoints;

            this.p2n %= this.numPoints;

            this.p0 = this.points[this.p0n];
            this.p1 = this.points[this.p1n];
            this.p2 = this.points[this.p2n];
            this.p3 = this.points[this.p3n];

            return this.catmullRom(this.p0, this.p1, this.p2, this.p3, this.i);
        }
        else {
            this.p1n = (point - 1 + this.numPoints) % this.numPoints;
            this.p2n = point;

            var v = new Laya.Vector3();
            Laya.Vector3.lerp(this.points[this.p1n], this.points[this.p2n], this.i, v);
            return v;
        }
    }

    catmullRom(p0, p1, p2, p3, i): Laya.Vector3 {
        // return 0.5 * ((2 * p1) + (-p0 + p2) * i + (2 * p0 - 5 * p1 + 4 * p2 - p3) * i * i + (-p0 + 3 * p1 - 3 * p2 + p3) * i * i * i);
        var v1 = this.v3Scale(this.p1, 2); // ((2 * p1)
        var v2 = this.v3Scale(this.v3Subtract(this.p2, this.p0), this.i); //(-p0 + p2) * i

        var v3_1 = this.v3Scale(this.p0, 2);
        var v3_2 = this.v3Scale(this.p1, 5);
        var v3_3 = this.v3Scale(this.p2, 4);
        var v3_4 = this.v3Add(this.v3Subtract(v3_1, v3_2), v3_3);
        var v3_4 = this.v3Subtract(v3_4, this.p3);
        var v3 = this.v3Scale(v3_4, Math.pow(this.i, 2)); // (2 * p0 - 5 * p1 + 4 * p2 - p3) * i * i

        var v4_1 = this.v3Scale(this.p1, 3);
        var v4_2 = this.v3Scale(this.p2, 3);
        var v4_3 = this.v3Subtract(v4_1, this.p0);
        v4_3 = this.v3Subtract(v4_3, v4_2);
        v4_3 = this.v3Add(v4_3, this.p3);
        var v4 = this.v3Scale(v4_3, Math.pow(this.i, 3));  //(-p0 + 3 * p1 - 3 * p2 + p3) * i * i * i

        var v = this.v3Scale(this.v3Add(this.v3Add(this.v3Add(v1, v2), v3), v4), 0.5);
        return v;
    }

    assignUsingAllChildObjects() {
        this.allPointVec3 = [];

        var self = this.owner as Laya.Sprite3D;
        // for (var i = self.numChildren - 1; i > 0; i--) {
        //     this.allPointVec3.push((self.getChildAt(i) as Laya.Sprite3D).transform.position.clone());
        // }
        for (var i = 0; i < self.numChildren; i++) {
            this.allPointVec3.push((self.getChildAt(i) as Laya.Sprite3D).transform.position.clone());
        }
        self.destroyChildren();
    }

    init() {
        this.assignUsingAllChildObjects();
        if (this.allPointVec3.length > 1) {
            this.cachePositionsAndDistances();
        }
        this.numPoints = this.allPointVec3.length;
    }

    /**反向插值 */
    inverseLerp(a, b, v) {
        return (v - a) / (b - a);
    }

}

class RoutePoint {
    public position: Laya.Vector3;
    public direction: Laya.Vector3;

    constructor(position, direction) {
        this.position = position;
        this.direction = direction;
    }
}


