import GameConfig from "../../GameConfig";
import Platform from "../Platform";

/**微信平台 */
export default class WxPlatform extends Platform {
    /******************** 广告 ********************/
    initAd() {
        this.canSubPackage = true;
        this.platformApi = Laya.Browser.window.wx;

        this.bannerId = 'adunit-13956b06e4d79f94';
        this.videoId = 'adunit-38c16dc5bb35fd3b';
        this.interId = 'adunit-6add71646b81ad4a';
        this.gridId = 'adunit-18242cdd0286c125';

        // this.createBannerAd();
        // this.createVideoAd();
        // this.createBoxProtalAd();

        this.showShareMenuWithTicket(true);
        this.regisiterWXShareCallback();
    }

    //#region Banner广告
    createBannerAd(isShow: boolean = false) {
        if (this.compareSDKVersion("2.0.4")) {
            console.warn("版本过低暂不支持Banner广告组件")
            return;
        }

        if (this.bannerAd) {
            this.destroyBannerAd();
        }

        let sysInfo = this.getSystemInfoSync();
        this.bannerAd = Laya.Browser.window.wx.createBannerAd({
            adUnitId: this.bannerId,
            style: {
                top: sysInfo.screenHeight * 0.8,
                width: 300,
                left: sysInfo.screenWidth / 2 - 150
            },
            adIntervals: 30
        });

        this.bannerAd.onLoad(() => {
            if (isShow) {
                this.bannerAd.show().then(() => { }).catch(err => {
                    this.createBannerAd();
                });
            }
            console.log("Banner广告加载成功");
        });

        this.bannerAd.onError(err => {
            this.bannerAd = null;
            console.error("Banner广告加载失败", JSON.stringify(err));
        });

        this.bannerAd.onResize(res => {
            let realHeight = this.bannerAd.style.realHeight + 0.1;
            if (sysInfo.model.startsWith("iPhone X")) realHeight += (40 / Laya.Browser.height) * GameConfig.height;
            this.bannerAd.style.top = sysInfo.screenHeight - realHeight;
        });
    }

    showBannerAd() {
        if (!this.bannerAd) {
            this.createBannerAd(true);
            return;
        }

        this.bannerAd.show().then(() => { }).catch((err) => {
            this.createBannerAd(true);
        });
    }

    hideBannerAd() {
        if (!this.bannerAd) return;
        this.bannerAd.hide();
    }

    destroyBannerAd() {
        if (!this.bannerAd) return;
        this.bannerAd.destroy();
        this.bannerAd = null;
    }
    //#endregion

    //#region 激励视频广告
    createVideoAd() {
        if (this.compareSDKVersion("2.0.4")) {
            console.warn("版本过低暂不支持激励视频广告组件")
            return;
        }

        if (this.videoAd) {
            this.destroyVideoAd();
        }

        this.videoAd = Laya.Browser.window.wx.createRewardedVideoAd({
            adUnitId: this.videoId
        });

        this.videoAd.onLoad(res => {
            console.log("激励视频广告加载成功");
        });

        this.videoAd.onError(err => {
            this.videoAd = null;
            console.error("激励视频广告加载失败", JSON.stringify(err));
        });

        this.videoAd.onClose((res) => {
            if (res) {
                if (res.isEnded) {
                    this.videoFinishCallback && this.videoFinishCallback();
                }
                else {
                    this.videoCancelCallback && this.videoCancelCallback();
                }

                this.videoFinishCallback = null;
                this.videoCancelCallback = null;
                this.videoAd.load();
            }
        })
    }

    showVideoAd(finishCB?: Function, cancelCB?: Function) {
        if (!this.videoAd) {
            this.createVideoAd();
            finishCB && finishCB();
            return;
        }

        this.videoFinishCallback = finishCB;
        this.videoCancelCallback = cancelCB;

        //vivo播放视频需要中断游戏音效
        Laya.SoundManager.muted = true;
        this.videoAd.show().then(() => { }).catch(err => {
            finishCB && finishCB();
            this.createVideoAd();
            this.videoFinishCallback = null;
            this.videoCancelCallback = null;
            //恢复游戏音效
            Laya.SoundManager.muted = false;
        })
    }

    destroyVideoAd() {
        if (!this.videoAd) return;
        this.videoAd.destroy();
        this.videoAd = null;
    }
    //#endregion

    /******************** 平台API ********************/
    /**当前平台版本号 */
    get getPlatformVersionCode() {
        return this.getSystemInfoSync().SDKVersion;
    }
    /**比对当前版本是否支持广告组件 */
    compareSDKVersion(version: string = "1.0.0"): boolean {
        let sdkVersion: string = this.getPlatformVersionCode; //当前版本
        let arr1 = sdkVersion.split('.');
        let arr2 = version.split('.');

        let isMismatch = false; //默认版本支持
        for (let i = 0; i < arr1.length; i++) {
            if (arr1[i] > arr2[i]) break;
            else if (arr1[i] == arr2[i]) continue;
            else { isMismatch = true; } //版本不支持
        }

        return isMismatch;
    }

    /**
     * 弹窗
     * @param msg 文本
     * @param duration 提示的延迟时间
     */
    showToast(msg: string, duration: number = 1500) {
        Laya.Browser.window.wx.showToast({
            title: msg,//提示文字
            duration: duration,//显示时长
        })
    }

    /**显示右上角转发分享功能 */
    showShareMenuWithTicket(ticket) {
        wx.showShareMenu({
            withShareTicket: ticket,
            success: function () { },
            fail: function () { },
            complete: function () { }
        })
    }

    /**注册右上角转发分享监听 */
    regisiterWXShareCallback() {
        var shareId = 1;
        if (Laya.Browser.onWeiXin) {
            var object = { title: "分享", image: "" };
            Laya.Browser.window.wx.onShareAppMessage(function () {
                console.log("用户点击了转发按钮");
                let shareObj = {
                    title: object.title,
                    imageUrl: object.image,
                    query: "share_id=" + shareId,
                }
                return shareObj;
            })
        }
    }
}