import { EventType, UiSceneType } from "../configs/EnumType";
import D2Tween from "../extensionTools/D2Tween";
import GameMgr from "../managers/GameMgr";
import FdMgr from "../platforms/cpLogicalMgr/FanDong/fdMgr";
import UiScene from "./UiScene";
/**加载页 */
export default class Load extends UiScene {
    /**公司logo底图 */
    private plCompanyLogoBg: Laya.Panel;
    /**公司logo */
    private imgCompanyLogo: Laya.Image;
    /**加载进度条 */
    private barLoadPgs: Laya.ProgressBar
    /**加载信息 */
    private lbLoadMsg: Laya.Label;
    /**记录当前'.'数量 */
    private curPointCount: number;
    /**'.'计时器 */
    private changePointTimer: number
    /**健康游戏忠告底图 */
    private warning: Laya.Panel;
    /**健康游戏忠告文字 */
    private warningWord: Laya.Box;
    init() {
        // this.showWarning()
        this.warning.visible = false;
        // this.showCompanyLogo();
        this.plCompanyLogoBg.visible = false;
        this.showLoading();
    }

    showWarning() {
        D2Tween.toAlpha(this.warningWord, 0, 1, 200, null, () => {
            Laya.timer.once(500, this, () => {
                D2Tween.toAlpha(this.warningWord, 1, 0, 500, null, () => {
                    this.warning.visible = false;
                    this.showCompanyLogo();
                })
            })
        });
    }

    showCompanyLogo() {
        this.plCompanyLogoBg.visible = true;
        D2Tween.toAlpha(this.imgCompanyLogo, 0, 1, 200, null, () => {
            Laya.timer.once(500, this, () => {
                D2Tween.toAlpha(this.imgCompanyLogo, 1, 0, 500, null, () => {
                    this.plCompanyLogoBg.visible = false;
                    this.showLoading();
                })
            })
        });
    }

    showLoading() {

        //监听读条
        GameMgr.Event.on(EventType.Load_Res_Msg_Event, this, this.loadResMsgEvent);

        //初始化读条
        if (GameMgr.Res.resList.length == 0) {
            this.loadResMsgEvent({ allComplete: true });
        }
        else {
            let type = GameMgr.Res.currentLoadType;
            this.loadResMsgEvent({ type: type, progress: 0, isComplete: false })

            this.changePointTimer = 0;
            this.curPointCount = 3;
            Laya.timer.frameLoop(1, this, this.frameLoadProgress)
        }
    }

    /**
     * 加载资源事件监听
     * @param e e.type：类型、 e.progress：进度、 e.isComplete：完成
     * 3d资源、图集、音效、预制体、缓存数据、分包
     */
    loadResMsgEvent(e) {
        if (e.allComplete) {
            this.lbLoadMsg.text = "资源加载完成";
            this.barLoadPgs.value = 1;
            Laya.timer.clear(this, this.frameLoadProgress);
            //TODON 加载首页（事件/ui管理器调用）
            GameMgr.UI.closeScene(UiSceneType.Load);
            GameMgr.Event.event(EventType.Init_Game_Event, "onLoad");
        }
        else {
            if (e.type != GameMgr.Res.currentLoadType) return;
            let msg = this.lbLoadMsg.text;

            let str = '';
            for (let i = 0; i < this.curPointCount; i++) {
                str += '.';
            }

            msg = e.type + "资源加载中" + str;
            this.lbLoadMsg.text = msg;

            if (e.isComplete) {
                this.barLoadPgs.value = 1;
            }
            else if (e.progress == 0) {
                this.barLoadPgs.value = 0;
            }
            else {
                this.barLoadPgs.value = Math.max(e.progress, this.barLoadPgs.value);
            }
        }
    }

    frameLoadProgress() {
        this.changePointTimer += Laya.timer.delta;
        if (this.changePointTimer > 200) {
            this.changePointTimer = 0;
            let oldStr = '';
            for (let i = 0; i < this.curPointCount; i++) {
                oldStr += '.';
            }
            this.curPointCount++;
            if (this.curPointCount > 3) {
                this.curPointCount = 1;
            }
            let str = '';
            for (let i = 0; i < this.curPointCount; i++) {
                str += '.';
            }

            let msg = this.lbLoadMsg.text;
            msg = msg.replace(oldStr, str);
            this.lbLoadMsg.text = msg;
        }

        this.barLoadPgs.value += 0.01;
    }
}