import Data from "../../../extensionTools/mods_Nimo/Data";
import NimoForFrame from "../../../extensionTools/mods_Nimo/NimoForFrame";
import Tools from "../../../extensionTools/mods_Nimo/Tools";
import GameMgr from "../../../managers/GameMgr";
import MachineComponent from "../MachineComponent";

export default class MachineHitCheck extends Laya.Script3D {

    machine: MachineComponent;
    user: Laya.Sprite3D;

    axis = 0;
    horizontal = 0;
    radius = 3;

    onEnable() {
        this.user = this.owner as Laya.Sprite3D;
        this.machine = this.user.getComponent(MachineComponent);
        this.init();
    }

    onDisable() {

    }

    init() {
        this.axis = this.user.transform.position.z;
        this.horizontal = this.user.transform.position.x;
    }

    onLateUpdate() {
        this.axis = this.user.transform.position.z;
        this.horizontal = this.user.transform.position.x;
        this.machineHitCheck();
        this.blcokHitCheck();
        this.pickHitCheck();
    }

    machineHitCheck() {

    }
    blcokHitCheck() {
        let mainPoint = NimoForFrame.stageRobot.getChildByName("Station_1").transform.position.clone();
        mainPoint.y = 0;
        let raidus = 30 - this.radius;
        let selfPoint = this.user.transform.position.clone();
        let dir = Tools.Vector3Reduce(selfPoint, mainPoint)
        let distance = Laya.Vector3.scalarLength(dir);
        if (distance > raidus) {
            let way = new Laya.Vector3();
            Laya.Vector3.normalize(dir, way);
            let goDir = Tools.Vector3Axis(way, raidus);
            let goPos = Tools.Vector3Add(mainPoint, goDir);
            this.user.transform.position = goPos;
        }
    }
    pickHitCheck() {
        let itemPick = NimoForFrame.pickItems;
        //道具拾取
        for (let i = 0; i < itemPick.length; i++) {
            if (itemPick[i].active) {
                let axis = itemPick[i].transform.position.z;
                let horizontal = itemPick[i].transform.position.x;
                let raidus = 2;
                let hitEvent = Tools.CircleCheck_2D(axis, horizontal, raidus, this.axis, this.horizontal, this.radius);
                if (hitEvent.isHit) {
                    itemPick[i].active = false;
                    // this.machine.getWeapon(i + 1);
                    GameMgr.ConsoleLog("拾取道具", i + 1);
                    if (i == 1) {
                        this.machine.heal();
                    }
                    if (i == 0) {
                        this.machine.attackUp();
                    }
                }
            }
        }
        if (this.machine.isGotWeapon)
            return;
        let weaponPick = NimoForFrame.pickWeapons;

        //武器获取
        for (let i = 0; i < weaponPick.length; i++) {
            if (weaponPick[i].active) {
                let axis = weaponPick[i].transform.position.z;
                let horizontal = weaponPick[i].transform.position.x;
                let raidus = 2;
                let hitEvent = Tools.CircleCheck_2D(axis, horizontal, raidus, this.axis, this.horizontal, this.radius);
                if (hitEvent.isHit) {
                    weaponPick[i].active = false;
                    this.machine.getWeapon(i + 1);
                    break;
                }
            }
        }

    }
}