import { EventType, PlatformType } from "../../../../configs/EnumType";
import D2Component from "../../../../d2Components/D2Component";
import ExtensionTool from "../../../../extensionTools/ExtensionTool";
import NimoForFrame from "../../../../extensionTools/mods_Nimo/NimoForFrame";
import GameMgr from "../../../../managers/GameMgr";
import { NativeAd } from "../../../Platform";
import { KolaSDK } from "../IKolaSDK";
import qy_oppo from "../qy_oppo";
import qy_vivo from "../qy_vivo";

export default class NativeAdComponent extends D2Component {
    /** @prop {name:triggerPos,tips:"触发位置",type:Option,option:"Home,Game,Over,Inter", default: "Home"}*/
    triggerPos: string = "Home";

    /** @prop {name:isEnable,tips:"是否启用",type:Bool, default: true}*/
    isEnable: boolean = true;

    imgAd: Laya.Image;
    btnClose: Laya.Image;
    btnCheck: Laya.Image;
    imgClick: Laya.Image;

    lbTitle: Laya.Label;
    lbDesc: Laya.Label;

    nativeAd: NativeAd;

    qy: any;

    init() {
        console.log("原生广告组件==============");
        this.self.visible = false;
        GameMgr.Event.event(EventType.Native_Visible_Event, false);
        if (!this.isEnable) return;
        switch (GameMgr.GlData.platform) {
            case PlatformType.OPPO: this.qy = qy_oppo; break;
            case PlatformType.VIVO: this.qy = qy_vivo; break;
            case PlatformType.PC: return;
        }
        GameMgr.Event.on("Update_NativeAd_Event", this, this.updateData);
        if (this.triggerPos == "Game") {
            // Laya.timer.once(5000, this, this.updateData);
        } else {
            this.updateData();
        }
    }

    canShow: boolean = true;
    updateData(args?) {
        this.self.visible = false;
        GameMgr.Event.event(EventType.Native_Visible_Event, false);
        if (args) {
            this.canShow = !args.isClose
        }

        if (!this.canShow) return;
        if (!this.qy || !this.qy.canShowNativeAd) return;
        if (this.triggerPos == "Game" && !this.qy.dataConfig.isShowGameNativeAd)
        {
            console.log("后台配置不允许游戏中原生广告显示，暂不显示")
            return;
        } 
        if (this.triggerPos == "Game" && !NimoForFrame.realStart) 
        {
            console.log("游戏还未正式开始，暂不显示")
            return
        };

        // GameMgr.ConsoleLog("原生广告更新", !this.canShow, !this.qy || !this.qy.canShowNativeAd, this.triggerPos == "Game" && !this.qy.dataConfig.isShowGameNativeAd)
        this.nativeAd = this.qy.nativeAd;
        GameMgr.ConsoleLog("是否有原生广告", this.nativeAd)
        if (this.nativeAd) {
            let ad = this.nativeAd;
            console.log("显示原生广告", JSON.stringify(ad));
            this.self.visible = true;
            GameMgr.Event.event(EventType.Native_Visible_Event, true);
            this.self.zOrder = 10;
            this.imgAd = this.self.getChildByName("imgAd") as Laya.Image;
            this.btnClose = this.self.getChildByName("btnClose") as Laya.Image;
            this.btnCheck = this.self.getChildByName("btnCheck") as Laya.Image;
            this.lbTitle = this.self.getChildByName("lbTitle") as Laya.Label;
            this.lbDesc = this.self.getChildByName("lbDesc") as Laya.Label;
            this.imgClick = this.self.getChildByName("imgClick") as Laya.Image;

            this.imgAd.skin = ad.imgUrl;
            this.lbTitle.text = ad.title;
            this.lbDesc.text = ad.desc;

            ExtensionTool.d2_AddClickEvent(this.btnClose, this, this.closeAd);
            ExtensionTool.d2_AddClickEvent(this.btnCheck, this, this.clickAd);
            ExtensionTool.d2_AddClickEvent(this.imgClick, this, this.clickAd, null, false, true, false);

            KolaSDK.OnNativeAdShow(this.triggerPos, "1");
        }
    }

    clickAd() {
        GameMgr.Platform.showNativeAd(this.nativeAd, (isShow) => {
            if (isShow) {
                //插屏原生广告不需要刷新显示
                if (this.triggerPos == "Inter") {
                    console.log("移除监听更新组件==========");
                    GameMgr.Event.off("Update_NativeAd_Event", this, this.updateData);
                }
                //重新刷新
                this.qy.loopCreateNativeAd(true);
                this.self.visible = false;
            }
        });
        KolaSDK.OnNativeAdClick(this.triggerPos, "1");
    }

    closeAd() {
        if (this.triggerPos != "Home") {
            GameMgr.Event.off("Update_NativeAd_Event", this, this.updateData);
        }
        this.self.visible = false;
        GameMgr.Event.event(EventType.Native_Visible_Event, false);
        if (this.triggerPos == "Game") {
            if (this.qy.dataConfig && this.qy.dataConfig.nativeAdGameReActiveTime && this.qy.dataConfig.nativeAdGameReActiveTime >= 0) {
                Laya.timer.once(this.qy.dataConfig.nativeAdGameReActiveTime * 1000, this, () => {
                    this.init();
                    this.canShow = true;
                    this.updateData();
                })
            }
        }
        if (this.triggerPos == "Inter") {
            GameMgr.Event.event("Update_NativeAd_Event", { isClose: false })
        }
    }

    onDisable() {
        console.log("移除原生广告组件");
        GameMgr.Event.event(EventType.Native_Visible_Event, false);
        Laya.timer.clearAll(this);
        GameMgr.Event.offAllCaller(this);
    }
}