import { SoundType } from "../configs/EnumType"
import SoundMgr from "../managers/SoundMgr"
import Data from "./mods_Nimo/Data"

export default class ExtensionTool {
    //#region File
    /**
     * 遍历Scene所有Var值及类型 输出
     * @param jsonDir Scene.json路径 example:'MyScenes/LvMoneyUI.json'
     * example out:
     * 遍历Var开始：
       xxx:Laya.Image
       xxx:Laya.List
       遍历Var结束！
     */
    static file_ExportVarByUIScene(jsonDir: string) {
        Laya.loader.load(jsonDir, Laya.Handler.create(this, (data: any) => {
            let str: string = '遍历Var开始：\n'
            let checkVar = (node: any) => {
                if (node && node.props && node.props.var != undefined && node.props.var != null) {
                    let type = node.type
                    let v = node.props.var
                    str += v + ':Laya.' + type + '\n'
                }
            }
            let checkChild = (node: any) => {
                let child: any[] = node.child ? node.child : []
                for (let i = 0; i < child.length; i++) {
                    let c: any = child[i]
                    checkVar(c)
                    if (c.child) checkChild(c)
                }
            }
            checkChild(data)
            str += '遍历Var结束！'
            console.log(str)
        }), null, Laya.Loader.JSON)
    }

    /**
     * 保存字符串内容到文件。效果相当于从浏览器下载了一个文件到本地。
     * @param textToWrite 要保存的文件内容
     * @param fileNameToSaveAs 要保存的文件名
     */
    static file_SaveForBrowser(textToWrite, fileNameToSaveAs) {
        console.log("浏览器");
        let textFileAsBlob = new Blob([textToWrite], { type: 'application/json' });
        let downloadLink = document.createElement("a");
        downloadLink.download = fileNameToSaveAs;
        downloadLink.innerHTML = "Download File";
        if (window.webkitURL != null) {
            downloadLink.href = window.webkitURL.createObjectURL(textFileAsBlob);
        }
        downloadLink.click();
    }

    /**
     * 读取json文件
     * @param jsonUrl json路径
     * @param completeCB 加载完成回调
     */
    static file_LoadJson(jsonUrl: string, completeCB?: Function) {
        Laya.loader.load(jsonUrl, Laya.Handler.create(this, () => {
            let data = Laya.Loader.getRes(jsonUrl);
            completeCB && completeCB(data);
        }), null, Laya.Loader.JSON);
    }
    //#endregion

    //#region 3D
    /**
     * 遍历rootNode的子节点，查找name匹配的节点
     * @param rootNode 根节点
     * @param name 需要查找的节点的name
     */
    static d3_FindNodeByName(rootNode: Laya.Sprite3D, name: string): Laya.Sprite3D {
        let targetNode: Laya.Sprite3D = null

        let funC = (node: Laya.Sprite3D) => {
            for (let i = 0; i < node.numChildren; i++) {
                if (node.getChildAt(i).name == name) {
                    targetNode = node.getChildAt(i) as Laya.Sprite3D
                    return
                } else {
                    funC(node.getChildAt(i) as Laya.Sprite3D)
                }
            }
        }

        funC(rootNode)
        return targetNode
    }

    /**
     * 随机获取半径范围内坐标点
     * @param maxRadius 最大半径范围
     * @param centerPos 中心点
     * @param minRadius 最小半径范围
     * @returns 
     */
    static d3_GetRandomPointInCircle(maxRadius: number, centerPos: Laya.Vector3, minRadius: number = 0) {
        let dir = new Laya.Vector3()
        let r = Math.random() * (maxRadius - minRadius) + minRadius

        dir.x = Math.random() * 2 - 1
        dir.z = Math.random() * 2 - 1
        Laya.Vector3.normalize(dir, dir)

        let pos = new Laya.Vector3()
        Laya.Vector3.scale(dir, r, dir)
        Laya.Vector3.add(centerPos, dir, pos)
        return pos
    }

    /**获取粒子系统 */
    static d3_GetParticleSystem(particle: Laya.ShuriKenParticle3D | any) {
        return particle.particleSystem;
    }

    /**克隆目标 */
    static d3_CloneSprite(spr: any) {
        if (!spr || !spr.clone) return null;
        return spr.clone();
    }
    //#endregion

    //#region 2D
        /**
     * 按钮事件
     * @param btn 按钮
     * @param caller 事件对象
     * @param callBack 回调函数
     * @param args 参数
     * @param isStopPropagation 是否阻止穿透
     */
    static d2_AddClickEvent(btn: Laya.Sprite, caller: any, callBack: Function, args?: any[], isStopPropagation: boolean = false, isSound = true, isTween = true) {
        btn.offAllCaller(caller);

        /**tween参数 */
        let tweenTime = 60;
        let oldSize = btn.scaleX;
        let newSize = oldSize * 0.9;

        let isPressed = false;
        let cbDown = (evt) => {
            isPressed = true;
            if (isTween)
                Laya.Tween.to(btn, { scaleX: newSize, scaleY: newSize }, tweenTime);
            if (isStopPropagation) evt.stopPropagation;
        }
        btn.on(Laya.Event.MOUSE_DOWN, caller, cbDown, args);

        let cbUp = (evt) => {
            if (isPressed == false) return;
            isPressed = false;
            if (isStopPropagation) evt.stopPropagation;
            if (isTween)
                Laya.Tween.to(btn, { scaleX: oldSize, scaleY: oldSize }, tweenTime);
            if (callBack) callBack.call(caller, evt);
            if (isSound)
                Data.sound.play(SoundType.CLICK);
        }
        btn.on(Laya.Event.MOUSE_UP, caller, cbUp, args);

        let cbOut = (evt) => {
            if (isPressed == false) return;
            isPressed = false;
            if (isTween)
                Laya.Tween.to(btn, { scaleX: oldSize, scaleY: oldSize }, tweenTime);
            if (isStopPropagation) evt.stopPropagation;
        }
        btn.on(Laya.Event.MOUSE_OUT, caller, cbOut, args);
    }

    //#endregion

    //#region Math
    static clamp01(v): number {
        if (v > 1) return 1;
        if (v < 0) return 0;
        return v;
    }

    static clamp(value, min, max) {
        if (value < min) return min;
        if (value > max) return max;
        return value;
    }
    //#endregion


    /**获取当前时间戳 ms*/
    static get getCurrentTime() {
        return new Date().getTime();
    }
}