
import { SoundType } from "../../configs/EnumType";
import D3Tween from "../../extensionTools/D3Tween";
import CamFollow from "../../extensionTools/mods_Nimo/cam/CamFollow";
import Data from "../../extensionTools/mods_Nimo/Data";
import FxController from "../../extensionTools/mods_Nimo/FxController";
import NimoForFrame from "../../extensionTools/mods_Nimo/NimoForFrame";
import { Nimo_EventType } from "../../extensionTools/mods_Nimo/Nimo_EventType";
import Tools from "../../extensionTools/mods_Nimo/Tools";
import PopupTextTool from "../../extensionTools/PopupTextTool/PopupTextTool";
import GameMgr from "../../managers/GameMgr";
import MachineComponent from "../Machine/MachineComponent";
import SpeedButton from "../UI/SpeedButton";
import CarHitCheck from "./HitCheck/CarHitCheck";
import TraceCircuit from "./Move/TraceCircuit";
import TraceProgressTracker from "./Move/TraceProgressTracker";



export default class CarComponent extends Laya.Script3D {

    playerId = 0;
    //默认组件
    circuit: TraceCircuit;
    tracker: TraceProgressTracker;
    user: Laya.Sprite3D;
    model: Laya.Sprite3D;
    modelCar: Laya.Sprite3D;
    modelRobot: Laya.Sprite3D;
    controlComp;
    colliderComp: CarHitCheck;
    //轮胎
    wheels: Laya.Sprite3D[] = [];
    //皮肤
    skinId = 1;
    //特效
    carHitEffect: Laya.Sprite3D;
    carPushEffect: Laya.Sprite3D;
    init(circuit, distance = 50, horizontal = 0, skinId = 1) {
        //获取玩家
        this.user = this.owner as Laya.Sprite3D;
        //#region 轨迹初始化
        let tracerComp = this.user.getComponent(TraceProgressTracker)
        if (tracerComp)
            tracerComp.destroy();
        let tracer = this.user.addComponent(TraceProgressTracker);
        tracer.circuit = circuit;
        tracer.init(distance);
        this.circuit = circuit
        this.tracker = tracer;
        //#endregion

        //#region 模型初始化
        let modelCar = this.user.getChildByName("ModelCar") as Laya.Sprite3D;
        this.modelCar = modelCar;
        this.modelRobot = this.user.getChildByName("ModelRobot") as Laya.Sprite3D;
        modelCar.active = true;
        this.modelRobot.active = true;
        // this.modelRobot.transform.localScale = new Laya.Vector3(0, 0, 0);
        let modelIndex = skinId
        this.skinId = skinId

        //model赋给
        let modelUrl = "Model" + skinId.toString();
        let machinePrefab = Data.prefabs.getChildByName("Machine").getChildByName(modelUrl) as Laya.Sprite3D;
        this.model = machinePrefab.clone() as Laya.Sprite3D;
        this.modelRobot.addChild(this.model);
        this.model.transform.localPosition = new Laya.Vector3(0, 0, 0);
        this.model.transform.localRotationEuler = new Laya.Vector3();
        this.model.transform.localScale = new Laya.Vector3(1, 1, 1);
        let prefab = Data.prefabs.getChildByName("Car").getChildByName(modelUrl) as Laya.Sprite3D;
        this.model = prefab.clone() as Laya.Sprite3D;
        modelCar.addChild(this.model);
        this.model.transform.localPosition = new Laya.Vector3();
        this.model.transform.localRotationEuler = new Laya.Vector3();
        this.model.transform.localScale = new Laya.Vector3(100, 100, 100);

        //特效
        this.carHitEffect = this.model.getChildByName("CarHit") as Laya.Sprite3D;
        this.carPushEffect = this.model.getChildByName("PushEffect") as Laya.Sprite3D;
        //轮胎
        for (let i = 0; i < 4; i++) {
            let wheel = this.model.getChildAt(i) as Laya.Sprite3D;
            this.wheels.push(wheel);
            GameMgr.ConsoleLog(wheel.name);
        }
        //#endregion

        //位置初始化
        this.horizontal = horizontal;
        this.model.transform.localPosition = new Laya.Vector3(-this.horizontal, 0, 0);
        //状态初始化
        this.isControl = true;
        this.isStart = false;
        Tools.setTransRotEulerY(this.model.transform, 0)

        //机甲变车初始化
        let machine = this.modelRobot.getChildByName("Model" + skinId.toString()) as Laya.Sprite3D;
        let effect1 = machine.getChildByName("Effect").getChildByName("BornEffect");
        let main = machine.getChildByName("main") as Laya.Sprite3D;
        this.modelRobot.transform.localScale = new Laya.Vector3(1, 1, 1);
        modelCar.transform.localScale = new Laya.Vector3(1, 1, 1);
        main.transform.localScale = new Laya.Vector3(250, 250, 250);
        main.transform.position = this.model.transform.position.clone();
        this.model.transform.localScale = new Laya.Vector3(0, 0, 0);

        Laya.timer.once(1000, this, () => {
            FxController.playEffectDestroy("BornEffect", effect1, 1000);
            main.transform.localScale = new Laya.Vector3(0, 0, 0);
            this.model.transform.localScale = new Laya.Vector3(100, 100, 100);
            Laya.timer.once(1000, this, () => {
                main.transform.localScale = new Laya.Vector3(300, 300, 300);
                main.transform.localPosition = new Laya.Vector3()
                this.modelRobot.transform.localScale = new Laya.Vector3(0, 0, 0);
            })
        })
        //不同类的初始化
        this.specialInit();
    }

    isEnding = false;
    gameEnd() {
        this.controlComp = {
            axis: 1,
            horizontal: 0
        };
        this.isEnding = true;
        this.colliderComp.active = false;
        this.colliderComp.destroy();
        this.CarStart();
        this.gameSpecialEnd();
        // this.destroy();
    }

    car_nowOffset;
    car_startPos;
    CarStart() {
        let endPos = NimoForFrame.endPos
        this.car_startPos = this.tracker.progressDistance;
        this.maxSpeed = 5
        this.tracker.speedFactor = 5;
        // CamFollow.instance.enabled = false;
        // this.car_nowOffset = Tools.Vector3Reduce(Data.mainCam.transform.position.clone(), Data.camPoint.transform.position.clone());
        Laya.timer.frameLoop(1, this, this.CarUpdate)
        this.CarStartSpecial();
    }
    CarUpdate() {
        let self = this;
        let progress = (this.tracker.progressDistance - this.car_startPos) / 500;
        let axis = progress * 2;
        let height = 21.6 * 5;
        let highAxis = - (Math.pow(axis - 1, 2) - 1)
        let modelHeight = height * highAxis;
        let pos = self.modelCar.transform.localPosition.clone();
        pos.y = modelHeight;
        self.modelCar.transform.localPosition = pos;
        Tools.setTransRotEulerX(self.modelCar.transform, (0.5 - progress) * -45);
        this.maxSpeed = 5
        this.tracker.speedFactor = 5;
        if (progress >= 1) {
            this.CarEnd();
        }
    }
    CarEnd() {
        Laya.timer.clear(this, this.CarUpdate);
        this.colliderComp = null;
        this.tracker.destroy();
        this.circuit.destroy();
        this.CarSpecialChange();
        this.destroy();
        // CamFollow.instance.enabled = true;
        // CamFollow.instance.GoPointRightNow(Data.camPoint);
        this.modelCar.transform.localPosition = new Laya.Vector3();
        this.modelCar.transform.localRotationEuler = new Laya.Vector3();
        this.model.destroy();
    }


    //#region 对外接口
    specialInit() {

    }
    getControlComp() {

    }
    getColliderComp() {

    }
    specialUpdate() {

    }
    CarSpecialChange() {

    }
    gameSpecialEnd() {

    }
    HitSpecial() {

    }
    reset() {

    }
    CarStartSpecial() {

    }
    //#endregion


    //#region 移动部分
    //最大加速度
    addMaxSpeed = 0.05;
    //最小加速度
    reduceMaxSpeed = -0.1;
    //最大前进速度
    maxSpeed = 7;
    //最小前进速度
    minSpeed = 1;
    //左右横移速度
    turnSpeed = 0.9;

    //左右最大横移速度
    turnMaxSpeed = 2;


    //左右
    horizontal = 0;//(-6,6)
    speed = 0;
    //启动
    isStart = false;
    //前后
    targetSpeed = 0;
    targetAxisSpeed = 0;
    axisSpeed = 0;
    //模型效果
    nowOffset = 0;
    targetOffset = 0;
    //失控
    isControl = true;

    //移动Update
    frameAxis = 1;
    controlTrunOffset = 0;
    carAddAxis = Data.carBaseAddSpeed;
    onUpdate() {
        let comp = this.controlComp
        if (!comp) {
            return;
        }


        //#region jie
        // let car = this.model; //6
        // let pos = car.transform.localPosition.clone();
        // let rot = car.transform.localRotationEuler.clone();
        // let lerpFactor = 0.01;
        // let addLerp = 0.05;
        // let posFactor = 0.15;
        // let angleFactor = 0.5;

        // //角度
        // let lerpRotFactor = 0.01;
        // let addRotLerp = 0.05;

        // if (comp.horizontal == -1) {//左
        //     pos.x += posFactor;
        //     rot.y += angleFactor;
        //     lerpFactor += addLerp;
        //     lerpRotFactor += addRotLerp;
        // }
        // else if (comp.horizontal == 1) { //右
        //     pos.x -= posFactor;
        //     rot.y -= angleFactor;
        //     lerpFactor += addLerp;
        //     lerpRotFactor += addRotLerp;
        // }
        // else {
        //     if (rot.y > 0) {
        //         rot.y -= angleFactor;
        //     }
        //     else if (rot.y < 0) {
        //         rot.y += angleFactor;
        //     }

        //     lerpFactor -= addLerp;
        //     if (lerpFactor < 0.01) lerpFactor = 0.01;

        //     lerpRotFactor -= addRotLerp;
        //     if (lerpRotFactor < 0.01) lerpRotFactor = 0.01;

        // }

        // if (lerpFactor > 1) lerpFactor = 1;

        // lerpPosX();
        // lerpRotY();

        // function lerpPosX() {
        //     if (pos.x > 6) pos.x = 6;
        //     else if (pos.x < -6) pos.x = -6;

        //     Laya.Vector3.lerp(car.transform.localPosition.clone(), pos, lerpFactor, pos);
        //     car.transform.localPosition = pos;
        // }

        // function lerpRotY() {
        //     if (rot.y > 8) rot.y = 8;
        //     else if (rot.y < -8) rot.y = -8;

        //     if (Math.abs(rot.y) < angleFactor) {
        //         rot.y = 0;
        //         car.transform.localRotationEuler = rot;
        //     }
        //     else {
        //         Laya.Vector3.lerp(car.transform.localRotationEuler.clone(), rot, lerpRotFactor, rot);
        //         car.transform.localRotationEuler = rot;
        //     }
        // }
        // return;
        //#endregion

        //#region 移动逻辑部分
        let tracer = this.tracker
        this.frameAxis = Laya.timer.delta / 16
        let frameAxis = this.frameAxis;

        if (tracer.speedFactor >= this.minSpeed) {
            this.isStart = true;

        }
        //给予速度
        if (this.isControl) {
            this.targetSpeed += comp.horizontal * frameAxis * 0.066;
            if (this.targetSpeed > this.turnSpeed) {
                this.targetSpeed = this.turnSpeed;
            } else if (this.targetSpeed < -this.turnSpeed) {
                this.targetSpeed = -this.turnSpeed
            }
            let axis;
            if (!this.isStart) {
                axis = 1;
            } else {
                axis = comp.axis
            }
            this.targetAxisSpeed += axis * frameAxis * this.carAddAxis
            let limitAxis = this.addMaxSpeed * this.carAddAxis / Data.carBaseAddSpeed
            if (this.targetAxisSpeed > limitAxis) {
                this.targetAxisSpeed = limitAxis;
            } else if (this.targetAxisSpeed < this.reduceMaxSpeed) {
                this.targetAxisSpeed = this.reduceMaxSpeed;
            }
            //衰减
            if (comp.horizontal == 0) {
                let change = 1 * frameAxis;
                if (this.targetSpeed > change) {
                    this.targetSpeed -= change;
                } else if (this.targetSpeed < -change) {
                    this.targetSpeed += change;
                } else {
                    this.targetSpeed = 0;
                }
            }
            if (axis == 0) {
                let speedChange;
                let targetSpeed = -0.05;
                if (this.isControl) {
                    speedChange = 0.05 * frameAxis;
                } else {
                    speedChange = 0.1 * frameAxis;
                }
                if (this.targetAxisSpeed > targetSpeed + speedChange) {
                    this.targetAxisSpeed -= speedChange;
                } else if (this.targetAxisSpeed < targetSpeed - speedChange) {
                    this.targetAxisSpeed += speedChange;
                }
                else {
                    this.targetAxisSpeed = targetSpeed;
                }
            }
        }

        //前后移动
        let dirAxis = this.targetAxisSpeed - this.axisSpeed;
        this.axisSpeed += dirAxis * 0.24 * frameAxis;
        let axisSpeed = tracer.speedFactor;
        axisSpeed += this.axisSpeed * frameAxis * 0.25;
        if (axisSpeed > this.maxSpeed) {
            axisSpeed = this.maxSpeed;
        }
        if (!this.isControl) {
            if (axisSpeed < -1) {
                axisSpeed = -1;
            }
        } else {
            if (!this.isStart) {
                if (axisSpeed < 0)
                    axisSpeed = 0;
            } else if (axisSpeed < this.minSpeed) {
                axisSpeed = this.minSpeed;
            }
        }
        //左右移动
        this.speed = this.targetSpeed;
        let forwardSpeed = axisSpeed;
        let horizontalSpeed = this.speed;
        //竖向速度给予
        this.tracker.speedFactor = forwardSpeed;
        this.tracker.onTrackerUpdate();
        //横向速度给予
        this.horizontal += horizontalSpeed * 0.08 * this.frameAxis;
        if (this.horizontal > 6) {
            this.horizontal = 6;
        }
        if (this.horizontal < -6) {
            this.horizontal = -6;
        }
        let horizontalPos = this.model.transform.localPosition.clone();
        horizontalPos.x = -this.horizontal;
        this.model.transform.localPosition = horizontalPos;

        //#region 移动表现部分
        //模型左右摆动
        let offset = this.tracker.getHorizontalOffset(this.tracker.forwardAxis);
        let turnSpeed = 0.6
        let reTurnSpeed = 0.52
        this.controlTrunOffset += this.controlComp.horizontal * turnSpeed * frameAxis;
        if (this.controlComp.horizontal == 0) {
            this.controlTrunOffset = Tools.numberTo(this.controlTrunOffset, 0, frameAxis * reTurnSpeed)
        }
        let maxTurn = 5;
        if (this.controlTrunOffset > maxTurn) {
            this.controlTrunOffset = maxTurn;
        } else if (this.controlTrunOffset < -maxTurn) {
            this.controlTrunOffset = -maxTurn;
        }

        this.targetOffset = -this.controlTrunOffset + offset * 0.15;
        this.nowOffset = this.model.transform.localRotationEuler.y;
        this.nowOffset = this.targetOffset;

        Tools.setTransRotEulerY(this.model.transform, this.nowOffset)
        //轮胎随速度移动
        let wheelSpeed = this.tracker.forwardAxis;//0-5
        for (let i = 0; i < this.wheels.length; i++) {
            let x = this.wheels[i].transform.localRotationEulerX;
            Tools.setTransRotEulerX(this.wheels[i].transform, x + wheelSpeed * 5);
        }
        //前轮左右转动
        for (let i = 2; i < this.wheels.length; i++) {
            let y = this.nowOffset;
            Tools.setTransRotEulerY(this.wheels[i].transform, y * 2);
        }
        //#endregion

        //#region  路程判断  50开始   倒数50结束
        if (!this.isEnding) {
            let nowRange = this.tracker.progressDistance;
            let targetRange = this.circuit.accumulateDistance - 1000;
            if (nowRange >= targetRange) {
                this.gameEnd();
                return;
            }
        }
        //#endregion
        this.specialUpdate();
    }
    getNextHorizontal(speed) {
        let nextHorizontal = this.horizontal + speed * 0.1 * this.frameAxis;
        if (nextHorizontal > 6) {
            nextHorizontal = 6;
        }
        if (nextHorizontal < -6) {
            nextHorizontal = -6;
        }
        return nextHorizontal;
    }

    onLateUpdate() {
        //#endregion
    }

    //击退
    backHorizontal;
    backAxis;
    backCD = 60;
    hitTarget;
    hitEvent;
    canPlayHitSound = true;
    hitSoundCD = 200;
    HitStart(hitEvent, target) {
        if (!this.controlComp)
            return;
        if (!this.isControl)
            return;
        this.HitEnd();
        //0 没撞到,1前后撞,2左右撞
        let hitCase = 0;
        if (hitEvent.isAxisHit && !hitEvent.isHorizontalHit) {
            hitCase = 1;
        } else if (!hitEvent.isAxisHit && hitEvent.isHorizontalHit) {
            hitCase = 2;
            //左右
        } else if (hitEvent.isAxisHit && hitEvent.isHorizontalHit) {
            let axisDistance = target.carComp.tracker.progressDistance - this.tracker.progressDistance;
            let horizontalDistance = target.carComp.horizontal - this.horizontal;
            if (axisDistance > horizontalDistance) {
                hitCase = 2;
            } else {
                hitCase = 1;
            }
        }

        if (hitCase == 1) {
            //前进
            let allSpeed = this.tracker.speedFactor + target.carComp.tracker.speedFactor;
            // if (this.tracker.progressDistance > target.carComp.progressDistance) {
            //     this.tracker.speedFactor = allSpeed * 0.6;
            //     target.carComp.tracker.speedFactor = allSpeed * 0.4;
            // } else {
            this.tracker.speedFactor = allSpeed * 0.4;
            target.carComp.tracker.speedFactor = allSpeed * 0.6;
            // }
            this.hitTarget = target.carComp;

            //被撞击对象处理
            let id = this.hitTarget.playerId;
            if (NimoForFrame.hpValues[id] > Data.minCarHp)
                NimoForFrame.reduceHpValue(id, Data.carHitReduceHp);
            GameMgr.ConsoleLog("撞击扣血", id, Data.minCarHp, NimoForFrame.hpValues[id])
            //变红
            // let car = this.hitTarget.model as Laya.MeshSprite3D;
            // let mat = car.meshRenderer.material.clone()
            // mat.albedoColorR = 1;
            // mat.albedoColorG = 0.5
            // mat.albedoColorB = 0.5;
            // car.meshRenderer.material = mat;
            this.hitTarget.HitTurnRed();
            //
        } else if (hitCase == 2) {
            let way = target.carComp.horizontal - this.horizontal;
            // this.targetSpeed *= -1.5;
            // if (Math.abs(this.targetSpeed) < 0.75) {

            // }
            this.hitTarget = target.carComp;
            let hitBackHorizontalSpeed = Data.carHitHorizontal;
            if (way > 0) {
                this.targetSpeed = -hitBackHorizontalSpeed;
                this.hitTarget.targetSpeed = hitBackHorizontalSpeed;
            }
            else {
                this.targetSpeed = hitBackHorizontalSpeed;
                this.hitTarget.targetSpeed = -hitBackHorizontalSpeed;
            }

        }
        //结束
        if (hitCase != 0) {
            Laya.timer.once(this.backCD, this, this.HitEnd)
            this.isControl = false;
            this.hitTarget.isControl = false;
            this.hitEvent = hitEvent;
            //特效
            // let way = new Laya.Vector2(this.hitTarget.colliderComp.axis - this.colliderComp.axis, this.hitTarget.colliderComp.horizontal - this.colliderComp.horizontal);
            // this.carHitEffect.transform.localPosition = new Laya.Vector3(-way.y * 0.005, 0.0078, way.x * 0.005);
            // FxController.playEffectDestroy("CarHit", this.carHitEffect, 500);
            let playerWay = Tools.Vector3Reduce(Data.player.transform.position, this.user.transform.position);
            let distance = Laya.Vector3.scalarLength(playerWay);
            if (distance < 50) {
                if (this.canPlayHitSound) {
                    Data.sound.play(SoundType.CarHit);
                    this.canPlayHitSound = false;
                    Laya.timer.once(this.hitSoundCD, this, () => {
                        this.canPlayHitSound = true;
                    })
                }
            }
            if (this.playerId == 0) {
                GameMgr.Platform.vibrateShort();
            }
            if (this.hitTarget.playerId == 0) {
                GameMgr.Platform.vibrateShort();
            }
            if (hitCase == 1)
                this.HitSpecial();
        }
    }
    HitEnd() {
        Laya.timer.clear(this, this.HitEnd)
        this.isControl = true;
        if (this.hitTarget) {
            //变白
            // let car = this.hitTarget.model as Laya.MeshSprite3D;
            // let mat = car.meshRenderer.material.clone()
            // mat.albedoColorR = 1;
            // mat.albedoColorG = 1;
            // mat.albedoColorB = 1;
            // car.meshRenderer.material = mat;

            this.hitTarget.isControl = true;
            this.hitTarget = null
        }
    }
    redTween: D3Tween;
    HitTurnRed() {
        if (this.redTween)
            this.redTween.clearTween();
        this.redTween = new D3Tween();
        let car = this.model as Laya.MeshSprite3D;
        let mat = car.meshRenderer.material.clone()
        mat.albedoColorR = 1;
        mat.albedoColorG = 0.5
        mat.albedoColorB = 0.5;
        car.meshRenderer.material = mat;
        this.redTween.updateTweenCallback = (progress) => {
            let mat = car.meshRenderer.material.clone()
            if (progress >= 0.3) {
                mat.albedoColorR = 1;
                mat.albedoColorG = 0.5 + (progress - 0.3) / 0.7 * 0.5
                mat.albedoColorB = 0.5 + (progress - 0.3) / 0.7 * 0.5
            }
            car.meshRenderer.material = mat;
        }
        this.redTween.toTween(car, {}, 250, () => {
            let mat = car.meshRenderer.material.clone()
            mat.albedoColorR = 1;
            mat.albedoColorG = 1
            mat.albedoColorB = 1;
            car.meshRenderer.material = mat;
        }, null)
    }
    //#endregion

    //#region 击飞销毁处理
    //需要付给一个重心为中心点的车模型
    car: Laya.Sprite3D;
    hitCD: number = 300;
    isFlying = false;
    HitFly(hitEvent, target) {
        if (this.isEnding)
            return;
        // //0 没撞到,1撞飞,2直接消失
        let hitCase = 0;
        if (hitEvent.isAxisHit && !hitEvent.isHorizontalHit) {
            hitCase = 1;
        } else if (!hitEvent.isAxisHit && hitEvent.isHorizontalHit) {
            hitCase = 1;
            //左右
        } else if (hitEvent.isAxisHit && hitEvent.isHorizontalHit) {
            let axisDistance = target.carComp.tracker.progressDistance - this.tracker.progressDistance;
            let horizontalDistance = target.carComp.horizontal - this.horizontal;
            if (axisDistance > 0) {
                hitCase = 2
            }
        }
        if (this.isFlying)
            return;
        if (hitCase == 0)
            return;
        if (hitCase == 2) {
            this.HitFlyEnd();
            return;
        }
        this.isFlying = true;
        if (target) {
            let pos = target.carComp.model.transform.position.clone();
            let force = 150 * (target.carComp.tracker.speedFactor - 1) + 30;
            let v1 = this.model.transform.position.clone();
            let dir = new Laya.Vector3;
            Laya.Vector3.subtract(v1, pos, dir);
            Laya.Vector3.scale(dir, force, dir);
            dir.y += force;
            // dir.z -=10*force;
            // dir = Tools.Vector3Axis(dir, force);
            Laya.Vector3.add(dir, v1, dir);

            let rot = this.model.transform.rotationEuler.clone();
            let angleX = Math.random() * 50 * force + 100;
            let angleZ = Math.random() * 10 * force + 100;
            rot.x += angleX;
            rot.z = pos.x > v1.x ? rot.z + angleZ : rot.z - angleZ;
            this.controlComp = null;
            let tween = new D3Tween();
            tween.toTween(this.model, { pos: dir, rot: rot }, force * 50, null, null, false)
            Laya.timer.once(this.hitCD, this, () => {
                tween.clearTween()
                this.HitFlyEnd()
            });
            if (target.carComp.playerId == 0) {
                GameMgr.Platform.vibrateShort();
                SpeedButton.instance.updateCD(500);
            }
            let playerWay = Tools.Vector3Reduce(Data.player.transform.position, this.user.transform.position);
            let distance = Laya.Vector3.scalarLength(playerWay);
            if (distance < 50) {
                if (this.canPlayHitSound) {
                    Data.sound.play(SoundType.CarHit);
                    this.canPlayHitSound = false;
                    Laya.timer.once(this.hitSoundCD, this, () => {
                        this.canPlayHitSound = true;
                    })
                }
            }
        }
    }
    HitFlyEnd() {
        if (this.car) {
            this.car.active = false;
        }
        if (this.model) {
            FxController.playEffectPositon("Expolsion", this.model.transform.position.clone(), 1000);
            let playerWay = Tools.Vector3Reduce(Data.player.transform.position, this.user.transform.position);
            let distance = Laya.Vector3.scalarLength(playerWay);
            if (distance < 50) {
                Data.sound.play(SoundType.Boom);
            }

        }
        this.HitFlyEndSpecial();
        this.isFlying = false;
    }
    HitFlyEndSpecial() {

    }
    //#endregion

    //#region 表情播放
    emojiCan = true;
    emojiPlay(type: emojiType) {
        if (!this.emojiCan)
            return;
        let emojiEffect = this.model.getChildByName("EmojiEffect") as Laya.Sprite3D;
        // GameMgr.ConsoleLog("表情包", emojiEffect);
        if (emojiEffect)
            FxController.playEffectDestroy(type, emojiEffect, 3000);
        this.emojiCan = false;
        Laya.timer.once(3000, this, () => {
            this.emojiCan = true;
        })
    }
    //#endregion
}
export enum emojiType {
    Cool = "EmojiCool",
    Cry = "EmojiCry",
    LaughCry = "EmojiLaughCry"
}