import JoystickComponentLeft from "../../extensionTools/mods_Nimo/ctrl/JoystickComponentLeft";
import Data from "../../extensionTools/mods_Nimo/Data";
import FxController from "../../extensionTools/mods_Nimo/FxController";
import NimoForFrame from "../../extensionTools/mods_Nimo/NimoForFrame";
import Tools from "../../extensionTools/mods_Nimo/Tools";
import PopupTextTool from "../../extensionTools/PopupTextTool/PopupTextTool";
import GameMgr from "../../managers/GameMgr";
import LevelMgr from "../../managers/LevelMgr";
import CarComponent from "../Car/CarComponent";
import MachineHitCheck_AI from "./HitCheck/MachineHitCheck_AI";


export default class MachineComponent extends Laya.Script3D {

    playerId = 0;

    modelRobot: Laya.Sprite3D;

    player: Laya.Sprite3D;
    model: Laya.Sprite3D;
    anim: Laya.Animator;
    effect: Laya.Sprite3D;
    attackPoint: Laya.Sprite3D;

    isMove: boolean;
    isAttack: boolean;
    isEnd: boolean = true;
    isInvincible: boolean = false;

    camAngle = 0;

    skinId = 1;

    controlComp;
    colliderComp;
    onEnable(): void {
        this.initEvent()
    }

    onDisable(): void {
        if (this.colliderComp)
            this.colliderComp.destroy();
        this.offEvent()
    }

    initEvent() {

    }
    offEvent() {

    }
    init(skinId = 1) {
        //游戏结束事件绑定
        GameMgr.Event.on(EventType.Over_Game_Event, this, () => {
            if (this.attackUpEffect)
                this.attackUpEffect.destroy();
            this.controlComp = null

        })
        //赋值
        this.player = this.owner as Laya.Sprite3D;
        let modelCar = this.player.getChildByName("ModelCar") as Laya.Sprite3D;
        modelCar.active = false;
        this.modelRobot = this.player.getChildByName("ModelRobot") as Laya.Sprite3D;
        this.modelRobot.active = true;
        this.modelRobot.transform.localScale = new Laya.Vector3(1, 1, 1);
        this.skinId = skinId;
        let modelIndex = this.skinId;

        //模型给予
        let modelUrl = "Model" + skinId.toString();
        this.model = this.modelRobot.getChildByName(modelUrl) as Laya.Sprite3D;
        //武器给与
        // this.getWeapon(0);
        // for (let i = 1; i <= 4; i++) {
        //     let modelUrl = "Model" + i.toString();
        //     if (i == modelIndex) {
        //         this.model = this.modelRobot.getChildByName(modelUrl) as Laya.Sprite3D;
        //         this.model.active = true;
        //     }
        //     else {
        //         let model = this.modelRobot.getChildByName(modelUrl) as Laya.Sprite3D;
        //         model.active = false;
        //     }
        // }


        this.anim = this.model.getComponent(Laya.Animator);
        // NimoForFrame.bornStageObj("Effect", this.model);
        this.effect = this.model.getChildByName("Effect") as Laya.Sprite3D;
        Tools.setTransRotEulerY(this.model.transform, 0)
        let pos = this.player.transform.position.clone();
        pos.x = Tools.randomNumber(-6, 6);
        this.player.transform.position = pos;
        this.attackPoint = this.modelRobot.getChildByName("AttackPoint") as Laya.Sprite3D;
        //模块初始化
        this.getColliderComp();
        this.getControlComp();
        //参数
        this.isEnd = false;
        this.camAngle = 0;
        this.isHitting = false;
        //状态
        this.goMove();
        this.stopAttack();
        //特效初始化
        this.attackEffect = [];
        let attackEffect1 = this.effect.getChildByName("SwordEffect1") as Laya.Sprite3D
        let attackEffect2 = this.effect.getChildByName("SwordEffect2") as Laya.Sprite3D
        let attackEffect3 = this.effect.getChildByName("SwordEffect3") as Laya.Sprite3D
        this.attackEffect.push(attackEffect1);
        this.attackEffect.push(attackEffect2);
        this.attackEffect.push(attackEffect3);
        let bornParent = this.effect.getChildByName("BornEffect");
        FxController.playEffectDestroy("BornEffect", bornParent, 1000);

        this.hp = NimoForFrame.hpValues[this.playerId];
        //特殊初始化
        this.specialInit()
    }
    isGotWeapon = false;
    getWeapon(weaponID) {
        this.isGotWeapon = true;
        let weaponParent = ExtensionTool.d3_FindNodeByName(this.model, "Weapon");
        let weaponUrl = "Weapon" + weaponID.toString();
        let weaponPrefab = Data.prefabs.getChildByName("Weapon").getChildByName(weaponUrl) as Laya.Sprite3D;
        let weapon = weaponPrefab.clone() as Laya.Sprite3D;
        weaponParent.addChild(weapon);
        weapon.transform.localPosition = new Laya.Vector3();
        weapon.transform.localRotationEuler = new Laya.Vector3();
        weapon.transform.localScale = new Laya.Vector3(1, 1, 1);
        this.getWeaponSpecial()
    }
    getWeaponSpecial() {

    }
    specialInit() {
        // let cam = this.modelRobot.getChildByName("CamRobotPoint");
        // let camComp = cam.getComponent(CamFollowBox)
        // if (camComp)
        //     camComp.destroy();
        // cam.addComponent(CamFollowBox);
        // Data.camPoint = cam.getChildByName("MainCam") as Laya.Sprite3D;
    }
    getControlComp() {
        // this.controlComp = JoystickComponentLeft;
        this.controlComp = { xAxis: 0, yAxis: 0 }
    }
    getColliderComp() {

    }
    onUpdate() {
        if (this.isEnd) {
            if (this.attackUpEffect)
                this.attackUpEffect.destroy();
            return;
        }

        if (!this.controlComp)
            return;
        if (this.isHitting) {
            this.HitDoing();
        }
        if (this.isDowning) {

        } else if (this.isHurting) {

        } else if (this.isAttack) {

        } else if (this.isMove) {
            this.MoveControl();
        }
    }

    onDestroy() {
        this.model.destroy();
    }

    //#region 皮肤切换
    nowHero = 1;
    // updateSkin() {
    //     this.nowHero = Data.readNowSkin() + 1;
    //     let index = this.nowHero;
    //     for (let i = 1; i <= 5; i++) {
    //         let url = "Model" + i.toString();
    //         let model = this.player.getChildByName(url) as Laya.Sprite3D;
    //         model.active = i == index;
    //         if (i == index) {
    //             this.model = model
    //             this.attackRange = this.model.getChildByName("AttackRange") as Laya.Sprite3D;
    //             this.anim = this.model.getComponent(Laya.Animator);
    //         }
    //     }
    //     this.attackFx = Tools.findNodeByName(this.model, "AttackFX") as Laya.ShuriKenParticle3D;
    //     this.attackFx.active = false;
    //     this.isEnd = false;
    //     this.goMove();
    //     this.stopAttack();
    //     this.camAngle = 0;
    //     Tools.setTransRotEulerY(this.model.transform, 0)
    //     this.offset = 0;
    //     for (let i = 0; i < this.arrows.length; i++) {
    //         if (this.arrows[i]) {
    //             this.arrows[i].active = true;
    //         }
    //     }
    // }
    //#endregion

    //#region 状态切换
    attackCombe = 0;
    atttackTime = [700, 367, 367]
    attackDoTime = [400, 100, 100];
    attackRange = [8, 8, 8]
    attackEffect = [];
    attack;
    goAttackSpecial() {

    }
    goAttack() {
        if (!this.isGotWeapon)
            return;
        if (NimoForFrame.isEnd)
            return;
        if (this.isEnd)
            return;
        if (!this.isMove)
            return;
        if (this.isAttack)
            return;
        if (this.isDowning)
            return;
        this.goAttackSpecial();
        this.isAttack = true;
        this.stopMove();
        let url = "attack_" + (this.attackCombe + 1).toString();
        this.anim.play(url, 0, 0);
        Laya.timer.once(this.attackDoTime[this.attackCombe], this, this.attackDo)
        Laya.timer.once(this.atttackTime[this.attackCombe], this, this.stopAttack)
        this.startCombe();
        if (!NimoForFrame.isPlayerCar)
            Data.sound.play(SoundType.PlayerSwordAttack);
    }
    attackDo() {
        let playEffect = this.attackCombe - 1;
        if (playEffect < 0) {
            playEffect = 2;
        }
        let effect = FxController.playEffectDestroy("SwordEffect", this.attackEffect[playEffect]);
        this.attackDoSpecial();
    }
    attackDoSpecial() {
        //碰撞判定
        // let ais = MachineHitCheck_AI.objs;
        // for (let i = 0; i < ais.length; i++) {
        //     let machine = ais[i].machine;
        //     machine.goHurt();
        // }
        // Data.camShake.vibrateShort(Data.mainCam);
    }
    stopAttack() {
        if (this.isEnd)
            return;
        Laya.timer.clear(this, this.attackDo);
        Laya.timer.clear(this, this.stopAttack);
        // for (let i = 0; i < this.attackEffect.length; i++) {
        //     this.attackEffect[i].active = false;
        // }
        this.isAttack = false;
        this.goMove();
    }
    //攻击连击用
    combeLostTime = 1500;
    startCombe() {
        if (this.isEnd)
            return
        Laya.timer.clear(this, this.stopCombe);
        this.attackCombe++;
        if (this.attackCombe > 2) {
            this.stopCombe();
        } else {
            Laya.timer.once(this.combeLostTime, this, this.stopCombe);
        }
    }
    stopCombe() {
        if (this.isEnd)
            return
        Laya.timer.clear(this, this.stopCombe);
        this.attackCombe = 0;
    }
    //受伤连击用
    combeHurtLostTime = 1500;
    hurtCombe = 0;
    startCombeHurt() {
        if (this.isEnd)
            return
        Laya.timer.clear(this, this.stopCombeHurt);
        this.hurtCombe++;
        if (this.hurtCombe > 4) {
            this.stopCombeHurt();
        } else {
            Laya.timer.once(this.combeHurtLostTime, this, this.stopCombeHurt);
        }
    }
    stopCombeHurt() {
        if (this.isEnd)
            return
        Laya.timer.clear(this, this.stopCombeHurt);
        this.hurtCombe = 0;
    }
    isHurting = false;
    hurtTime = 200;
    hp = 20;
    goHurt(value = 1, must = false) {

        if (this.isEnd)
            return false
        if (!must) {
            if (this.isDowning)
                return false;
            if (this.isHurting)
                return false;
            if (this.isInvincible) {
                this.hurtSpecial();
                let hitParent = this.effect.getChildByName("EffectHit")
                FxController.playEffectDestroy("EffectHit", hitParent)
                Tools.objectShake(this.player, 0.1, 0.05);
                if (this.hurtTime != 0) {
                    // this.anim.play("littlehit", 0, 0);
                    this.isHurting = true;
                    Laya.timer.once(this.hurtTime, this, this.stopHurt);
                }
                return false;
            }
        }
        this.stopHurt();
        this.stopDown();
        if (this.hurtTime != 0) {
            this.stopAttack();
            this.stopCombe();
            this.stopMove();
        }

        if (this.hurtCombe >= 4) {
            this.goDown();
        } else {
            let hitParent = this.effect.getChildByName("EffectHit")
            FxController.playEffectDestroy("EffectHit", hitParent)
            if (this.hurtTime != 0) {
                this.anim.play("littlehit", 0, 0);
                this.isHurting = true;
                Laya.timer.once(this.hurtTime, this, this.stopHurt);
            }
        }
        if (this.hurtTime != 0) {
            this.startCombeHurt();
        }
        this.hp = NimoForFrame.hpValues[this.playerId];

        value = Math.floor(value)
        this.hp -= value;
        GameMgr.ConsoleLog("扣血", value);


        if (this.hp <= 0) {
            this.goEnd();
        }
        NimoForFrame.reduceHpValue(this.playerId, value);
        this.hurtSpecial()
        return true;
    }
    hurtSpecial() {

    }
    stopHurt() {
        if (this.isEnd)
            return
        Laya.timer.clear(this, this.stopHurt);
        this.isHurting = false;
        if (!this.isInvincible)
            this.goMove();
    }

    heal() {
        GameMgr.ConsoleLog("治疗", this.playerId);
        let healValue = Math.floor(NimoForFrame.maxHpValues[this.playerId] / 3);
        NimoForFrame.addHpValue(this.playerId, healValue);
    }
    attackUpEffect;
    isAttackUp = false;
    attackUp() {
        let attackEffect = this.model.getChildByName("Effect").getChildByName("AttackEffect") as Laya.Sprite3D;
        this.attackUpEffect = FxController.playEffectReturn("AttackEffect", attackEffect);
        this.attack *= 2;
    }
    //倒地
    isDowning = false;
    downTime = 2500
    goDown() {
        if (this.isEnd)
            return
        this.stopCombeHurt();
        this.stopDown();
        this.stopCombe();
        this.stopHurt();
        this.stopAttack();
        this.stopMove();
        this.goDownSpecial();
        this.isDowning = true;
        this.anim.play("down", 0, 0);
        Laya.timer.once(this.downTime, this, this.stopDown)
    }
    goDownSpecial() {

    }
    stopDown() {
        if (this.isEnd)
            return
        Laya.timer.clear(this, this.stopDown);
        this.isDowning = false;
        this.goMove();
    }
    //移动
    stopMove() {
        if (this.isEnd)
            return
        this.isMove = false;
        this.anim.getControllerLayer(1).defaultWeight = 0;
    }
    goMove() {
        if (this.isEnd)
            return
        this.isMove = true;
        if (!this.isAttack)
            this.anim.crossFade("idle", 0, 0);
    }
    //结束
    goEnd() {
        this.stopMove();
        this.stopAttack();
        this.stopCombe();
        this.stopHurt();
        this.stopCombeHurt();
        this.anim.play("dead", 0, 0);
        this.isEnd = true;
        this.specialEnd();
    }

    specialEnd() {

    }
    //#endregion

    //#region 移动
    //移动控制
    MoveControl() {
        let xAxis = 0;
        let yAxis = 0;
        let angle = 0;
        let way = new Laya.Vector3(-xAxis, 0, yAxis)
        let wayAxis = Laya.Vector3.scalarLength(way);
        if (wayAxis > 1) {
            wayAxis = 1;
        }
        this.Move(angle, wayAxis);
    }
    moveSpeed = 1;
    nowAngle = 0;
    isHitting = false;
    backX = 0;
    backZ = 0;
    nowBackCount = 0;
    backCount = 5;
    hitBackAxis = 1;
    Move(angle, wayAxis) {
        Tools.setTransRotEulerY(this.model.transform, -angle)
        //玩家行走方向
        let radius = angle * Math.PI / 180;
        let goAdd = new Laya.Vector3(Math.sin(radius), 0, -Math.cos(radius));
        let pos = this.player.transform.position.clone();
        let timeAxis = Laya.timer.delta / 16;
        pos = Tools.Vector3Add(pos, Tools.Vector3Axis(goAdd, this.moveSpeed * 0.33 * wayAxis * timeAxis));
        //动画更新
        if (!this.isAttack)
            this.anim.getControllerLayer(1).defaultWeight = wayAxis;
        //TODO 碰撞检测

        this.player.transform.position = pos;

    }
    CamRot(angle) {
        this.camAngle = angle;
    }
    HitBack(comeAxis, comeHorizontal, scale = 1) {
        if (this.isHitting)
            return;
        this.isHitting = true;
        let axis = this.colliderComp.axis;
        let horizontal = this.colliderComp.horizontal;
        this.nowBackCount = 0;
        let way = new Laya.Vector2(axis - comeAxis, horizontal - comeHorizontal);
        Laya.Vector2.normalize(way, way);
        this.backZ = way.x * scale * 0.2 / this.backCount * this.hitBackAxis
        this.backX = way.y * scale * 0.2 / this.backCount * this.hitBackAxis
    }
    HitDoing() {
        let pos = this.player.transform.position.clone();
        let hitOffset = new Laya.Vector3(this.backX, 0, this.backZ);
        this.player.transform.position = Tools.Vector3Add(pos, hitOffset);
        this.nowBackCount++;
        if (this.nowBackCount >= this.backCount)
            this.HitEnd();
    }
    HitEnd() {
        this.isHitting = false;
        this.nowBackCount = 0;
        this.backZ = 0;
        this.backX = 0;
    }
    //#endregion

}