import Platform from "../platforms/Platform";
import OppoPlatform from "../platforms/oppo/OppoPlatform";
import WxPlatform from "../platforms/wx/WxPlatform";
import LevelMgr from "./LevelMgr";
import StorageMgr from "./StorageMgr";
import GlobalData from "../configs/GlobalData";
import { EquipmentPerformance, EventType, PlatformType } from "../configs/EnumType";
import { StorageData } from "../configs/StorageData";
import EventMgr from "./EventMgr";
import ResMgr from "./ResMgr";
import PrefabMgr from "./PrefabMgr";
import UiMgr from "./UiMgr";
import qyMgr from "../platforms/cpLogicalMgr/QingYou/qyMgr";
import VivoPlatform from "../platforms/vivo/VivoPlatform";
import FdMgr from "../platforms/cpLogicalMgr/FanDong/fdMgr";

/**游戏管理器 */
export default class GameMgr {
    static Res: ResMgr;

    static UI: UiMgr;
    static GlData: GlobalData;
    static Level: LevelMgr;
    static Platform: Platform;
    static Storage: StorageMgr;
    static Event: EventMgr;
    static Prefab: PrefabMgr;

    /**缓存键名 */
    static StorageKey: StorageData;

    static init() {
     
        this.GlData = new GlobalData;
        this.initPlatform();
        this.UI = new UiMgr;
        this.Event = new EventMgr;
        this.Res = new ResMgr;
        this.Prefab = new PrefabMgr;
        this.initStorage();
        //策略
        // qyMgr.init();
  

        this.Level = new LevelMgr;
    }

    /**初始化缓存 */
    private static initStorage() {
        this.Storage = new StorageMgr;
        this.StorageKey = this.Storage.storageName;
    }

    /**初始化平台 */
    private static initPlatform() {
        if (Laya.Browser.onTTMiniGame) {//抖音平台
            this.GlData.platform = PlatformType.TickTok;
            this.Platform = new Platform;
        }
        else if (Laya.Browser.onWeiXin) { //微信平台
            this.GlData.platform = PlatformType.WX;
            this.Platform = new WxPlatform;
        }
        else if (Laya.Browser.onVVMiniGame) { //vivo平台
            this.GlData.platform = PlatformType.VIVO;
            this.Platform = new VivoPlatform;
        }
        else if (Laya.Browser.onQGMiniGame) { //oppo平台
            this.GlData.platform = PlatformType.OPPO;
            this.Platform = new OppoPlatform;
        }
        else if (Laya.Browser.onPC || Laya.Browser.onMobile) { //PC
            this.GlData.platform = PlatformType.PC;
            this.Platform = new Platform;
            // this.GlData.platform = PlatformType.OPPO;
            // this.Platform = new OppoPlatform;
        }
    }

    static ConsoleLog(...data: any[]) {
        if (this.GlData.isShowLog) {
            console.log(...data);
        }
    }

    static ConsoleWarn(...data: any[]) {
        if (this.GlData.isShowLog) {
            console.warn(...data);
        }
    }

    static ConsoleError(...data: any[]) {
        if (this.GlData.isShowLog) {
            console.error(...data);
        }
    }
    /**当前帧率 */
    static get FPS() {
        return 1000 / Laya.timer.delta;
    }
    /**设备性能 */
    static equipmentPerformance: EquipmentPerformance;
    /**检测设备性能 */
    static checkPerformance(cb?: Function) {
        let value = 0;
        let count = 50;
        let curCount = count;
        Laya.timer.loop(100, this, addValue);

        function addValue() {
            value += GameMgr.FPS;
            curCount--;

            if (curCount <= 0) {
                Laya.timer.clear(this, addValue);
                let fps = value / count;
                if (fps > 50) {
                    GameMgr.equipmentPerformance = EquipmentPerformance.High;
                }
                else if (fps > 40) {
                    GameMgr.equipmentPerformance = EquipmentPerformance.Mid;
                }
                else {
                    GameMgr.equipmentPerformance = EquipmentPerformance.Low;
                }

                cb && cb();
            }
        }
    }
}


