import { EventType, UiSceneType } from "../configs/EnumType";
import ExtensionTool from "../extensionTools/ExtensionTool";
import NimoForFrame from "../extensionTools/mods_Nimo/NimoForFrame";
import CoinWindow from "../gameComponents/UI/CoinWindow";
import GameMgr from "../managers/GameMgr";
import FdAd from "../platforms/cpLogicalMgr/FanDong/FdAd";
import FdMgr from "../platforms/cpLogicalMgr/FanDong/fdMgr";
import qy_oppo from "../platforms/cpLogicalMgr/QingYou/qy_oppo";
import UiScene from "./UiScene";

/**首页 */
export default class Home extends UiScene {
    static instance;
    private btnStart: Laya.Button;
    private btnRight: Laya.Button;
    private btnLeft: Laya.Button;
    private btnVideo: Laya.Button;
    private nativeAd: Laya.Image;

    private btnCoin: Laya.Button;
    private coinValue: Laya.FontClip;
    private getCoinWindow: Laya.Box;

    initEvent() {
        // FdMgr.inHomePage(() => {
        // FdAd.hideBannerAd();
        GameMgr.Event.on(EventType.Skin_Unlock, this, this.gameCoinState);
        GameMgr.Event.on(EventType.Skin_Active, this, this.gameStartState);
        GameMgr.Event.on(EventType.Update_Coin_Event, this, this.coinChange);
        GameMgr.Event.on(EventType.Coin_Less_Event, this, this.showGetCoinWindow);
        GameMgr.Event.on(EventType.Native_Visible_Event, this, this.nativeChange);
        ExtensionTool.d2_AddClickEvent(this.btnStart, this, this.onStartClick);
        ExtensionTool.d2_AddClickEvent(this.btnRight, this, this.rightBtnClick);
        ExtensionTool.d2_AddClickEvent(this.btnLeft, this, this.leftBtnClick);
        ExtensionTool.d2_AddClickEvent(this.btnVideo, this, this.onVideoClick);
        ExtensionTool.d2_AddClickEvent(this.btnCoin, this, this.onCoinClick);
        // })
    }
    onOpened(data) {
        // if (data == "onLoad" && GameMgr.Storage.storageData.level == 1) {
        //     Laya.timer.once(100, this, this.onStartClick);
        // }

    }
    init() {
        Home.instance = this;
        this.coinChange();
    }

    onStartClick() {
        GameMgr.ConsoleLog("游戏开始");
        // FdMgr.startGame(() => {
        GameMgr.UI.closeScene(UiSceneType.Home);
        GameMgr.Event.event(EventType.Play_Game_Event);
        // });
    }
    onVideoClick() {
        GameMgr.ConsoleLog("点击广告解锁");
        NimoForFrame.videoUnlock();
    }

    onCoinClick() {
        GameMgr.ConsoleLog("点击金币解锁");
        NimoForFrame.coinUnlock();
    }
    rightBtnClick() {
        NimoForFrame.right();
    }
    leftBtnClick() {
        NimoForFrame.left();
    }
    //游戏开始界面
    gameStartState() {
        GameMgr.ConsoleLog("游戏开始界面")
        this.btnStart.set_visible(true);
        this.btnVideo.set_visible(false);
        this.btnCoin.set_visible(false);
        GameMgr.Event.event("Update_NativeAd_Event", { isClose: false })

    }
    //视频试用界面(弃用)
    gameVideoState() {
        GameMgr.ConsoleLog("游戏视频界面")
        this.btnStart.set_visible(false);
        this.btnVideo.set_visible(true);
        this.btnCoin.set_visible(false);
        GameMgr.Event.event("Update_NativeAd_Event", { isClose: false })
    }
    //金币解锁界面
    gameCoinState() {
        GameMgr.ConsoleLog("金币解锁界面")
        this.btnStart.set_visible(false);
        this.btnCoin.set_visible(true);
        this.btnVideo.set_visible(true);
        GameMgr.Event.event("Update_NativeAd_Event", { isClose: false })
    }

    coinChange() {
        this.coinValue.value = GameMgr.Storage.storageData.coin.toString();
    }

    nativeChange(isVisible?) {
        if (isVisible) {
            this.btnCoin.bottom = 165;
        } else {
            this.btnCoin.bottom = 50;
        }
    }
    showGetCoinWindow() {
        CoinWindow.instance.showGetCoin();
    }
}