import MachineComponent_AI from "../Machine/MachineComponent_AI";
import CarComponent from "./CarComponent";
import Car_AI from "./Car_AI";
import CarHitCheck_AI from "./HitCheck/CarHitCheck_AI";




export default class CarComponent_AI extends CarComponent {



    getControlComp() {
        if (!this.controlComp)
            this.controlComp = this.user.addComponent(Car_AI);
        else
            this.controlComp = this.user.getComponent(Car_AI);
    }
    getColliderComp() {
        this.colliderComp = this.user.addComponent(CarHitCheck_AI);
    }
    gameSpecialEnd() {

    }
    CarSpecialChange() {
        this.user.addComponent(MachineComponent_AI);
        this.user.getComponent(MachineComponent_AI).playerId = this.playerId;
        this.user.getComponent(MachineComponent_AI).init(this.skinId);
    }

    //重置位置，并初始化
    reset() {
        this.controlComp = null;
        this.colliderComp.destroy();
        Laya.timer.once(300, this, () => {
            // this.init(this.circuit, this.tracker.progressDistance + 1000)
            this.getControlComp();
            this.getColliderComp();
        })
    }
}