import Tools from "../../../extensionTools/mods_Nimo/Tools";
import MachineHitCheck from "./MachineHitCheck";
import MachineHitCheck_Player from "./MachineHitCheck_Player";

export default class MachineHitCheck_AI extends MachineHitCheck {
    static objs: MachineHitCheck_AI[];

    aiId;

    init() {
        if (!MachineHitCheck_AI.objs) {
            MachineHitCheck_AI.objs = [];
        }
        MachineHitCheck_AI.objs.push(this);
        this.aiId = MachineHitCheck_AI.objs.length - 1;
    }

    machineHitCheck() {
        let collider: MachineHitCheck[] = [];
        for (let i = 0; i < MachineHitCheck_AI.objs.length; i++) {
            if (i != this.aiId) {
                collider.push(MachineHitCheck_AI.objs[i]);
            }
        }
        if (MachineHitCheck_Player.player)
            collider.push(MachineHitCheck_Player.player);
        for (let i = 0; i < collider.length; i++) {
            let hitEvent = Tools.CircleCheck_2D(this.axis, this.horizontal, this.radius, collider[i].axis, collider[i].horizontal, collider[i].radius)
            if (hitEvent.isHit) {
                if (this.machine.isDowning) {
                    if (this.machine.isHitting) {
                        collider[i].machine.HitBack(this.axis, this.horizontal, hitEvent.range - hitEvent.distance);
                    } else
                        this.machine.HitBack(collider[i].axis, collider[i].horizontal, hitEvent.range - hitEvent.distance);
                }
                return;
            }
        }
    }
}