import { SoundType } from "../../../configs/EnumType";
import Data from "../../../extensionTools/mods_Nimo/Data";
import FxController from "../../../extensionTools/mods_Nimo/FxController";
import NimoForFrame from "../../../extensionTools/mods_Nimo/NimoForFrame";
import Tools from "../../../extensionTools/mods_Nimo/Tools";
import GameMgr from "../../../managers/GameMgr";
import Game from "../../../uiScenes/Game";
import CarHitCheck from "./CarHitCheck";
import CarHitCheck_AI from "./CarHitCheck_AI";
import CarHitCheck_Player from "./CarHitCheck_Player";

export default class CarHitCheck_NPC extends CarHitCheck {
    static objs: CarHitCheck_NPC[] = [];

    npcId = 0;

    init() {
        if (!CarHitCheck_NPC.objs) {
            CarHitCheck_NPC.objs = [];
        }
        CarHitCheck_NPC.objs.push(this);
        this.npcId = CarHitCheck_NPC.objs.length - 1;

    }
    CarCheckUpdate() {
        if (!NimoForFrame.isPlayerCar) {
            this.destroy()
            return;
        }
        let collider: CarHitCheck[] = [];
        let hitTarget = null;

        collider.push(CarHitCheck_Player.player)
        for (let i = 0; i < CarHitCheck_AI.objs.length; i++) {
            if (CarHitCheck_AI.objs[i].active)
                collider.push(CarHitCheck_AI.objs[i]);
        }

        let hitEvent = {
            isAxisHit: false,
            isHorizontalHit: false
        }

        for (let i = 0; i < collider.length; i++) {
            let addColliderAxis = collider[i].carComp.tracker.speedFactor / collider[i].carComp.maxSpeed * 2;
            hitEvent.isAxisHit
                = Tools.BoxCheck_2D_Normal(this.axis, this.horizontal, this.rangeAxis, this.widthAxis,
                    collider[i].axis, collider[i].horizontal, collider[i].rangeAxis + addColliderAxis * collider[i].rangeAxis, collider[i].widthAxis);
            hitEvent.isHorizontalHit
                = Tools.BoxCheck_2D_Normal(this.axis, this.horizontal, this.rangeHorizontal, this.widthHorizontal,
                    collider[i].axis, collider[i].horizontal, collider[i].rangeHorizontal, collider[i].widthHorizontal)

            if (hitEvent.isAxisHit || hitEvent.isHorizontalHit) {
                hitTarget = collider[i]
                break;
            }
        }
        this.carComp.HitFly(hitEvent, hitTarget);
        // this.carComp.HitStart(hitEvent, hitTarget);
    }
}