import { EventType, UiSceneType } from "../../../configs/EnumType";
import GameMgr from "../../../managers/GameMgr";
import { KolaSDK, PlatformType } from "./IKolaSDK";
import { UmengTool } from "../Umeng/UmengTool";

/**合作cp运营策略逻辑 */
export default class qy_wx {
    static code = "";

    /**获取userId */
    static getUserId(completeCB?: Function) {
        this.httpRequest("https://service.gamepeach.cn", "/funservice/wxcode2token?appid=wx8128821038939f82&js_code=" + this.code, 'get', (res) => {
            res = JSON.parse(res);
            completeCB && completeCB();
        });
    }

    //网络请求
    static httpRequest(url: string, params: any, type: string = 'get', completeHandler?: Function) {
        var xhr: Laya.HttpRequest = new Laya.HttpRequest();
        xhr.http.timeout = 5000;//设置超时时间；
        xhr.once(Laya.Event.COMPLETE, this, completeHandler);
        xhr.once(Laya.Event.ERROR, this, this.httpRequest, [url, params, type, completeHandler]);
        if (type == "get") {
            xhr.send(url + params, "", type, "text");
        } else if (type == "post") {
            xhr.send(url, JSON.stringify(params), type, "text");
        }
    }

    static init() {
        KolaSDK.Initialize(PlatformType.WeChat, "android", "jszw", "", "gamelog", "https://cy-game.cn-shenzhen.log.aliyuncs.com/logstores/", "https://cy-game.oss-cn-shenzhen.aliyuncs.com/gameJSConfig/h5games/", null);
        this.login();

        GameMgr.Event.on(EventType.Platform_Wake_Event, this, (e) => {
            KolaSDK.OnShow(e.param);
        });
        GameMgr.Event.on(EventType.Platform_Sleep_Event, this, () => {
            KolaSDK.OnHide();
        });

        GameMgr.Event.on(EventType.Open_Ui_Event, this, this.onOpenUiEvent);
        GameMgr.Event.on(EventType.Close_Ui_Event, this, this.onCloseUiEvent);
        GameMgr.Event.on(EventType.Show_Video_Event, this, () => {
            UmengTool.TrackEvent("ADReward");
            KolaSDK.LogEvent("ADReward");
        });
    }

    static onOpenUiEvent(url) {
        GameMgr.ConsoleLog("开启页面", url);

        switch (url) {
            case UiSceneType.Load: break;
            case UiSceneType.Home:
                GameMgr.Platform.showBannerAd();
                break;
            case UiSceneType.Game: break;
            case UiSceneType.Over:
                GameMgr.Platform.showBannerAd();
                break;
        }
    }

    static onCloseUiEvent(param) {
        GameMgr.ConsoleLog("关闭页面", param.url);

        switch (param.url) {
            case UiSceneType.Load:
                UmengTool.TrackEvent("OnLoading");
                KolaSDK.LogEvent("OnLoading");
                break;
            case UiSceneType.Home:
                GameMgr.Platform.hideBannerAd();
                break;
            case UiSceneType.Game: break;
            case UiSceneType.Over:
                GameMgr.Platform.hideBannerAd();
                if (param.param.isWin) {
                    UmengTool.TrackEvent("FinishLevel", "level" + GameMgr.Storage.storageData.level);
                    KolaSDK.LogEvent("FinishLevel", { level: GameMgr.Storage.storageData.level });
                }
                else {
                    UmengTool.TrackEvent("FailLevel", "level" + GameMgr.Storage.storageData.level);
                    KolaSDK.LogEvent("FailLevel", { level: GameMgr.Storage.storageData.level });
                }
                break;
        }
    }

    static login() {
        if(Laya.Browser.window.wx){
            Laya.Browser.window.wx.login({
                success(res) {
                    if (res.code) {
                        qy_wx.code = res.code;
                        qy_wx.getUserId();
                    } else {
                        console.log('登录失败！' + res.errMsg);
                    }
                }
            });
        }
    }
}