import { EventType } from "../../../configs/EnumType";
import GameMgr from "../../../managers/GameMgr";
import { Nimo_EventType } from "../Nimo_EventType";
import Tools from "../Tools";

export default class CarControlComponent extends Laya.Script {

    static instance;



    left: Laya.Image;
    right: Laya.Image;
    go: Laya.Image;
    stop: Laya.Image;



    // /** @prop {name:left_release, tips:"左松", type:string, accept:res}*/
    // left_release: string = "";
    // /** @prop {name:left_Press, tips:"左按", type:string, accept:res}*/
    // leftPress: string = "";


    // /** @prop {name:right_release, tips:"右松", type:string, accept:res}*/
    // rightRelease: string = "";
    // /** @prop {name:right_Press, tips:"右按", type:string, accept:res}*/
    // rightPress: string = "";

    // /** @prop {name:go_release, tips:"油松", type:string, accept:res}*/
    // goRelease: string = "";
    // /** @prop {name:go_press, tips:"油按", type:string, accept:res}*/
    // goPress: string = "";
    // /** @prop {name:stop_release, tips:"刹松", type:string, accept:res}*/
    // stopRelease: string = "";
    // /** @prop {name:stop_press, tips:"刹按", type:string, accept:res}*/
    // stopPress: string = "";

    leftUrl = ["ui/controlUI/yxn_an_jt1.png", "ui/controlUI/yxn_an_jt2.png"]
    rightUrl = ["ui/controlUI/yxn_an_jt3.png", "ui/controlUI/yxn_an_jt4.png"]
    stopUrl = ["ui/controlUI/yxn_an_jy2.png", "ui/controlUI/yxn_an_jy.png"]
    goUrl = ["ui/controlUI/yxn_an_sc2.png", "ui/controlUI/yxn_an_sc.png"]

    axis = 0;
    horizontal = 0;
    isLeft = false;
    isRight = false;
    isGo = false;
    isStop = false;
    onEnable(): void {
        //附唯一值
        CarControlComponent.instance = this;
        //初始化数值
        this.axis = 0;
        this.horizontal = 0;
        this.isLeft = false;
        this.isRight = false;
        this.isGo = false;
        this.isStop = false;
        // this.leftUrl[0] = this.leftRelease;
        // this.leftUrl[1] = this.leftPress;
        // this.rightUrl[0] = this.rightRelease;
        // this.rightUrl[1] = this.rightPress;
        // this.goUrl[0] = this.goRelease;
        // this.goUrl[1] = this.goPress;
        // this.stopUrl[0] = this.stopRelease;
        // this.stopUrl[1] = this.stopPress;
        //初始化图标
        this.left = this.owner.getChildAt(0) as Laya.Image;
        this.right = this.owner.getChildAt(1) as Laya.Image;
        this.go = this.owner.getChildAt(2) as Laya.Image;
        this.stop = this.owner.getChildAt(3) as Laya.Image;
        this.left.skin = this.leftUrl[0];
        this.right.skin = this.rightUrl[0];
        this.go.skin = this.goUrl[0];
        this.stop.skin = this.stopUrl[0];
        //绑定按钮事件
        Tools.gameButtonOn(this.left, this, this.LeftDown, this.LeftUp)
        Tools.gameButtonOn(this.right, this, this.RightDown, this.RightUp);
        Tools.gameButtonOn(this.go, this, this.GoDown, this.GoUp);
        Tools.gameButtonOn(this.stop, this, this.StopDown, this.StopUp);
        Laya.stage.on(Laya.Event.KEY_DOWN, this, this.onKeyDown);
        Laya.stage.on(Laya.Event.KEY_UP, this, this.onKeyUp);
        //绑定事件
        GameMgr.Event.on(EventType.Native_Visible_Event, this, this.onNativeChange);
    }
    onDisable() {
        GameMgr.Event.off(EventType.Native_Visible_Event, this, this.onNativeChange);
    }

    onUpdate() {
        let frameAxis = 16 / Laya.timer.delta;
        //左右处理
        if (this.isRight) {
            this.horizontal += 0.3;
            if (this.horizontal > 1) {
                this.horizontal = 1;
            }
        }
        if (this.isLeft) {
            this.horizontal -= 0.3;
            if (this.horizontal < -1) {
                this.horizontal = -1;
            }
        }
        if (!this.isLeft && !this.isRight) {
            let horizontalChange = 1 * frameAxis;
            if (this.horizontal > horizontalChange) {
                this.horizontal -= horizontalChange
            } else if (this.horizontal < -horizontalChange) {
                this.horizontal += horizontalChange;
            } else if (Math.abs(this.horizontal) <= horizontalChange) {
                this.horizontal = 0;
            }
        }
        //加速减速
        if (this.isGo) {
            this.axis += 0.3;
            if (this.axis > 1) {
                this.axis = 1;
            }
        }
        if (this.isStop) {
            this.axis -= 0.3;
            if (this.axis < -1) {
                this.axis = -1;
            }
        }
        //衰减处理
        if (!this.isGo && !this.isStop) {
            let axisChange = 0.1 * frameAxis;
            if (this.axis > axisChange) {
                this.axis -= axisChange
            } else if (this.axis < -axisChange) {
                this.axis += axisChange;
            } else if (Math.abs(this.axis) <= axisChange) {
                this.axis = 0;
            }
        }
    }

    //按钮事件
    LeftDown() {
        this.left.skin = this.leftUrl[1];
        this.isLeft = true;
    }
    LeftUp() {
        this.left.skin = this.leftUrl[0];
        this.isLeft = false;
    }
    RightDown() {
        this.right.skin = this.rightUrl[1];
        this.isRight = true;
    }
    RightUp() {
        this.right.skin = this.rightUrl[0];
        this.isRight = false;
    }
    GoDown() {
        this.go.skin = this.goUrl[1];
        this.isGo = true;
    }
    GoUp() {
        this.go.skin = this.goUrl[0];
        this.isGo = false;
    }
    StopDown() {
        this.stop.skin = this.stopUrl[1];
        this.isStop = true;
    }
    StopUp() {
        this.stop.skin = this.stopUrl[0];
        this.isStop = false;
    }

    onKeyDown(e) {
        //up
        if (e.keyCode == 38) {
            this.GoDown();
        }
        //down
        else if (e.keyCode == 40) {
            this.StopDown()
        }
        //left
        else if (e.keyCode == 37) {
            this.LeftDown();
        }
        //right
        else if (e.keyCode == 39) {
            this.RightDown();
        }
        //Space
        else if (e.keyCode == 32) {
            GameMgr.Event.event(Nimo_EventType.GameSpeed1On);
        }
    }
    onKeyUp(e) {
        //up
        if (e.keyCode == 38) {
            this.GoUp();
        }
        //down
        else if (e.keyCode == 40) {
            this.StopUp()
        }
        //left
        else if (e.keyCode == 37) {
            this.LeftUp();
        }
        //right
        else if (e.keyCode == 39) {
            this.RightUp();
        }
        //Space
        else if (e.keyCode == 32) {
            GameMgr.Event.event(Nimo_EventType.GameSpeed1Off);
        }

    }

    //运营位置修改事件
    onNativeChange(isVisible?) {
        GameMgr.ConsoleLog("原生广告事件", isVisible)
        if (isVisible) {
            this.left.bottom = 19;
            this.right.bottom = 19;
        } else {
            this.left.bottom = 100;
            this.right.bottom = 100;
        }
    }
}