import D2Component from "../../d2Components/D2Component";
import D2Tween from "../../extensionTools/D2Tween";
import ExtensionTool from "../../extensionTools/ExtensionTool";
import GameMgr from "../../managers/GameMgr";


export default class CoinWindow extends D2Component {
    static instance;
    bg: Laya.Panel;
    imgCoinBg: Laya.Image;
    btnGetCoin: Laya.Image;
    btnCloseGetCoin: Laya.Image;

    init() {
        CoinWindow.instance = this;
        this.self.size(Laya.stage.width, Laya.stage.height);
        this.bg = this.self.getChildByName("bg") as Laya.Panel;
        this.imgCoinBg = this.self.getChildByName("imgCoinBg") as Laya.Image;
        this.btnGetCoin = this.imgCoinBg.getChildByName("btnGetCoin") as Laya.Image;
        this.btnCloseGetCoin = this.imgCoinBg.getChildByName("btnCloseGetCoin") as Laya.Image;
        this.bg.size(Laya.stage.width, Laya.stage.height);
        // this.showGetCoin();
    }

    showGetCoin() {
        this.self.visible = true;
        D2Tween.toScale(this.imgCoinBg, 0, 1, 200, Laya.Ease.backOut);
        ExtensionTool.d2_AddClickEvent(this.btnGetCoin, this, () => {
            GameMgr.Platform.showVideoAd(() => {
                GameMgr.Storage.addCoin(1000);
                this.self.visible = false;
            });
        });
        ExtensionTool.d2_AddClickEvent(this.btnCloseGetCoin, this, () => {
            this.self.visible = false;
        });
    }
}