interface ProductInfo {
	name: string; //商品名称
	id: string; // 商品id
	orderId: string; // 订单号
	price: number; // 商品价格（分为单位）
	number: number; // 商品数量
}

export const enum PlatformType {
	WeChat = 0, // 微信
	OppoMiniGame = 1, // oppo小游戏
	iOS = 2, // ios原生
	Vivo = 4, // vivo小游戏
	TouTiao = 7, // 头条小游戏
	QQ = 8, // QQ小游戏
	Baidu = 9, // 百度小游戏
	UC = 12, // UC小游戏
	Bilibili = 13, // bilibili小游戏
	Empty = 16, // 开发测试平台
	Android = 29, // 安卓原生
	Android_Oppo = 30, // 安卓oppo原生
	Android_Vivo = 31, // 安卓vivo原生
}

export interface IKolaSDK {

	// [必要接口]用户的唯一标识符，必须赋值才能保证统计的正确性
	userId: string;

	// [可选接口]，表示当前的渠道，暂时不用
	channel: string;

	// [必要接口] 游戏退回到后台时调用，参考微信
	OnHide();

	// [必要接口] 游戏展示到前台时调用，参考微信
	OnShow(param: any);

	/**
	 *  [必要接口] 初始化，在调用其他函数前使用
	 * @param platformType 平台类型
	 * @param platformName 平台名称 ios或者android
	 * @param gameId 游戏id，由bi后台分配
	 * @param deviceModel 设备类型，可选
	 * @param logDbName 数据库日志名称，由bi后台分配
	 * @param logUrl 日志访问服务器地址，由bi后台分配
	 * @param remoteSettingUrl 远程配置服务器地址，由bi后台分配
	 * @param sysInfo 目前传null即可
	 */
	Initialize(platformType: PlatformType | number, platformName: string, gameId: string, deviceModel: string, 
		logDbName: string, logUrl: string, remoteSettingUrl: string, sysInfo: any);

	// [可选接口] 加载远程配置文件
	// ---------------------------------------------------------------------------------------------------
	LoadRemoteSetting(onSuc: Function, onFailed: Function);

	/**
	 *  [必要接口] 事件打点
	 * @param name 事件名称
	 * @param params 事件参数，参数自定义，支持数值跟文本 比如:{scene:升级}，后台会根据参数分别统计，但需要后台配置，需要请联系相关人员
	 * @param levelIndex 关卡索引，如果不需要可以随便填0
	 * @param retryCnt 发送失败重试次数，不填就是1次
	 */
	LogEvent(name: string, params?: any, levelIndex?: string | number, retryCnt?: number);

	// 充值成功事件打点
	LogPay(product: ProductInfo, channel: string, levelIndex: string | number)

	// 关卡
	// ----------------------------------------------------------------------------------------------------

	/**
	 *  [可选接口] 关卡开始打点
	 * @param levelIndex 关卡索引
	 * @param property 用来统计每次进入关卡数值分布，比如: {金币:1000, 体力:100}
	 */
	LogStartLevel(levelIndex: string | number, property?: any);

	/**
	 *  [可选接口] 关卡结束打点
	 * @param levelIndex 关卡索引
	 * @param win 是否胜利
	 */
	LogFinishLevel(levelIndex: string | number, win: boolean);

	/**
	 *  [可选接口] 使用道具打点
	 * @param scene 使用场景，比如：复活
	 * @param levelIndex 关卡索引
	 * @param itemId 道具id
	 * @param count 道具数量
	 */
	LogUseItem(scene: string, levelIndex: string | number, itemId: string, count: number);

	/**
	 *  [可选接口] 获得道具打点
	 * @param scene 使用场景，比如：开宝箱
	 * @param levelIndex 关卡索引
	 * @param itemId 道具id
	 * @param count 道具数量
	 */
	LogGetItem(scene: string, levelIndex: string | number, itemId: string, count: number);

	/**
	 *  [可选接口] 分享打点
	 * @param scene 使用场景
	 * @param suc 是否成功
	 * @param levelIndex 关卡索引，如果不需要可以随便填0
	 * @param shareId 玩家的唯一标识id
	 * @param nickName 玩家的昵称
	 * @param videoId 暂时忽略
	 * @param klVidelId 暂时忽略
	 */
	LogShare(scene: string, suc: boolean, levelIndex: string | number, shareId: string, nickName: string, videoId?: string, klVidelId?: string);

	/**
	 *  [可选接口] 视频广告打点
	 * @param scene 使用场景
	 * @param finish 是否播放完成
	 * @param levelIndex 关卡索引，如果不需要可以随便填0
	 */
	LogPlayVideoAd(scene: string, finish: boolean, levelIndex: string | number);

	// 原生广告
	// ----------------------------------------------------------------------------------------------------
	// [可选接口] 当原生广告加载成功后调用
	OnNativeAdLoaded();

	/**
	 *  [可选接口] 当原生广告展示时调用，只要游戏有展示行为就统计，不需要过滤
	 * @param scene 使用场景
	 * @param adId 广告id
	 */
	OnNativeAdShow(scene: string, adId: string);

	/**
	 *  [可选接口] 当原生广告点击时调用，只要用户有点击行为就统计，不需要过滤
	 * @param scene 使用场景
	 * @param adId 广告id
	 */
	OnNativeAdClick(scene: string, adId: string);


	// 下面的接口忽略不管
	// ----------------------------------------------------------------------------------------------------

	// 导出统计
	// ----------------------------------------------------------------------------------------------------
	OnAppIconShow(scene: string, appCfg, iconIdx: number);

	// 当导出icon隐藏时调用
	OnAppIconHide(scene: string, appCfg, iconIdx: number);

	// 当导出icon跳转时调用
	OnNavigateToApp(scene: string, successed: boolean, appConfig, iconIdx: number);

	// 内部函数
	// ----------------------------------------------------------------------------------------------------
	// extJsonObj 格式：{name:extJson}
	LogEvent_(name: string, params: any, levelIndex?: string | number, retryCnt?: number, extJsonObj?: any, noChannelInfo?: any);

	httpGet(url: string, suc?: Function, failed?: Function, caller?: any, header?: any);

	httpPostJson(url: string, params: string, suc?: Function, failed?: Function, caller?: any, header?: any);

}

export const KolaSDK: IKolaSDK = window['KolaSDK'];