import FxController from "../../extensionTools/mods_Nimo/FxController";
import Tools from "../../extensionTools/mods_Nimo/Tools";
import CarComponent, { emojiType } from "./CarComponent";
import CarHitCheck_AI from "./HitCheck/CarHitCheck_AI";
import CarHitCheck_Player from "./HitCheck/CarHitCheck_Player";

export default class Car_AI extends Laya.Script3D {

    carComp: CarComponent;

    limitSpeed;

    onEnable(): void {
        this.carComp = this.owner.getComponent(CarComponent);
        this.axis = 1
        this.change();
        Laya.timer.once(Tools.randomInt(10000, 20000), this, this.showFace);
    }

    onDisable(): void {
        Laya.timer.clearAll(this);
    }
    axis = 0;
    horizontal = 0;

    isRuning = false;
    wayLeft = true;//0 1

    carPushEffect;
    change() {
        this.wayLeft = !this.wayLeft
        let time = Tools.randomInt(1000, 3000);
        let distance = CarHitCheck_Player.player.carComp.tracker.progressDistance - this.carComp.tracker.progressDistance;
        if (!this.isRuning) {
            if (distance > 25) {
                console.log("氮气冲刺！")
                this.isRuning = true;
                this.limitSpeed = 6
                let hitParent = this.carComp.carPushEffect;
                this.carPushEffect = FxController.playEffectReturn("PushEffect", hitParent);
            } else {
                this.limitSpeed = Tools.randomNumber(3, 4);
            }
        } else {
            if (distance < -15) {
                this.isRuning = false;
                this.carPushEffect.destroy();
            }
        }
        Laya.timer.once(time, this, this.change);
    }
    onUpdate() {
        let targetHorizontal = 0;
        if (this.wayLeft) {
            targetHorizontal = -3;
            if (this.carComp.horizontal > targetHorizontal) {
                this.horizontal = -1;
            }
            else {
                this.horizontal = 0;
            }
        } else {
            targetHorizontal = 3;
            if (this.carComp.horizontal < targetHorizontal) {
                this.horizontal = 1;
            } else {
                this.horizontal = 0;
            }
        }
        if (this.carComp.tracker.speedFactor > this.limitSpeed) {
            this.axis = 0;
        } else {
            this.axis = 1;
        }

    }

    showFace() {
        if (Tools.percent(0.3)) {
            this.carComp.emojiPlay(emojiType.LaughCry);
        } else if (Tools.percent(0.5)) {
            this.carComp.emojiPlay(emojiType.Cry);
        } else {
            this.carComp.emojiPlay(emojiType.Cool);
        }
        Laya.timer.once(Tools.randomInt(10000, 20000), this, this.showFace);
    }
}