
import ImageButton from "../../extensionTools/mods_Nimo/ctrl/ImageButton";
import { Nimo_EventType } from "../../extensionTools/mods_Nimo/Nimo_EventType";
import GameMgr from "../../managers/GameMgr";


export default class AttackButton3 extends ImageButton {
    static instance: AttackButton3
    colorMask;
    init() {
        AttackButton3.instance = this;
        this.colorMask = this.color.mask;
        this.color.set_visible(true);
        this.black.set_visible(true);
        this.btnTween = new Laya.Tween()
        this.tween = new Laya.Tween();
    }

    press() {

        GameMgr.Event.event(Nimo_EventType.GameAttack3On);
    }
    release() {

        GameMgr.Event.event(Nimo_EventType.GameAttack3Off);
    }


    btnTween;
    pressReal() {
        let btn = this.owner as Laya.Image;
        this.btnTween.to(btn, { scaleX: 0.9, scaleY: 0.9 }, 60)
        if (!this.isActive)
            return;
        this.press();
        //表现
        this.isPress = true;
    }
    releaseReal() {
        let btn = this.owner as Laya.Image;
        this.btnTween.to(btn, { scaleX: 1, scaleY: 1 }, 60)
        if (!this.isActive)
            return;
        this.release()
        //表现
        this.isPress = false;
    }
    tween: Laya.Tween;
    unActive(time) {
        this.isActive = false;
        this.isPress = false;
        this.colorMask.x = -133;
        let self = this;
        this.tween.complete();
        this.tween.to(this.colorMask, { x: -16 }, time, null, Laya.Handler.create(this, () => { }));
    }
    goActive() {

        this.tween.complete();
        this.isActive = true;
    }
}