    //trackEvent('ViewProductDetails',{'Category':'家电','ItemName':'西门子冰箱'});
    // 数值型属性值
    //trackEvent('Pay',{'PayAmount':6999});
    public static TrackEvent(eventid: string, params = "") {
        switch (GameMgr.GlData.platform) {
            case PlatformType.WX:
                var wx = Laya.Browser.window.wx;
                if (!wx) return;
                wx.uma.trackEvent(eventid, params);
                break;
            case PlatformType.OPPO:
                break;
            case PlatformType.VIVO:
                break;
        }

    }
