import { EventType, UiSceneType } from "../configs/EnumType";
import GameMgr from "./GameMgr";

export default class UiMgr {
    private scenes: Laya.Scene[];
    private topScene: Laya.Scene;

    constructor() {
        this.scenes = [];
    }

    get TopScene() {
        return this.topScene;
    }

    openScene(url: UiSceneType, closeOther: boolean = false, param?: any, callBack?: Function, parent?: Laya.Node) {
        let scene = this.scenes.filter((w) => { return w.url == url; });
        if (scene && scene.length > 0) {
            GameMgr.ConsoleLog("已打开此场景", scene);
            scene[0].close();
            this.scenes.splice(this.scenes.indexOf(scene[0]), 1);
        }

        /**关闭其他页面 */
        if (closeOther) {
            for (let i = this.scenes.length - 1; i >= 0; i--) {
                this.closeScene(this.scenes[i].url as UiSceneType);
            }
        }

        Laya.Scene.open(url, false, param, Laya.Handler.create(this, s => {
            //重置父对象，此操作会再次触发生命周期OnEnable
            if (parent) parent.addChild(s);
            // else Laya.stage.addChild(s);

            this.scenes.push(s);
            this.topScene = s;

            callBack && callBack(s);
            GameMgr.Event.event(EventType.Open_Ui_Event, s.url);
        }))
    }

    closeScene(url?: UiSceneType, param?: any) {
        let scene = null;
        if (url) {
            this.scenes.forEach(s => {
                if (s.url == url) {
                    scene = s;
                    return;
                }
            });
        }
        else if (this.topScene) {
            scene = this.topScene;
        }

        if (scene && scene instanceof Laya.Scene) {
            this.scenes.splice(this.scenes.indexOf(scene), 1);
            if (this.scenes.length > 0) this.topScene = this.scenes[this.scenes.length - 1];
            else this.topScene = null;

            scene.close();
            GameMgr.Event.event(EventType.Close_Ui_Event, { url: scene.url, param: param });
        }
    }
}