
import ImageButton from "../../extensionTools/mods_Nimo/ctrl/ImageButton";
import { Nimo_EventType } from "../../extensionTools/mods_Nimo/Nimo_EventType";
import GameMgr from "../../managers/GameMgr";


export default class emojiCryLaughButton extends ImageButton {
    static instance: emojiCryLaughButton
    init() {
        emojiCryLaughButton.instance = this;
    }
    btnTween: Laya.Tween;
    press() {
        let btn = this.owner as Laya.Image;
        if (this.btnTween)
            this.btnTween.clear()
        this.btnTween = Laya.Tween.to(btn, { scaleX: 0.9, scaleY: 0.9 }, 60)
        GameMgr.Event.event(Nimo_EventType.GameEmojiCryLaugh);
    }
    release() {
        let btn = this.owner as Laya.Image;
        if (this.btnTween)
            this.btnTween.clear()
        this.btnTween = Laya.Tween.to(btn, { scaleX: 1, scaleY: 1 }, 60)
        // GameMgr.Event.event(Nimo_EventType.GameEmojiCool);
    }
}