import { SoundType } from "../configs/EnumType";
import GameMgr from "./GameMgr";

export default class SoundMgr {
    private _soundFile: string[];
    private _pathRoot: string;
    private _soundCtx: any;

    constructor() {
        this._pathRoot = "res/sound/";
        this._soundCtx = {};

        this._soundFile = [];
        for (var key in SoundType) {
            let sound = SoundType[key];
            this._soundFile.push(sound);
        }
        GameMgr.ConsoleLog("音效：", this._soundFile);
        this.init();
    }

    init() {
        let path = this._pathRoot;
        let file = "";
        let soundFile = this._soundFile;
        let soundCount = this._soundFile.length;
        for (let i = 0; i < soundCount; ++i) {
            file = soundFile[i];
            let innerAudioContext = new Laya.SoundChannel;
            innerAudioContext.url = path + file + ".mp3";

            //innerAudioContext.loops = 0; 循环
            Laya.SoundManager.addChannel(innerAudioContext);
            this._soundCtx[file] = true;
        }

        let loadUrl = ["res/sound/" + SoundType.BGM + ".mp3"];
        Laya.loader.load(loadUrl, Laya.Handler.create(this, () => { this.playMusic(SoundType.BGM) }));
        // Laya.SoundManager.autoReleaseSound = false;
        Laya.SoundManager.autoStopMusic = true;
    }

    play(name) {
        if (this._soundCtx[name]) {
            Laya.SoundManager.playSound(this._pathRoot + name + ".mp3");
        }
    }

    stop(name) {
        if (this._soundCtx[name]) {
            Laya.SoundManager.stopSound(this._pathRoot + name + ".mp3");
        }
    }

    nowLoopSound = [];
    playLoop(name) {
        let isPlaying = false;
        for (let i = 0; i < this.nowLoopSound.length; i++) {
            if (this.nowLoopSound[i] == name) {
                isPlaying = true;
                break;
            }
        }
        if (!isPlaying) {
            this.nowLoopSound.push(name);
        }
        if (this._soundCtx[name] && !isPlaying) {
            Laya.SoundManager.playSound(this._pathRoot + name + ".mp3", 0);
        }
    }
    stopLoop(name) {
        for (let i = 0; i < this.nowLoopSound.length; i++) {
            if (this.nowLoopSound[i] == name) {
                this.nowLoopSound.splice(i, 1);
                break;
            }
        }
        if (this._soundCtx[name]) {
            Laya.SoundManager.stopSound(this._pathRoot + name + ".mp3");
        }
    }
    setSoundVolume(name, value) {
        if (this._soundCtx[name]) {
            Laya.SoundManager.setSoundVolume(value, this._pathRoot + name + ".mp3")
        }
    }
    setMusicVolume(value) {
        Laya.SoundManager.setMusicVolume(value);
    }

    currentBgm: string;
    playMusic(name?) {
        Laya.SoundManager.stopMusic();
        if (name) {
            Laya.SoundManager.playMusic(this._pathRoot + name + ".mp3", 0);
            this.currentBgm = name;
        }
        else if (this.currentBgm) {
            Laya.SoundManager.playMusic(this._pathRoot + this.currentBgm + ".mp3", 0);
        }
        else {
            Laya.SoundManager.playMusic(this._pathRoot + SoundType.BGM + ".mp3", 0);
        }
    }

    stopMusic() {
        Laya.SoundManager.stopMusic();
    }
}