import { EventType, SoundType, UiSceneType } from "../configs/EnumType";
import D2Tween from "../extensionTools/D2Tween";
import ExtensionTool from "../extensionTools/ExtensionTool";
import Data from "../extensionTools/mods_Nimo/Data";
import NimoForFrame from "../extensionTools/mods_Nimo/NimoForFrame";
import { Nimo_EventType } from "../extensionTools/mods_Nimo/Nimo_EventType";
import GameMgr from "../managers/GameMgr";
import StorageMgr from "../managers/StorageMgr";
import qy_tt from "../platforms/cpLogicalMgr/QingYou/qy_tt";
import UiScene from "./UiScene";

/**游戏页 */
export default class Game extends UiScene {
    public static instance: Game;
    private AirUI: Laya.Image;
    private gradeUI: Laya.Image;
    private guideUI: Laya.Image;

    public nativeAd: Laya.Image;

    private RebornPanel: Laya.Image;
    private reborn: Laya.Button;
    private giveup: Laya.Button;
    recordStartBtn: Laya.Button;
    recordEndBtn: Laya.Button;


    //——————————————个人向——————————————————
    //数字倒数UI
    numberCountAni: Laya.Animation;

    init() {
        Game.instance = this;
        //显示数字倒数
        if (GameMgr.Storage.storageData.level != 1) {
            Laya.timer.once(1500, this, this.NumberCountOn)
        } else {
            this.guideUI.set_visible(true);
            let button = this.guideUI.getChildByName("button") as Laya.Button;
            ExtensionTool.d2_AddClickEvent(button, this, this.guideClose);
        }
        // this.NumberCountOn()
        D2Tween.toScale(this.AirUI, 1, 1.2, 750, null, null, false, true);
        this.gradeUI.set_visible(false);
    }
    //初始化事件
    initEvent() {
        GameMgr.Event.on(EventType.AirUI_On, this, this.AirUIOn);
        GameMgr.Event.on(EventType.AirUI_Off, this, this.AirUIOff);
        GameMgr.Event.on(EventType.Over_Game_Event, this, this.GameEnd);
        GameMgr.Event.on(EventType.Player_Dead_Event, this, this.playerRebornOn);
        ExtensionTool.d2_AddClickEvent(this.reborn, this, this.playerReborn);
        ExtensionTool.d2_AddClickEvent(this.giveup, this, this.playerGiveUp);
        ExtensionTool.d2_AddClickEvent(this.recordStartBtn, this, this.recordStart);
        ExtensionTool.d2_AddClickEvent(this.recordEndBtn, this, this.recoredEnd);
    }
    recordStart() {
        this.recordStartBtn.set_visible(false);
        this.recordEndBtn.set_visible(true);
        qy_tt.startRecord();
    }
    recoredEnd() {
        if (qy_tt.stopRecord()) {
            this.recordStartBtn.set_visible(true);
            this.recordEndBtn.set_visible(false);
        }
    }

    GameEnd(e) {
        GameMgr.ConsoleLog("游戏结果", e.isWin)
        if (e.isWin) {
            Data.sound.play(SoundType.Win);
            GameMgr.Storage.passLevel();
        } else {
            Data.sound.play(SoundType.Fail);
        }
        GameMgr.UI.closeScene(UiSceneType.Game);
    }

    //个人向
    NumberCountOn() {
        this.numberCountAni.play(0, false);
        Data.sound.play(SoundType.Count);
        Laya.timer.once(1000, this, () => {
            Data.sound.play(SoundType.Count);
        })
        Laya.timer.once(2000, this, () => {
            Data.sound.play(SoundType.Count);
        })
        Laya.timer.once(3000, this, () => {
            Data.sound.play(SoundType.Go);
        })
        Laya.timer.once(3000, this, this.NumberCountOff);
    }
    NumberCountOff() {
        GameMgr.Event.event(Nimo_EventType.GameRealStart);
        let value = this.gradeUI.getChildByName("value") as Laya.FontClip;
        value.value = GameMgr.Storage.storageData.level.toString();
        this.gradeUI.set_visible(true);
    }

    AirUIOn() {
        this.AirUI.set_visible(true);
    }
    AirUIOff() {
        this.AirUI.set_visible(false);
    }

    guideClose() {
        this.guideUI.set_visible(false);
        this.NumberCountOn();
    }

    //#region 玩家复活面板
    playerRebornOn() {
        this.RebornPanel.visible = true;
    }
    playerReborn() {
        GameMgr.Platform.showVideoAd(() => {
            GameMgr.Event.event(EventType.Player_Reborn_Event);
            this.RebornPanel.visible = false;
        })
    }
    playerGiveUp() {
        GameMgr.Event.event(EventType.Player_GiveUp_Event);
    }
    //#endregion

    onClosed() {
        super.onClosed();
        D2Tween.clearTween(this.AirUI);
    }
}