import { EventType } from "../../configs/EnumType";
import { Nimo_EventType } from "../../extensionTools/mods_Nimo/Nimo_EventType";
import GameMgr from "../../managers/GameMgr";
import Game from "../../uiScenes/Game";


export default class SpeedUI extends Laya.Script {

    min = -135;
    max = 125;
    arrow: Laya.Image;
    value: Laya.FontClip;
    label: Laya.Label;

    static instance: SpeedUI;
    onEnable() {
        SpeedUI.instance = this;
        this.arrow = this.owner.getChildByName("arrow") as Laya.Image;
        this.value = this.owner.getChildByName("value") as Laya.FontClip;
        this.label = this.owner.getChildByName("label") as Laya.Label;
        GameMgr.Event.on(Nimo_EventType.GameCarSpeedChange, this, this.speedChange);
    }
    onDisable() {
        GameMgr.Event.off(Nimo_EventType.GameCarSpeedChange, this, this.speedChange);
    }
    // 0~7
    speedChange(value) {
        this.arrow.rotation = this.min + (this.max - this.min) * value / 7
        this.label.text = Math.floor((value / 7 * 200)).toString();
        this.value.value = Math.floor((value / 7 * 200)).toString();
    }
}
