import GameConfig from "../../../GameConfig";
import GameMgr from "../../../managers/GameMgr";
import { TouchEventType } from "./TouchScan";



export default class JoystickComponentRight extends Laya.Script {

    static instance;
    static xAxis = 0;
    static yAxis = 0;
    xAxis = 0;
    yAxis = 0;
    stopSpeed = 0.1;
    onUpdate() {
        if (!this.isPressed) {
            if (Math.abs(this.xAxis) < this.stopSpeed) {
                this.xAxis = 0;
            } else {
                this.xAxis = Laya.MathUtil.lerp(this.xAxis, 0, 0.1);
            }
            if (Math.abs(this.yAxis) < this.stopSpeed) {
                this.yAxis = 0;
            } else {
                this.yAxis = Laya.MathUtil.lerp(this.yAxis, 0, 0.1);
            }
        }
        JoystickComponentRight.xAxis = this.xAxis;
        JoystickComponentRight.yAxis = this.yAxis;
    }

    self;
    joyBg: Laya.Image;
    joystick: Laya.Image;

    firstTouchPoint: Laya.Point;

    isPressed: boolean;
    canMove: boolean = true;
    angle: number;


    onEnable() {
        JoystickComponentRight.instance = this;
        this.self = this.owner;
        this.joyBg = this.self.getChildAt(0) as Laya.Image;
        this.joystick = this.joyBg.getChildAt(0) as Laya.Image;

        this.self.width = Laya.stage.width;
        this.self.height = Laya.stage.height;


        GameMgr.Event.on(TouchEventType.start, this, this.onMouseDownEvent)
        GameMgr.Event.on(TouchEventType.move, this, this.onMouseMoveEvent);
        GameMgr.Event.on(TouchEventType.end, this, this.onMouseUpEvent);
    }
    onDestroy() {
        GameMgr.Event.off(TouchEventType.start, this, this.onMouseDownEvent)
        GameMgr.Event.off(TouchEventType.move, this, this.onMouseMoveEvent);
        GameMgr.Event.off(TouchEventType.end, this, this.onMouseUpEvent);
    }
    onMouseDownEvent(e) {
        if (!this.firstTouchId && (e.stageX > Laya.stage.width / 2)) {
            this.firstTouchId = e.touchId;
            this.firstTouchPoint = new Laya.Point(e.stageX, e.stageY);
            this.joyBg.pos(e.stageX, e.stageY - (GameConfig.height - this.self.height));
            this.isPressed = true;
        }
    }
    onMouseUpEvent(e) {
        if (this.firstTouchId == e.touchId) {
            this.firstTouchId = null;

            this.isPressed = false;

            // this.joyBg.pos(Laya.stage.width -147, 600);
            this.joyBg.pos(Laya.stage.width - 147, 1200)
            this.joystick.pos(147, 147);
        }
    }
    firstTouchId: number;
    onMouseMoveEvent(e) {
        if (this.firstTouchId == e.touchId) {
            this.moveCtrl(e);
        }
    }

    moveCtrl(e) {
        if (this.isPressed) {
            var vec1 = new Laya.Vector2(0, -1);
            var vec2 = new Laya.Vector2(e.stageX - this.firstTouchPoint.x, e.stageY - this.firstTouchPoint.y);
            var touchMoveDis = Laya.Vector2.scalarLength(vec2);
            if (touchMoveDis == 0) {
                this.canMove = false;
            }
            else {
                //显示摇杆
                if (touchMoveDis > 5 && !this.canMove) {
                    this.joyBg.pos(this.firstTouchPoint.x, this.firstTouchPoint.y - (GameConfig.height - this.self.height));
                }
                var lastStatus = this.canMove;
                this.canMove = touchMoveDis > 5;
                var cosAngle = Laya.Vector2.dot(vec1, vec2) / (Laya.Vector2.scalarLength(vec1) * Laya.Vector2.scalarLength(vec2));
                var hudu = Math.acos(cosAngle);
                this.angle = 180 * hudu / Math.PI;

                if (e.stageX < this.firstTouchPoint.x) {
                    this.angle = -this.angle;
                }

                let xAxis = (e.stageX - this.firstTouchPoint.x) / 76
                let yAxis = -(e.stageY - this.firstTouchPoint.y) / 76
                if (xAxis > 1) {
                    xAxis = 1;
                } else if (xAxis < -1) {
                    xAxis = -1;
                }
                if (yAxis > 1) {
                    yAxis = 1;
                } else if (yAxis < -1) {
                    yAxis = -1;
                }
                this.xAxis = xAxis;
                this.yAxis = yAxis;
            }

            if (this.canMove) {
                if (e.stageX < this.firstTouchPoint.x) {
                    this.joystick.x = 147 + Math.max(-76, (e.stageX - this.firstTouchPoint.x));
                }
                else {
                    this.joystick.x = 147 + Math.min(76, (e.stageX - this.firstTouchPoint.x));
                }

                if (e.stageY < this.firstTouchPoint.y) {
                    this.joystick.y = 147 + Math.max(-76, (e.stageY - this.firstTouchPoint.y));
                }
                else {
                    this.joystick.y = 147 + Math.min(76, (e.stageY - this.firstTouchPoint.y));
                }
                //限制滑块距离
                if (touchMoveDis > 76) {
                    var v = new Laya.Vector2(e.stageX - this.firstTouchPoint.x, e.stageY - this.firstTouchPoint.y);
                    Laya.Vector2.normalize(v, v);
                    Laya.Vector2.scale(v, 76, v);
                    this.joystick.pos(136 + v.x, 136 + v.y);
                }
            }
        }
    }
}