import { EventType } from "../configs/EnumType";
import GameMgr from "../managers/GameMgr";

/**平台管理类 */
export default class Platform {
    /**Banner广告Id */
    bannerId: string = '';
    /**激励视频广告Id */
    videoId: string = '';
    /**插屏广告Id */
    interId: string = '';
    /**格子广告Id */
    gridId: string = '';
    /**原生广告Id */
    nativeId: string = '';
    /**九宫格广告Id */
    boxPortalId: string = '';

    /**Banner广告组件 */
    bannerAd: any;
    /**激励视频广告组件 */
    videoAd: any;
    /**插屏广告组件 */
    interAd: any;
    /**格子广告组件 */
    gridAd: any;
    /**原生广告组件 */
    nativeAd: any;
    /**九宫格广告组件 */
    boxPortalAd: any;

    /**激励视频广告播放完成回调 */
    videoFinishCallback: Function;
    /**激励视频广告播放取消回调 */
    videoCancelCallback: Function;

    /**九宫格广告关闭回调 */
    boxPortalCloseCallback: Function;

    /**平台api对象 */
    platformApi: any;
    /**系统信息 */
    sysInfo: any;

    /**是否支持分包 */
    canSubPackage: boolean = false;

    constructor() {
        this.initAd();
        this.regisiterCallback();
    }

    //#region 广告
    /**初始化广告 */
    initAd() { console.warn("未初始化广告组件"); }
    /**创建Banner广告 */
    createBannerAd(isShow?: boolean) { console.warn("暂不支持Banner广告"); }
    /**创建Video广告 */
    createVideoAd() { console.warn("暂不支持激励视频广告"); }
    /**创建插屏广告 */
    createInterAd() { console.warn("暂不支持插屏广告"); }
    /**创建格子广告 */
    createGridAd() { console.warn("暂不支持格子广告"); }
    /**创建原生广告 */
    createNativeAd(id?: string, cb?: Function): NativeAd { console.warn("暂不支持原生广告"); return null }
    /**创建九宫格广告 */
    createBoxProtalAd() { console.warn("暂不支持九宫格广告"); }

    /**展示Banner广告 */
    showBannerAd() { }
    /**隐藏Banner广告 */
    hideBannerAd() { }
    /**销毁Banner广告 */
    destroyBannerAd() { }

    /**展示激励视频广告 */
    showVideoAd(finishCB?: Function, cancelCB?: Function) {
        finishCB && finishCB();
    }

    /**销毁激励视频广告 */
    destroyVideoAd() { }

    /**展示插屏广告 */
    showInterAd() { }
    /**销毁插屏广告 */
    destroyInterAd() { }

    /**展示格子广告 */
    showGridAd() { }
    /**隐藏格子广告 */
    hideGridAd() { }
    /**销毁格子广告 */
    destroyGridAd() { }

    /**展示原生广告 */
    showNativeAd(ad: NativeAd, cb?: Function) { };
    /**隐藏原生广告 */
    loadNativeAd(ad: NativeAd, cb?: Function) { };
    /**销毁原生广告 */
    destroyNativeAd(ad: NativeAd) { }

    /**展示九宫格广告 */
    showBoxProtalAd(closeCB?: Function, successCB?: Function) {
        successCB && successCB();
    }
    /**销毁九宫格广告 */
    destroyBoxProtalAd() { }
    //#endregion

       //#region 录屏
       initRecorder() { }
       recordStart() { }
       recordPause() { }
       recordResume() { }
       recordStop(cb?) { }
       clipVideo() { }
       shareVideo(cb?, topics = ["", ""]) { }
       //#endregion
    //#region 通用

    /**获取系统信息 */
    getSystemInfoSync() {
        if (!this.sysInfo && this.platformApi) {
            this.sysInfo = this.platformApi.getSystemInfoSync();
        }
        return this.sysInfo;
    }
    /**弹窗 */
    showToast(msg: string, duration?: number) { }
    /**短震动 */
    vibrateShort() {
        if (this.platformApi)
            this.platformApi.vibrateShort();
    }
    /**长震动 */
    vibrateLong() {
        if (this.platformApi)
            this.platformApi.vibrateLong();
    }
    /**
     * 分包 
     * 需配置game.json
     * "subpackages": [
         {
            "name": "scene", //包名
            "root": "res/scene" //路径
         }
        ],
     */
    loadSubpackage(loadName: string, caller: any, callback: Function) {
        if (!this.platformApi) return;
        let loadTask = this.platformApi.loadSubpackage({
            name: loadName,
            success: function (res) {
                GameMgr.ConsoleLog("分包加载成功：", loadName);
                callback.call(res, caller, true);
            },
            fail: function (res) {
                GameMgr.ConsoleError("分包加载失败：", loadName, JSON.stringify(res));
                callback.call(res, caller, false);
            }
        })

        loadTask.onProgressUpdate(res => {
            GameMgr.ConsoleLog("分包================", JSON.stringify(res));
        });
    }
    /**同步加载多个分包 */
    loadSubpackagesSync(loadNames: string[], caller, callback) {
        if (!this.platformApi) return;
        var loadCount = loadNames.length;
        for (let i = 0; i < loadCount; i++) {
            this.loadSubpackage(loadNames[i], caller, (caller, isSuccess) => {
                if (isSuccess && --loadCount == 0) {
                    GameMgr.ConsoleLog("全部分包加载完成");
                    callback.call(this, caller, isSuccess);
                }
            });
        }
    }

    /**前后台切换回调 */
    regisiterCallback() {
        if (this.platformApi) {
            this.platformApi.offShow(this.onShowEvent);
            this.platformApi.onShow(this.onShowEvent);
            this.platformApi.offHide(this.onHideEvent);
            this.platformApi.onHide(this.onHideEvent);
        }
    }

    onShowEvent(e) {
        GameMgr.Event.event(EventType.Platform_Wake_Event, { param: e });
    }

    onHideEvent(e) {
        GameMgr.Event.event(EventType.Platform_Sleep_Event, { param: e });
    }


    /**创建桌面图标 */
    createShortcut(cb?: Function) { }
    /**是否已创建桌面图标 */
    hasShortcut(cb?: Function) { }

    /**打开调试 */
    setEnableDebug() { }

    //#endregion
}

export class NativeAd {
    nativeAd: any;
    title: string;
    desc: string;
    imgUrl: string;
    adId: string;
}