import { SoundType } from "../../configs/EnumType";
import CamFollowBox, { CamFollowEvent } from "../../extensionTools/mods_Nimo/cam/CamFollowBox";
import CarControlComponent from "../../extensionTools/mods_Nimo/ctrl/CarControlComponent";
import JoystickComponentLeft from "../../extensionTools/mods_Nimo/ctrl/JoystickComponentLeft";
import JoystickComponentRight from "../../extensionTools/mods_Nimo/ctrl/JoystickComponentRight";
import Data from "../../extensionTools/mods_Nimo/Data";
import FxController from "../../extensionTools/mods_Nimo/FxController";
import NimoForFrame from "../../extensionTools/mods_Nimo/NimoForFrame";
import { Nimo_EventType } from "../../extensionTools/mods_Nimo/Nimo_EventType";
import Tools from "../../extensionTools/mods_Nimo/Tools";
import GameMgr from "../../managers/GameMgr";
import MachineHitCheck from "./HitCheck/MachineHitCheck";
import MachineHitCheck_AI from "./HitCheck/MachineHitCheck_AI";
import MachineHitCheck_Player from "./HitCheck/MachineHitCheck_Player";
import MachineComponent from "./MachineComponent";
import Machine_AI from "./Machine_AI";


export default class MachineComponent_AI extends MachineComponent {
    moveSpeed = 0.75;
    initEvent() {

    }
    offEvent() {

    }
    specialEnd() {
        if (this.skill1Effect)
            this.skill1Effect.destroy();
        Laya.timer.clear(this, this.skill1Off);
        Laya.timer.clear(this, this.skill1Update);
        this.isAttack = false;
        this.isInvincible = false;
        Laya.timer.clear(this, this.skill1Update);
        GameMgr.Event.event(Nimo_EventType.GameAIDead);
    }
    attackInvertal
    specialInit() {
        //特效
        this.skill2Effect = this.effect.getChildByName("BoomEffect") as Laya.Sprite3D;
        //属性赋值
        this.attack = Data.AIAttack[(GameMgr.Storage.storageData.level - 1) % Data.AIAttack.length];
        this.skill1CD = Data.AISkill1CD[(GameMgr.Storage.storageData.level - 1) % Data.AISkill1CD.length]
        this.skill2CD = Data.AISkill2CD[(GameMgr.Storage.storageData.level - 1) % Data.AISkill2CD.length]
    }
    goDownSpecial() {
        GameMgr.ConsoleLog("AI倒下！！")
        this.skill1Off();
        // this.skill2Off();
    }


    getControlComp() {
        this.controlComp = this.player.addComponent(Machine_AI);
    }
    getColliderComp() {
        this.colliderComp = this.player.addComponent(MachineHitCheck_AI)
    }

    way = new Laya.Vector3(0, 0, 0);
    MoveControl() {
        let way = this.way
        if (way.x == 0 && way.z == 0) {
            this.anim.getControllerLayer(1).defaultWeight = 0;
            return;
        }
        let joyAngle;
        if (way.z == -1 && way.x == 0) {
            joyAngle = 0
        } else if (way.z == 1 && way.x == 0) {
            joyAngle = 180;
        }
        else {
            let hudu = Math.atan2(way.x, -way.z);
            joyAngle = hudu * 180 / Math.PI
        }
        let angle = joyAngle;

        let wayAxis = Laya.Vector3.scalarLength(way);
        if (wayAxis > 1) {
            wayAxis = 1;
        }

        this.Move(angle, wayAxis);
    }
    hurtSpecial() {

        if (!NimoForFrame.isPlayerCar)
            Data.sound.play(SoundType.PlayerSwordHit);
    }
    //攻击配置
    goAttackSpecial() {
        let target = this.controlComp.target;
        if (target) {
            let way = new Laya.Vector3(target.horizontal - this.colliderComp.horizontal, 0, target.axis - this.colliderComp.axis)
            this.getWayAngle(way);
        }
    }
    getWayAngle(way) {
        if (way.x == 0 && way.z == 0) {
            this.anim.getControllerLayer(1).defaultWeight = 0;
            return;
        }
        let joyAngle;
        if (way.z == -1 && way.x == 0) {
            joyAngle = 0
        } else if (way.z == 1 && way.x == 0) {
            joyAngle = 180;
        }
        else {
            let hudu = Math.atan2(way.x, -way.z);
            joyAngle = hudu * 180 / Math.PI
        }
        let angle = joyAngle;
        Tools.setTransRotEulerY(this.model.transform, -angle)
    }
    attackDoSpecial() {
        // let comp: MachineHitCheck_AI = this.colliderComp as MachineHitCheck_AI;
        // this.controlComp.target.machine.goHurt();
        let ais = MachineHitCheck_AI.objs;
        let aiId = this.colliderComp.aiId;
        let colliders: MachineHitCheck[] = []
        for (let i = 0; i < ais.length; i++) {
            if (i != aiId) {
                if (ais[i])
                    colliders.push(ais[i])
            }
        }
        if (MachineHitCheck_Player.player) {
            colliders.push(MachineHitCheck_Player.player)
        }

        let nowAttackId = this.attackCombe;
        let range = this.attackRange[nowAttackId];
        for (let i = 0; i < colliders.length; i++) {
            let machine = colliders[i].machine;
            let hitEvent = Tools.CircleCheck_2D(this.colliderComp.axis, this.colliderComp.horizontal, range, colliders[i].axis, colliders[i].horizontal, colliders[i].radius)
            if (hitEvent.isHit) {
                let dir = Tools.Vector3Reduce(machine.player.transform.position.clone(), this.player.transform.position.clone());
                let forward = Tools.getTransformForward(this.model.transform);
                let angle = Tools.getAngle(dir, forward);
                if (angle < 90) {
                    let axis = Tools.randomNumber(0.8, 1.2)
                    let attack = Math.floor(this.attack * axis);
                    machine.goHurt(attack);
                }
            }
        }
    }
    //#region 剑刃风暴配置
    skill1Range = 8;
    skill1Time = 2500;
    skill1CD = 6000;
    skill1Effect
    isCanSkill1 = true;
    skill1On() {
        if (!this.isGotWeapon)
            return;
        if (NimoForFrame.isEnd)
            return;
        if (this.isEnd)
            return;
        if (!this.isMove)
            return;
        if (this.isAttack)
            return;
        if (this.isDowning)
            return;
        this.isAttack = true;
        this.isInvincible = true;
        // this.stopMove();
        let url = "swordSkill";
        this.anim.play(url, 0, 0);
        Laya.timer.frameLoop(1, this, this.skill1Update);
        Laya.timer.once(this.skill1Time, this, this.skill1Off);
        //关闭攻击入口
        // AttackButton2.instance.unActive(this.skill1CD);
        this.isCanSkill1 = false;
        setTimeout(() => {
            //结束技能CD
            // AttackButton2.instance.goActive();
            this.isCanSkill1 = true;
        }, this.skill1CD);
        let hitParent = this.effect.getChildByName("SwordSpinEffect")
        this.skill1Effect = FxController.playEffectReturn("SwordSpinEffect", hitParent);
        this.anim.getControllerLayer(1).defaultWeight = 0;
        if (!NimoForFrame.isPlayerCar)
            Data.sound.play(SoundType.SwordSkill);
    }
    skill1Update() {
        this.MoveControl();
        this.skill1Do();
    }
    skill1Do() {
        let collider: MachineHitCheck[] = [];
        for (let i = 0; i < MachineHitCheck_AI.objs.length; i++) {
            if (i != this.colliderComp.aiId) {
                collider.push(MachineHitCheck_AI.objs[i]);
            }
        }
        if (MachineHitCheck_Player.player)
            collider.push(MachineHitCheck_Player.player);
        let range = 6;
        for (let i = 0; i < collider.length; i++) {
            let machine = collider[i].machine;
            let hitEvent = Tools.CircleCheck_2D(this.colliderComp.axis, this.colliderComp.horizontal, range, collider[i].axis, collider[i].horizontal, collider[i].radius)
            if (hitEvent.isHit) {
                //伤害浮动
                let axis = Tools.randomNumber(0.8, 1.2)
                let attack = Math.floor(this.attack * axis);
                machine.goHurt(attack);
            }
        }
    }
    skill1Off() {
        if (this.isEnd)
            return;
        this.anim.play("idle", 0, 0);
        if (this.skill1Effect)
            this.skill1Effect.destroy();
        Laya.timer.clear(this, this.skill1Off);
        Laya.timer.clear(this, this.skill1Update);
        this.isAttack = false;
        this.isInvincible = false;
        GameMgr.ConsoleLog("技能1关闭")
    }
    //#endregion

    //#region 咆哮配置
    skill2Range = 8;
    skill2DoTime = 200;
    skill2Time = 600;
    skill2Effect: Laya.Sprite3D;
    skill2CD = 6000;
    isCanSkill2 = true;
    skill2On() {
        if (NimoForFrame.isEnd)
            return;
        if (this.isEnd)
            return;
        if (!this.isMove)
            return;
        if (this.isAttack)
            return;
        if (this.isDowning)
            return;
        this.isAttack = true;
        this.isInvincible = true;
        this.stopMove();
        let url = "shout";
        this.anim.play(url, 0, 0);
        Laya.timer.once(this.skill2DoTime, this, this.skill2Do);
        //设定不能攻击
        // AttackButton3.instance.unActive(this.skill2CD);
        this.isCanSkill2 = false;
        Laya.timer.once(this.skill2Time, this, this.skill2Off);
        setTimeout(() => {
            //可以攻击了
            this.isCanSkill2 = true;
            // AttackButton3.instance.goActive();
        }, this.skill2CD);
        if (!NimoForFrame.isPlayerCar)
            Data.sound.play(SoundType.Shout);
    }
    skill2Do() {
        let collider: MachineHitCheck[] = [];
        for (let i = 0; i < MachineHitCheck_AI.objs.length; i++) {
            if (i != this.colliderComp.aiId) {
                collider.push(MachineHitCheck_AI.objs[i]);
            }
        }
        if (MachineHitCheck_Player.player)
            collider.push(MachineHitCheck_Player.player);
        let range = 10;
        for (let i = 0; i < collider.length; i++) {
            let machine = collider[i].machine;
            let hitEvent = Tools.CircleCheck_2D(this.colliderComp.axis, this.colliderComp.horizontal, range, collider[i].axis, collider[i].horizontal, collider[i].radius)
            if (hitEvent.isHit) {
                let axis = Tools.randomNumber(0.8, 1.2)
                let attack = Math.floor(this.attack * axis);
                machine.goHurt(attack, true);
                machine.goDown();
                machine.HitBack(this.colliderComp.axis, this.colliderComp.horizontal, 50);
            }
        }
        let parent = this.skill2Effect;
        FxController.playEffectDestroy("BoomEffect", parent);
    }
    skill2Off() {
        if (this.isEnd)
            return;
        Laya.timer.clear(this, this.skill1Update);
        this.isAttack = false;
        this.isInvincible = false;
        if (!this.isDowning)
            this.goMove();
    }
    //#endregion
}