import FxController from "../../extensionTools/mods_Nimo/FxController";
import Tools from "../../extensionTools/mods_Nimo/Tools";
import CarComponent from "./CarComponent";
import CarHitCheck_Player from "./HitCheck/CarHitCheck_Player";

export default class Car_NPC extends Laya.Script3D {

    carComp: CarComponent;


    limitSpeed = 0.5;
    onEnable(): void {
        this.carComp = this.owner.getComponent(CarComponent);
        this.axis = 1
        this.carComp.tracker.speedFactor = this.limitSpeed;
    }

    onDisable(): void {
        Laya.timer.clearAll(this);
    }
    axis = 0;
    horizontal = 0;

    onUpdate() {
        if (this.carComp.tracker.speedFactor > this.limitSpeed) {
            this.axis = 0;
        } else {
            this.axis = 1;
        }

    }
}