import { EventType, UiSceneType } from "../configs/EnumType";
import ExtensionTool from "../extensionTools/ExtensionTool";
import GameMgr from "../managers/GameMgr";
import FdMgr from "../platforms/cpLogicalMgr/FanDong/fdMgr";
import UiScene from "./UiScene";

/**结束页 */
export default class Over extends UiScene {
    private imgStatus: Laya.Image;
    private btnContinue: Laya.Button;
    private videoBtn: Laya.Button;

    private coinValue: Laya.FontClip;

    isWin: boolean;

    onOpened(data) {
        if (data) {
            this.isWin = data.isWin;
            this.imgStatus.skin = data.isWin ? "ui/game/js_sl_01.png" : "ui/game/js_sl_02.png";
            if (data.isWin) {
                GameMgr.Storage.addCoin(1000);
            } else {
                GameMgr.Storage.addCoin(500);
            }
        }
    }

    onClosed() {
        super.onClosed();
        GameMgr.GlData.playCount++;
    }

    initEvent() {

        GameMgr.Event.on(EventType.Update_Coin_Event, this, this.coinChange);
        ExtensionTool.d2_AddClickEvent(this.btnContinue, this, this.onContinueClick);
        ExtensionTool.d2_AddClickEvent(this.videoBtn, this, this.doubleVideoClick);
    }
    doubleVideoClick() {
        // GameMgr.Platform.showVideoAd(() => {
        this.videoBtn.set_visible(false)
        if (this.isWin) {
            GameMgr.Storage.addCoin(1000);
        } else {
            GameMgr.Storage.addCoin(500);
        }
        // })
    }
    init() {
        this.coinChange();
    }

    onContinueClick() {
        // FdMgr.loadGame(() => {
        GameMgr.UI.closeScene(UiSceneType.Over, { isWin: this.isWin });
        GameMgr.Event.event(EventType.Init_Game_Event);
        // })
    }
    coinChange() {
        this.coinValue.value = GameMgr.Storage.storageData.coin.toString();
    }
}