import D2Tween from "../../D2Tween";

export default class ScaleUITween extends Laya.Script {



    onEnable(): void {
        let target = this.owner as Laya.Sprite;
        D2Tween.toScale(target, 0.9, 1.1, 500, null, () => { }, false, true)
    }

    onDisable(): void {
        let target = this.owner as Laya.Sprite;
        D2Tween.clearTween(target);
        target.scale(1, 1);
    }
}