import FdMgr from "./fdMgr";

export default class FdAd {
    static initAd() {
        this.createMoreBanner();
        this.createVideoAd();
    }

    static sysInfo: any;
    static getSystemInfoSync() {
        if (!this.sysInfo) {
            this.sysInfo = wx.getSystemInfoSync();
        }
        return this.sysInfo;
    }

    //#region Banner广告
    static bannerAds: any[] = [];
    static bannerIndex: number = 0;
    static createMoreBanner() {
        if (this.bannerAds.length > 0) {
            for (let i = 0; i < this.bannerAds.length; i++) {
                this.bannerAds[i].destroy();
            }
            this.bannerAds = [];
        }

        for (let i = 0; i < 3; i++) {
            let bannerAd = this.createBannerAd();
            this.bannerAds.push(bannerAd);
        }
    }

    static showNextBanner(index?) {
        if (index) {
            this.bannerIndex = index;
        }

        let bannerAd = this.bannerAds[this.bannerIndex];
        if (bannerAd) {
            bannerAd.show();
        }
    }

    static hideNextBanner() {
        let bannerAd = this.bannerAds[this.bannerIndex];
        if (bannerAd) {
            bannerAd.hide();
            this.bannerIndex++;
            if (this.bannerIndex >= this.bannerAds.length) { this.bannerIndex = 0; }
        }
    }

    static bannerAd: any
    static createBannerAd(isShow: boolean = false) {
        if (!Laya.Browser.onWeiXin) return;

        // if (this.bannerAd) {
        //     this.destroyBannerAd();
        // }

        let sysInfo = this.getSystemInfoSync();
        let bannerAd = Laya.Browser.window.wx.createBannerAd({
            adUnitId: FdMgr.bannerId,
            style: {
                top: sysInfo.screenHeight * 0.8,
                width: 300,
                left: sysInfo.screenWidth / 2 - 150
            },
            adIntervals: 30
        });

        bannerAd.onLoad(() => {
            if (isShow) {
                FdAd.showBannerAd();
            }
            console.log("Banner广告加载成功");
        });

        bannerAd.onError(err => {
            this.bannerAd = null;
            console.error("Banner广告加载失败", JSON.stringify(err));
        });

        bannerAd.onResize(res => {
            let realHeight = bannerAd.style.realHeight + 0.1;
            // if (sysInfo.model.startsWith("iPhone X")) realHeight += (40 / Laya.Browser.height) * GameConfig.height;
            bannerAd.style.top = sysInfo.screenHeight - realHeight;
        });

        this.bannerAd = bannerAd;
        return bannerAd;
    }

    static showBannerAd() {
        if (!this.bannerAd) {
            this.createBannerAd(true);
            return;
        }
        this.bannerAd.show().then(() => {
        }).catch((err) => {
            this.createBannerAd(true);
        });
    }

    static hideBannerAd() {
        if (!this.bannerAd) return;
        this.bannerAd.hide();
    }

    static destroyBannerAd() {
        if (!this.bannerAd) return;
        this.bannerAd.destroy();
        this.bannerAd = null;
    }
    //#endregion

    //#region 激励视频广告
    static videoAd: any;
    static videoFinishCallback: Function;
    static videoCancelCallback: Function;
    static isCreatingVideoAd: boolean = false;
    static isCreateAndShow: boolean = false;
    static canShowVideo: boolean = false;

    static createVideoAd() {
        if (!Laya.Browser.onWeiXin) return;
        if (this.isCreatingVideoAd) return;
        this.isCreatingVideoAd = true;
        var videoAd = Laya.Browser.window.wx.createRewardedVideoAd({
            adUnitId: FdMgr.videoId
        });

        videoAd.onLoad(res => {
            this.isCreatingVideoAd = false;
            this.videoAd = videoAd;
            console.log("激励视频广告加载成功");
            if (this.isCreateAndShow) {
                videoAd.show();
                this.isCreateAndShow = false;
            }
            this.canShowVideo = true;
        });

        videoAd.onError(err => {
            videoAd.destroy();
            this.isCreatingVideoAd = false;
            this.videoAd = null;
            console.error("激励视频广告加载失败", JSON.stringify(err));
            console.log(this.videoCancelCallback);

            if (this.videoCancelCallback) {
                this.videoCancelCallback();
                this.videoCancelCallback = null;
                this.videoFinishCallback = null;
            }
        });

        videoAd.onClose((res) => {
            if (res) {
                if (res.isEnded) {
                    this.videoFinishCallback && this.videoFinishCallback();
                }
                else {
                    this.videoCancelCallback && this.videoCancelCallback();
                }
                this.videoFinishCallback = null;
                this.videoCancelCallback = null;
            }
        })
    }

    static showVideoAd(finishCB?: Function, cancelCB?: Function) {
        if (!Laya.Browser.onWeiXin) {
            finishCB && finishCB();
            return;
        }
        this.videoFinishCallback = finishCB;
        this.videoCancelCallback = cancelCB;
        if (!this.videoAd) {
            this.isCreateAndShow = true;
            this.createVideoAd();
            return;
        }
        if (this.canShowVideo) {
            this.videoAd.show();
            this.canShowVideo = false;
        }
        else {
            cancelCB && cancelCB();
            this.videoFinishCallback = null;
            this.videoCancelCallback = null;
        }
    }

    static destroyVideoAd() {
        if (!this.videoAd) return;
        this.videoAd.destroy();
        this.videoAd = null;
    }
    //#endregion



    //#region 格子广告
    static gridAd;
    static initGridCB: Function;

    static initGridAD(cb?: Function, left = 0, top = 0) {
        if (!Laya.Browser.onWeiXin) {
            cb && cb(false);
            return;
        }

        this.CreateGridAD(FdMgr.gridId, left, top);
        if (cb) { this.initGridCB = cb; }
    }

    /**创建并展示格子 */
    private static CreateGridAD(id, posx, posy) {
        try {
            if (Laya.Browser.window.wx.createCustomAd) {
                var grid = Laya.Browser.window.wx.createCustomAd({
                    adUnitId: id,
                    adIntervals: 31,
                    style: {
                        left: posx,
                        top: posy,
                        width: Laya.Browser.clientWidth / 2.2
                    }
                });

                grid.onLoad(load => {
                    FdAd.gridAd = grid;
                    grid.show();

                    if (FdAd.initGridCB) {
                        FdAd.initGridCB(true);
                        FdAd.initGridCB = null;
                    }
                });

                grid.onError(err => {
                    console.error(err);

                    if (FdAd.initGridCB) {
                        FdAd.initGridCB(false);
                        FdAd.initGridCB = null;
                    }
                })
            }
        }
        catch (err) {
            console.log("原生格子报错" + err);

            if (FdAd.initGridCB) {
                FdAd.initGridCB(false);
                FdAd.initGridCB = null;
            }
        }
    }

    static showGridAD() {
        if (this.gridAd) {
            this.gridAd.show();
            return true;
        }
        return false;
    }

    static hideGridAD() {
        if (this.gridAd) {
            this.gridAd.hide();
        }

        //防止没关掉
        Laya.timer.once(500, this, () => {
            if (this.gridAd) {
                this.gridAd.hide();
            }
        });
    }
    //#endregion
}