/**
 * 缓存数据类型
 * 根据游戏去做配置
 */
export class StorageData {
    //常用缓存数据
    /**分数 */
    score = 0;
    /**关卡 */
    level = 1;
    /**金币 */
    coin = 3000;

    /**游戏刷新时间 */
    freshTime = new Date().getTime() - 86400000;
    /**游戏次数 */
    playCount = 0;

    /**签到天数 */
    signDay = 1;
    /**签到周数 */
    signWeek = 1;
    /**当日是否已签到 */
    isSign = false;

    /**皮肤列表：角色/武器(可扩展) */
    skinList = [[1]];
    /**当前皮肤：角色/武器(可扩展) */
    skinId = [1];
}