import { EventType } from "../configs/EnumType";
import GameMgr from "./GameMgr";

export default class ResMgr {
    resList: string[];
    currentLoadType: string;

    /**加载的数量 */
    resCount: number;

    constructor() {
        this.resList = [];
        this.resCount = 0;
    }

    addLoadRes(type) {
        if (this.resList.indexOf(type) == -1) {
            this.resCount++;
            this.resList.push(type);
            if (!this.currentLoadType) {
                this.currentLoadType = type;
                GameMgr.Event.event(EventType.Load_Res_Msg_Event, { type: type, progress: 0, isComplete: false });
            }
        }
    }

    removeLoadRes(type) {
        let index = this.resList.indexOf(type);
        if (index != -1) {
            this.resList.splice(index, 1);
            this.resCount--;
            GameMgr.Event.event(EventType.Load_Res_Msg_Event, { type: type, progress: 1, isComplete: true });

            // Laya.timer.once(100, this, () => {
            if (this.resList.length > 0) {
                this.currentLoadType = this.resList[0];
                GameMgr.Event.event(EventType.Load_Res_Msg_Event, { type: this.resList[0], progress: 0, isComplete: false });
            }
            else if (this.resCount == 0) {
                GameMgr.ConsoleLog("全部资源加载完成");
                GameMgr.Event.event(EventType.Load_Res_Msg_Event, { allComplete: true });
            }
            else {
                this.currentLoadType = null;
            }
            // })
        }
    }

    updateLoadRes(type, progress, isComplete) {
        GameMgr.Event.event(EventType.Load_Res_Msg_Event, { type: type, progress: progress, isComplete: isComplete });
    }
}