import Data from "../../extensionTools/mods_Nimo/Data";
import NimoForFrame from "../../extensionTools/mods_Nimo/NimoForFrame";
import Tools from "../../extensionTools/mods_Nimo/Tools";
import GameMgr from "../../managers/GameMgr";
import Game from "../../uiScenes/Game";
import MachineHitCheck from "./HitCheck/MachineHitCheck";
import MachineHitCheck_AI from "./HitCheck/MachineHitCheck_AI";
import MachineHitCheck_Player from "./HitCheck/MachineHitCheck_Player";
import MachineComponent from "./MachineComponent";
import MachineComponent_AI from "./MachineComponent_AI";


export default class Machine_AI extends Laya.Script3D {

    hitComp: MachineHitCheck_AI;
    machineComp: MachineComponent_AI

    onEnable(): void {
        this.machineComp = this.owner.getComponent(MachineComponent_AI);
        this.hitComp = this.owner.getComponent(MachineHitCheck);
        this.attackCD = Data.AIAttackInterval[(GameMgr.Storage.storageData.level - 1) % Data.AIAttackInterval.length];
    }

    onDisable(): void {
        Laya.timer.clearAll(this);
    }

    target;
    attackCD = 100;
    isAttack = true;
    onUpdate() {
        if (this.machineComp.isEnd)
            return;
        let hitEvent = this.search();
        if (!hitEvent)
            this.target = null;
        let target = this.target;
        //移动到目标处
        if (target) {
            let way = new Laya.Vector3(target.horizontal - this.hitComp.horizontal, 0, target.axis - this.hitComp.axis)
            let scale = 0;
            if (hitEvent.isHit) {
                scale = 0
                if (this.isAttack) {
                    let self = this;
                    let attackCb = []
                    let attackCount = Data.AIAttackWeight;
                    for (let i = 0; i < attackCount; i++) {
                        attackCb.push("goAttack")
                    }
                    if (this.machineComp.isCanSkill1) {
                        attackCb.push("skill1On");
                    }
                    if (this.machineComp.isCanSkill2) {
                        attackCb.push("skill2On");
                    }
                    let id = Tools.randomInt(0, attackCb.length - 1)
                    let cb = attackCb[id];
                    this.machineComp[cb]();
                    this.isAttack = false;
                    Laya.timer.once(this.attackCD, this, () => {
                        this.isAttack = true;
                    })
                }
            } else {

                scale = hitEvent.distance - hitEvent.range;
                // scale = scale * 0.1;
            }
            let outWay = new Laya.Vector3();
            Laya.Vector3.normalize(way, outWay);

            this.machineComp.way = Tools.Vector3Axis(outWay, scale);
        }
    }
    search() {
        let allTarget = [];
        let goHealHpPrecent = Data.AIgoHealHpPrecent;
        let attackOn = false;
        let healOn = false;
        let hitEventFinal;
        if (NimoForFrame.pickItems[0].active) {
            attackOn = true;
        }
        if (NimoForFrame.hpValues[this.machineComp.playerId] / NimoForFrame.maxHpValues[this.machineComp.playerId] < goHealHpPrecent) {
            if (NimoForFrame.pickItems[1].active) {
                healOn = true;
            }
        }
        if (attackOn && this.machineComp.isGotWeapon) {
            let axis = NimoForFrame.pickItems[0].transform.position.z;
            let horizontal = NimoForFrame.pickItems[0].transform.position.x;

            let data = {
                axis: axis,
                horizontal: horizontal,
                radius: 2
            }
            allTarget.push(data);
        }
        if (!this.machineComp.isGotWeapon) {
            for (let i = 0; i < NimoForFrame.pickWeapons.length; i++) {
                let obj = NimoForFrame.pickWeapons[i];
                if (!obj.active)
                    continue;
                let axis = obj.transform.position.z;
                let horizontal = obj.transform.position.x;
                let raidus = 2;
                let data = {
                    axis: axis,
                    horizontal: horizontal,
                    radius: raidus
                }
                allTarget.push(data);
            }
        } else if (healOn) {
            let axis = NimoForFrame.pickItems[1].transform.position.z;
            let horizontal = NimoForFrame.pickItems[1].transform.position.x;

            let data = {
                axis: axis,
                horizontal: horizontal,
                radius: 2
            }
            allTarget.push(data);
        } else {
            for (let i = 0; i < MachineHitCheck_AI.objs.length; i++) {
                if (i != this.hitComp.aiId) {
                    if (!MachineHitCheck_AI.objs[i].machine.isDowning && !MachineHitCheck_AI.objs[i].machine.isEnd)
                        allTarget.push(MachineHitCheck_AI.objs[i]);
                }
            }
            if (MachineHitCheck_Player.player) {
                if (!MachineHitCheck_Player.player.machine.isDowning && !MachineHitCheck_Player.player.machine.isEnd)
                    allTarget.push(MachineHitCheck_Player.player)
            }
            // GameMgr.ConsoleLog("打架")
        }
        let shortDistance = 10000;
        for (let i = 0; i < allTarget.length; i++) {
            GameMgr.ConsoleLog(allTarget[i].radius, this.hitComp.radius)
            let hitEvent = Tools.CircleCheck_2D(allTarget[i].axis, allTarget[i].horizontal, allTarget[i].radius, this.hitComp.axis, this.hitComp.horizontal, this.hitComp.radius);
            if (shortDistance > hitEvent.distance) {
                shortDistance = hitEvent.distance;
                hitEventFinal = hitEvent;
                this.target = allTarget[i];
            }
        }

        return hitEventFinal;
    }
}