import { EventType, PrefabType, UiSceneType } from "../../../configs/EnumType";
import ExtensionTool from "../../../extensionTools/ExtensionTool";
import SHA256 from "../../../extensionTools/SHA256";
import GameMgr from "../../../managers/GameMgr";
import { NativeAd } from "../../Platform";
// import Md5 from "../../../extensionTools/MD5";
import { KolaSDK, PlatformType } from "./IKolaSDK";
import { DataConfig } from "./qyMgr";

/**合作cp运营策略逻辑 */
export default class qy_vivo {
    static code = "";

    static pkgName = "com.gzqy.bxjjcr.vivominigame";
    static appid = "105513994";
    static appKey = "bfb159bf29ddea11b8972aa17c81b8a0";
    static appSecret = "5e9e5a8ce2b4a2cd62ee987f58dd5fe8";
    static gameId = "bxjjcr";
    static token = "";
    static platform = PlatformType.Vivo;

    /**获取userId */
    static getUserId(completeCB?: Function) {
        let timeStamp = ExtensionTool.getCurrentTime;
        let nonce = this.getNonce(16);
        let str = this.abstractPar("appKey", this.appKey) + this.abstractPar("appSecret", this.appSecret) + this.abstractPar("nonce", nonce) +
            this.abstractPar("pkgName", this.pkgName) + this.abstractPar("timestamp", timeStamp) + this.abstractPar("token", this.token, true);

        // let md5 = new Md5;
        // let signStr = md5.hexMD5(str).toUpperCase();
        let signStr = SHA256.SHA256(str);
        console.log("签名：", signStr, str);

        let addStr = this.abstractPar("pkgName", this.pkgName) + this.abstractPar("token", this.token) + this.abstractPar("timestamp", timeStamp) +
            this.abstractPar("nonce", nonce) + this.abstractPar("signature", signStr, true);
        // + this.abstractPar("version", GameMgr.GlData.gameVersion, true)

        let url = "https://quickgame.vivo.com.cn/api/quickgame/cp/account/userInfo?" + addStr;

        console.log("请求地址：", url, addStr);

        this.httpRequest(url, "", 'get', (res) => {
            console.log("正式请求")
            res = JSON.parse(res);
            completeCB && completeCB();
            console.log("userData", JSON.stringify(res));

            if (res.data && res.data.openId) {
                console.log("用户id:", res.data.openId);
                KolaSDK.userId = res.data.openId;
                KolaSDK.OnShow(null);
            }
        });
    }

    //网络请求
    static httpRequest(url: string, params: any, type: string = 'get', completeHandler?: Function) {
        var xhr: Laya.HttpRequest = new Laya.HttpRequest();
        xhr.http.timeout = 5000;//设置超时时间；
        xhr.once(Laya.Event.COMPLETE, this, completeHandler);
        xhr.once(Laya.Event.ERROR, this, this.httpRequest, [url, params, type, completeHandler]);
        if (type == "get") {
            xhr.send(url, "", type, "text");
        } else if (type == "post") {
            xhr.send(url, JSON.stringify(params), type, "text");
        }
    }

    static init() {
        KolaSDK.LogEvent("EnterGame");
        console.log("轻游SDK=====================")
        KolaSDK.Initialize(this.platform, "android", this.gameId, "", "gamelog", "https://cy-game.cn-shenzhen.log.aliyuncs.com/logstores/", "https://cy-game.oss-cn-shenzhen.aliyuncs.com/gameJSConfig/h5games/", null);

        this.dataConfig = new DataConfig;
        KolaSDK.LoadRemoteSetting((args) => {
            console.log("加载配置文件成功", JSON.stringify(args));
            if (args) {
                let version = GameMgr.GlData.gameVersion;
                let str = version.split('.');
                let versionStr = 'v' + str[0] + str[1] + str[2];
                if (args[versionStr] != null) {
                    for (let key in args) {
                        this.dataConfig[key] = args[versionStr][key] != null ? args[versionStr][key] : args[key];
                    }
                }
                else {
                    for (let key in args) {
                        this.dataConfig[key] = args[key];
                    }
                }
            }
            Laya.timer.once(this.dataConfig.showNativeDelayTime * 1000, this, () => {
                this.canShowNativeAd = true;
                this.refreshNativeAd(0);
            });

        }, (err) => {
            console.error("加载配置文件失败", JSON.stringify(err));
            Laya.timer.once(this.dataConfig.showNativeDelayTime * 1000, this, () => {
                this.canShowNativeAd = true;
                this.refreshNativeAd(0);
            });
        });
        this.login();

        GameMgr.Event.on(EventType.Platform_Wake_Event, this, (e) => {
            KolaSDK.OnShow(e.param);
        });
        GameMgr.Event.on(EventType.Platform_Sleep_Event, this, () => {
            KolaSDK.OnHide();
        });

        GameMgr.Event.on(EventType.Open_Ui_Event, this, this.onOpenUiEvent);
        GameMgr.Event.on(EventType.Close_Ui_Event, this, this.onCloseUiEvent);
        GameMgr.Event.on(EventType.Show_Video_Event, this, () => {
            KolaSDK.LogEvent("ADReward");
        });

        GameMgr.Event.on(EventType.Load_Res_Msg_Event, this, (e) => {
            if (e.progress == 1) {
                KolaSDK.LogEvent("LoadFinish" + e.type);
            }
            else if (e.progress == 0) {
                KolaSDK.LogEvent("LoadStart" + e.type);
            }
        });
    }

    static onOpenUiEvent(url) {
        GameMgr.ConsoleLog("开启页面", url);

        switch (url) {
            case UiSceneType.Load:
                // Laya.timer.once(60000, this, () => {
                //     this.canShowNativeAd = true;
                // });
                //创建原生广告
                // this.loopCreateNativeAd();
                break;
            case UiSceneType.Home:
                // GameMgr.Platform.showBannerAd();
                this.createShortcut(300, 150);
                // this.createMoreGame(260, Laya.stage.displayHeight - 20);


                break;
            case UiSceneType.Game:
                var level = GameMgr.Storage.storageData.level;
                // KolaSDK.LogEvent("StartLevel " + level);
                if (level == 1) {
                    this.playCount = 0;
                    Laya.timer.loop(5000, this, this.countPlayTime);
                }
                break;
            case UiSceneType.Over:
                // GameMgr.Platform.showBannerAd();
                var level2 = GameMgr.Storage.storageData.level;
                if (level2 == 1 || level2 == 2) {
                    Laya.timer.clear(this, this.countPlayTime);
                }

                // if (this.dataConfig.isShowInterNativeAd) {
                //     Laya.timer.once(1000, this, () => {
                //         let ad = GameMgr.Prefab.getItem(PrefabType.interNativeAd);
                //         let scene = GameMgr.UI.TopScene;
                //         scene.addChild(ad);
                //     });
                // }
                break;
        }
    }

    static playCount = 0
    static countPlayTime() {
        this.playCount++;
        KolaSDK.LogEvent("LevelOnePlayTime：" + (this.playCount * 5) + "second");
    }

    static curLevel: number;
    static onCloseUiEvent(param) {
        GameMgr.ConsoleLog("关闭页面", param.url);

        switch (param.url) {
            case UiSceneType.Load:
                KolaSDK.LogEvent("OnMainScene");
                break;
            case UiSceneType.Home:
                // GameMgr.Platform.hideBannerAd();
                this.curLevel = 10000 + GameMgr.Storage.storageData.level;
                KolaSDK.LogStartLevel(this.curLevel);
                break;
            case UiSceneType.Game: break;
            case UiSceneType.Over:
                // GameMgr.Platform.hideBannerAd();
                if (param.param) {
                    KolaSDK.LogFinishLevel(this.curLevel, param.param.isWin);
                    // if (param.param.isWin) {
                    //     KolaSDK.LogEvent("FinishLevel " + GameMgr.Storage.storageData.level);
                    // }
                    // else {
                    //     KolaSDK.LogEvent("FailLevel " + GameMgr.Storage.storageData.level);
                    // }
                }
                if (this.dataConfig.isShowInterNativeAd) {
                    Laya.timer.once(1000, this, () => {
                        GameMgr.Event.event("Update_NativeAd_Event", { isClose: true })
                        let ad = GameMgr.Prefab.getItem(PrefabType.interNativeAd);
                        let scene = GameMgr.UI.TopScene;
                        scene.addChild(ad);
                    });
                }
                break;
        }
    }

    static login() {
        if (Laya.Browser.window.qg) {
            Laya.Browser.window.qg.login({
                success(res) {
                    console.log("登录成功", JSON.stringify(res));
                    if (res.token) {
                        qy_vivo.token = res.token;
                        qy_vivo.getUserId();
                    } else {
                        console.log('登录失败！' + res.errMsg);
                    }
                }
            });
        }
    }

    static abstractPar(par, va, islast = false) {
        var str = par + "=" + va;
        if (!islast)
            str += "&";
        return str;
    }

    static createShortcut(left = 0, top = 0) {
        GameMgr.Platform.hasShortcut((has) => {
            if (has) return;

            let prefab = GameMgr.Prefab.getItem(PrefabType.shortcut);
            let scene = GameMgr.UI.TopScene;
            scene.addChild(prefab);
            prefab.visible = true;

            prefab.left = left;
            prefab.top = top - prefab.height;

            ExtensionTool.d2_AddClickEvent(prefab, scene, () => {
                GameMgr.Platform.createShortcut(() => {
                    GameMgr.Prefab.recoverItem(PrefabType.shortcut, prefab);
                    prefab.visible = false;
                });
            });
        });
    }

    static createMoreGame(left = 0, top = 0) {
        let prefab = GameMgr.Prefab.getItem(PrefabType.moreGame);
        let scene = GameMgr.UI.TopScene;
        scene.addChild(prefab);

        prefab.left = left;
        prefab.top = top - prefab.height;

        ExtensionTool.d2_AddClickEvent(prefab, scene, () => {
            GameMgr.Platform.showBoxProtalAd();
        });
    }

    static canShowNativeAd = true; //false
    static dataConfig: DataConfig;
    static refreshNativeAd(time?) {
        time = time != null ? time * 1000 : this.dataConfig.nativeAdRefreshTime * 1000;
        Laya.timer.clear(this, this.loopCreateNativeAd);
        Laya.timer.loop(time, this, this.loopCreateNativeAd);
    }
    static loopCreateNativeAd(isClick = false) {
        if (isClick) {
            if (this.nativeAd) {
                GameMgr.Platform.destroyNativeAd(this.nativeAd);
            }
            this.nativeAd = null;
        }

        GameMgr.Platform.loadNativeAd(this.nativeAd, (ad: NativeAd) => {
            if (ad && ad.nativeAd) {
                console.log("原生广告创建成功=======", JSON.stringify(ad));
                this.nativeAd = ad;
            }
            else {
                this.refreshNativeAd(5);
            }
            GameMgr.Event.event("Update_NativeAd_Event");
        });
        this.refreshNativeAd();
    }

    static nativeAd: NativeAd;

    static getNonce(amount: number) {
        let nonce = "";
        for (let i = 0; i < amount; i++) {
            let random = Math.floor(Math.random() * 10);
            nonce += random;
        }
        console.log("nonce:", nonce);
        return nonce;
    }
}


/**
 * 埋点信息
 * EnterGame    到达游戏第一帧
 * LoadStart    开始加载
 * LoadFinish   加载完成
 * OnMainScene  到达主场景
 * StartLevel   开始游戏
 * FinishLevel  通关
 * FailLevel    失败
 * LevelOnePlayTime：   第一关玩家时长
 */