
import ImageButton from "../../extensionTools/mods_Nimo/ctrl/ImageButton";
import { Nimo_EventType } from "../../extensionTools/mods_Nimo/Nimo_EventType";
import GameMgr from "../../managers/GameMgr";


export default class SpeedButton extends ImageButton {
    static instance: SpeedButton

    colorMask;
    init() {
        SpeedButton.instance = this;
        this.colorMask = this.color.mask;
        this.color.set_visible(true);
        this.black.set_visible(true);
    }
    pressReal() {
        if (!this.isActive)
            return;
        this.press();
        //表现
        this.isPress = true;
    }
    releaseReal() {
        if (!this.isActive)
            return;
        this.release()
        //表现
        this.isPress = false;
    }
    press() {
        GameMgr.Event.event(Nimo_EventType.GameSpeed1On);
    }
    release() {
        GameMgr.Event.event(Nimo_EventType.GameSpeed1Off);
    }
    tween: Laya.Tween;
    cdTime: number = 8;
    unActive(time) {
        this.isActive = false;
        this.isPress = false;
        this.colorMask.x = -150;
        this.cdTime = time;
        this.tween = Laya.Tween.to(this.colorMask, { x: 0 }, time, null, Laya.Handler.create(this, this.goActive));
    }
    updateCD(value) {

        let nowProgress = 1 - (-this.colorMask.x / 150);
        let addProgress = value / this.cdTime;
        let nextProgress = nowProgress + addProgress;
        if (nextProgress < 1) {
            if (this.tween)
                this.tween.clear();
            this.colorMask.x = -150 * (1 - nextProgress);
            GameMgr.ConsoleLog(value, this.cdTime)
            this.tween = Laya.Tween.to(this.colorMask, { x: 0 }, (1 - nextProgress) * this.cdTime, null, Laya.Handler.create(this, this.goActive));
        } else {
            if (this.tween)
                this.tween.complete();
        }
    }
    goActive() {
        this.isActive = true;
    }
}