import ExtensionTool from "../../ExtensionTool";
import Tools from "../Tools";

export default class ImageButton extends Laya.Script {

    user: Laya.Image

    color: Laya.Image
    black: Laya.Image

    isPress: boolean

    onEnable() {
        this.user = this.owner as Laya.Image;
        this.color = this.user.getChildByName("color") as Laya.Image;
        this.black = this.user.getChildByName("black") as Laya.Image;
        this.isPress = false;
        this.color.set_visible(!this.isPress);
        this.black.set_visible(this.isPress);
        Tools.gameButtonOn(this.user, this, this.pressReal, this.releaseReal);
        this.init();
    }

    //#region 接口
    init() {

    }
    press() {

    }
    release() {

    }
    //#endregion

    pressReal() {
        if (!this.isActive)
            return;
        this.press();
        //表现
        this.isPress = true;
        this.color.set_visible(!this.isPress);
        this.black.set_visible(this.isPress);
    }
    releaseReal() {
        if (!this.isActive)
            return;
        this.release()
        //表现
        this.isPress = false;
        this.color.set_visible(!this.isPress);
        this.black.set_visible(this.isPress);
    }
    isActive = true;
    unActive(time?) {
        this.isActive = false;
        this.isPress = false;
        this.color.set_visible(false);
        this.black.set_visible(true);
    }
    goActive() {
        this.isActive = true;
        this.color.set_visible(true);
        this.black.set_visible(false);
    }

}