import { EventType, UiSceneType } from "../configs/EnumType";
import ExtensionTool from "../extensionTools/ExtensionTool";
import Data from "../extensionTools/mods_Nimo/Data";
import NimoForFrame from "../extensionTools/mods_Nimo/NimoForFrame";
import GameMgr from "./GameMgr";

/**关卡管理器 */
export default class LevelMgr {
    mainScene: Laya.Scene3D;
    mainCamera: Laya.Camera;

    constructor() {
        this.init();
    }

    init() {
        if (GameMgr.GlData.isSubPackage && GameMgr.Platform.canSubPackage) {
            let loadNames = ["scene"];
            GameMgr.Res.addLoadRes("分包");
            GameMgr.Platform.loadSubpackagesSync(loadNames, this, (caller, isSuccess) => {
                if (isSuccess) {
                    caller.load3DRes();
                    GameMgr.Res.removeLoadRes("分包");
                }
            });
        }
        else { this.load3DRes(); }
        this.initEvent();
        this.readJson();
    }

    /**3D游戏加载 */
    load3DRes() {
        GameMgr.Res.addLoadRes("场景");
        var resource = [
            { url: GameMgr.GlData.sceneResUrl, clas: Laya.Scene3D, priority: 1 },   //场景
            // { url: GameMgr.GlData.modelResUrl, clas: Laya.Sprite3D, priority: 2 },  //预设
        ];

        Laya.loader.create(resource,
            Laya.Handler.create(this, this.onLoadFinished, [{ target: "3dRes" }], false),
            Laya.Handler.create(this, this.onLoading, null, false));
        Laya.loader.on(Laya.Event.ERROR, this, err => {
            console.error("资源加载失败", err);
        });
    }
    onLoadFinished(evt) {
        GameMgr.ConsoleLog("资源加载完成", evt);
        GameMgr.Res.removeLoadRes("场景");
    }
    onLoading(pres) {
        GameMgr.Res.updateLoadRes("场景", pres, false);
    }

    initEvent() {
        GameMgr.Event.on(EventType.Load_Res_Msg_Event, this, this.onLoadResEvent);
        GameMgr.Event.on(EventType.Init_Game_Event, this, this.onInitGameEvent);
        GameMgr.Event.on(EventType.Play_Game_Event, this, this.onPlayGameEvent);
        GameMgr.Event.on(EventType.Over_Game_Event, this, this.onOverGameEvent);
        NimoForFrame.initEvent();
    }
    onLoadResEvent(e) {
        if (e.allComplete) {
            this.loadSceneRes();
        }
    }

    /**游戏初始化事件 */
    onInitGameEvent(e) {
        GameMgr.UI.openScene(UiSceneType.Home, true, e);
    }
    /**游戏开始时间 */
    onPlayGameEvent(e) {
        GameMgr.UI.openScene(UiSceneType.Game);
    }
    /**游戏结束事件 */
    onOverGameEvent(e) {
        if (e.win) {
            Laya.timer.once(1000, this, () => {
                GameMgr.UI.openScene(UiSceneType.Over, false, e);
            })
        } else {
            GameMgr.UI.openScene(UiSceneType.Over, false, e);
        }
    }

    /************ 3D资源调用-结合游戏实际需求 ************/
    /**加载场景资源 */
    loadSceneRes(index = 0) {
        //主场景
        let scene = Laya.loader.getRes(GameMgr.GlData.sceneResUrl);
        Laya.stage.addChild(scene);
        Laya.stage.setChildIndex(scene, index);
        this.mainScene = scene;

        //遍历查找主相机
        let camera = ExtensionTool.d3_FindNodeByName(scene, 'Main Camera') as Laya.Camera;
        if (camera) {
            this.mainCamera = camera;
        }
        else { GameMgr.ConsoleWarn("未找到场景主相机"); }

        return scene;
    }

    /**加载预制体资源 */
    loadModelRes() {
        return this.loadResByUrl(GameMgr.GlData.modelResUrl);
    }

    /**根据url加载预制体资源 */
    loadResByUrl(url: string) {
        return Laya.loader.getRes(url);
    }


    /**开启场景阴影 */
    createShadow(light: Laya.DirectionLight, mode: Laya.ShadowMode = Laya.ShadowMode.None) {
        light.shadowMode = mode;  //Laya.ShadowMode.SoftHigh
        light.shadowDistance = 60;
        light.shadowResolution = 2048;
        light.shadowCascadesMode = Laya.ShadowCascadesMode.TwoCascades;
        light.shadowNormalBias = 0.4;
        light.shadowStrength = 0.3;
    }

    /**设置场景雾化 */
    setFog(isOpen: boolean, scene?: Laya.Scene3D, color?: Laya.Vector3) {
        scene.enableFog = isOpen;
        if (isOpen) {
            //设置雾化的颜色
            scene.fogColor = color; //"#33D7E3"转RGB
            //设置雾化的起始位置，相对于相机的距离
            scene.fogStart = 300;
            //设置雾化最浓处的距离。
            scene.fogRange = 500;
        }
    }


    /**初始化关卡 */
    initLevel() {
        //TODON 动态关卡
    }

    /**更新关卡信息：皮肤/武器/属性buff */
    updateLevel() {

    }

    //读Json配置文件
    prefab: Laya.Sprite3D;
    mapData: Models;
    readJson() {
        //读取json
        GameMgr.Res.addLoadRes("配置");
        //关卡Json
        let urlBase = "res/json/";
        let allJson = ["Data", "Road", "Stage"]
        let baseEnd = ".json";
        let id = 0;
        let cb = () => {
            if (id < allJson.length) {
                let url = urlBase + allJson[id] + baseEnd;
                Laya.loader.load(url, Laya.Handler.create(this, () => {
                    id++;
                    cb();
                }), null, Laya.Loader.JSON)
            } else {
                //Json配置读取
                for (let i = 0; i < allJson.length; i++) {
                    let url = urlBase + allJson[i] + baseEnd;
                    let data = Laya.Loader.getRes(url);
                    switch (allJson[i]) {
                        case "Data":
                            Data.PlayerHp = data.PlayerHp;
                            Data.PlayerAttack = data.PlayerAttack;
                            Data.PlayerSkill1CD = data.PlayerSkill1CD;
                            Data.PlayerSkill2CD = data.PlayerSkill2CD;
                            Data.AIHp = data.AIHp;
                            Data.AIAttack = data.AIAttack;
                            Data.AIAttackInterval = data.AIAttackInterval;
                            Data.AISkill1CD = data.AISkill1CD;
                            Data.AISkill2CD = data.AISkill2CD;
                            Data.minCarHp = data.minCarHp;
                            Data.carHitReduceHp = data.carHitReduceHp;
                            Data.AIAttackWeight = data.AIAttackWeight;
                            Data.AIgoHealHpPrecent = data.AIgoHealHpPrecent;
                            GameMgr.ConsoleLog("属性配置", data);
                            break;
                        case "Road":
                            Data.stagesData = data[0].Road;
                            GameMgr.ConsoleLog("关卡配置", data);
                            break;
                        case "Stage":
                            this.mapData = data[0];
                            GameMgr.ConsoleLog("地图配置", data);
                            break;
                        default:
                            GameMgr.ConsoleError("配置读取错误", allJson[i]);
                            break;
                    }
                }
                GameMgr.Res.removeLoadRes("配置");
            }
        }
        cb();
        // Laya.loader.load('res/json/Road.json', Laya.Handler.create(this, () => {
        //     let data = Laya.Loader.getRes('res/json/Road.json');
        //     Data.stagesData = data[0].Road;
        //     ExtensionTool.file_LoadJson("res/json/Stage.json", (data) => {
        //         this.mapData = data[0];
        //         GameMgr.Res.removeLoadRes("配置");
        //     });
        // }), null, Laya.Loader.JSON);
    }

    //#region 生成地图
    createMap(models: Models, parent: Laya.Sprite3D) {
        let model = models.self;
        let target = this.prefab.getChildByName(model.name);
        if (target) {
            var clone = ExtensionTool.d3_CloneSprite(target);
        }
        else {
            clone = new Laya.Sprite3D();
        }
        clone.name = model.name;

        parent.addChild(clone);
        clone.transform.position = new Laya.Vector3(model.position.x, model.position.y, model.position.z);
        clone.transform.rotationEuler = new Laya.Vector3(model.rotation.x, model.rotation.y, model.rotation.z);
        // clone.transform.setWorldLossyScale(new Laya.Vector3(model.scale.x, model.scale.y, model.scale.z));

        if (models.models.length > 0) {
            for (let i = 0; i < models.models.length; i++) {
                this.createMap(models.models[i], clone);
            }
        }
    }

}
class Models {
    public models: Models[];
    public self: Model;
}

class Model {
    public name: string;
    public position: V3;
    public rotation: V3;
    public scale: V3;
}

class V3 {
    public x: number;
    public y: number;
    public z: number;
}
//#endregion