import GameMgr from "../managers/GameMgr";

export default class UiScene extends Laya.Scene {
    onAwake() {
        this.size(Laya.stage.width, Laya.stage.height);
        this.init();
        this.initEvent();
    }

    onClosed() {
        GameMgr.Event.offAllCaller(this);
        Laya.timer.clearAll(this);
    }

    /**初始化界面 */
    init() { }
    /**初始化事件 */
    initEvent() { }
}