import Tools from "../Tools";
export default class camShake {
    isShaking
    vibrateMoreSoft(cam: Laya.Sprite3D): void {
        Laya.timer.clearAll(this);
        Tools.objectShake(cam, 0.1, 0.025);
        this.isShaking = true;
        Laya.timer.once(0.1 * 1000, this, () => {
            this.isShaking = false;
        })
    }
    vibrateSoft(cam: Laya.Sprite3D): void {
        Laya.timer.clearAll(this);
        Tools.objectShake(cam, 0.1, 0.05);
        this.isShaking = true;
        Laya.timer.once(0.1 * 1000, this, () => {
            this.isShaking = false;
        })
    }
    vibrateShort(cam: Laya.Sprite3D): void {
        Laya.timer.clearAll(this);
        Tools.objectShake(cam, 0.1, 0.25);
        this.isShaking = true;
        Laya.timer.once(0.1 * 1000, this, () => {
            this.isShaking = false;
        })
    }
    vibrateLong(cam: Laya.Sprite3D): void {
        Laya.timer.clearAll(this);
        Tools.objectShake(cam, 1, 0.25);
        this.isShaking = true;
        Laya.timer.once(1000, this, () => {
            this.isShaking = false;
        })
    }
}