import { PlatformType } from "./EnumType";

/**全局数据 */
export default class GlobalData {
    /**游戏版本号 */
    gameVersion = '1.2.1';
    /**游戏平台 */
    platform: PlatformType;
    /**本次进入游戏局数 */
    playCount: number = 0;

    /**是否输出日志 */
    isShowLog: boolean = false;
    /**是否分包 */
    isSubPackage: boolean = true;

    /**场景资源路径 */
    sceneResUrl: string = "res/scene/Conventional/ResScene.ls";
    /**预设资源路径 */
    modelResUrl: string = "res/scene/Conventional/ResScene.lh";
    /**分批加载可选路径 */
    modelsResUrl: string[] = [];
}
