import D2Tween from "../../D2Tween";

export default class AlphaUITween extends Laya.Script {


    onEnable(): void {
        let target = this.owner as Laya.Sprite;
        D2Tween.toAlpha(target, 1, 0, 500, null, () => { }, false, true)
    }

    onDisable(): void {
        let target = this.owner as Laya.Sprite;
        D2Tween.clearTween(target);
        target.alpha = 1;
    }
}