
import GameMgr from "../../../managers/GameMgr";
export enum TouchEventType {
    start = "touchStart",
    move = "touchMove",
    end = "touchEnd"
}
export default class TouchScan extends Laya.Script {
    static instance;
    onEnable() {
        TouchScan.instance = this;
    }
    //点击事件
    isPressed = false;
    hOriginal = 0;
    vOriginal = 0;
    hAxis = 0;
    vAxis = 0;
    //输出数值
    outAxis = 0;
    onMouseDown(e) {
        // this.isPressed = true;
        // this.hOriginal = e.stageX / Laya.stage.width;
        // this.vOriginal = e.stageY / Laya.stage.height;
        GameMgr.Event.event(TouchEventType.start, [e])
    }
    onMouseMove(e) {
        // if (this.isPressed) {
        //     this.hAxis = e.stageX / Laya.stage.width - this.hOriginal
        //     this.vAxis = e.stageY / Laya.stage.height - this.vOriginal;
        //     //重置初始点
        //     if (false) {
        //         this.hOriginal = e.stageX / Laya.stage.width
        //         this.vOriginal = e.stageY / Laya.stage.height;
        //     }
        // }
        GameMgr.Event.event(TouchEventType.move, [e])
    }
    onMouseUp(e) {
        // this.isPressed = false;
        GameMgr.Event.event(TouchEventType.end, [e])
    }
}