import { EventType, PlatformType, SoundType } from "../../configs/EnumType";
import ExtensionTool from "../../extensionTools/ExtensionTool";
import CamFollowBox, { CamFollowEvent } from "../../extensionTools/mods_Nimo/cam/CamFollowBox";
import CarControlComponent from "../../extensionTools/mods_Nimo/ctrl/CarControlComponent";
import JoystickComponentLeft from "../../extensionTools/mods_Nimo/ctrl/JoystickComponentLeft";
import JoystickComponentRight from "../../extensionTools/mods_Nimo/ctrl/JoystickComponentRight";
import Data from "../../extensionTools/mods_Nimo/Data";
import NimoForFrame from "../../extensionTools/mods_Nimo/NimoForFrame";
import { Nimo_EventType } from "../../extensionTools/mods_Nimo/Nimo_EventType";
import Tools from "../../extensionTools/mods_Nimo/Tools";
import GameMgr from "../../managers/GameMgr";
import MachineHitCheck_AI from "./HitCheck/MachineHitCheck_AI";
import MachineHitCheck_Player from "./HitCheck/MachineHitCheck_Player";
import MachineComponent from "./MachineComponent";
import AttackButton1 from "../UI/AttackButton1";
import SpeedButton from "../UI/SpeedButton";
import AttackButton2 from "../UI/AttackButton2";
import AttackButton3 from "../UI/AttackButton3";
import FxController from "../../extensionTools/mods_Nimo/FxController";
import PopupTextTool from "../../extensionTools/PopupTextTool/PopupTextTool";
import HpShow from "../UI/HpShow";


export default class MachineComponent_Player extends MachineComponent {

    hitBackAxis = 0.1
    hurtTime = 100;
    moveSpeed = 1.5;

    initEvent() {
        // GameMgr.Event.on(CamFollowEvent.ControlEvent, this, this.CamRot);
        GameMgr.Event.on(Nimo_EventType.GameAttack1On, this, this.attackOn);
        GameMgr.Event.on(Nimo_EventType.GameAttack1Off, this, this.attackOff);
        GameMgr.Event.on(Nimo_EventType.GameAttack2On, this, this.skill1On);
        GameMgr.Event.on(Nimo_EventType.GameAttack3On, this, this.skill2On);
    }
    offEvent() {
        // GameMgr.Event.off(CamFollowEvent.ControlEvent, this, this.CamRot);
        GameMgr.Event.off(Nimo_EventType.GameAttack1On, this, this.attackOn);
        GameMgr.Event.off(Nimo_EventType.GameAttack1Off, this, this.attackOff);
        GameMgr.Event.off(Nimo_EventType.GameAttack2On, this, this.skill1On);
        GameMgr.Event.off(Nimo_EventType.GameAttack3On, this, this.skill2On);
        this.attackOff();
    }
    specialEnd() {
        this.isAttack = false;
        this.isInvincible = false;
        this.isEnd = true;
        // this.RealEnd();
        GameMgr.Event.event(EventType.Player_Dead_Event);
        GameMgr.Event.on(EventType.Player_Reborn_Event, this, this.reBorn);
        GameMgr.Event.on(EventType.Player_GiveUp_Event, this, this.RealEnd);

    }
    reBorn() {
        GameMgr.Event.off(EventType.Player_Reborn_Event, this, this.reBorn);
        GameMgr.Event.off(EventType.Player_GiveUp_Event, this, this.RealEnd);
        this.isEnd = false;
        this.heal();
        this.stopAttack();
        this.goMove();
        this.skill2On();
    }
    RealEnd() {
        GameMgr.Event.off(EventType.Player_Reborn_Event, this, this.reBorn);
        GameMgr.Event.off(EventType.Player_GiveUp_Event, this, this.RealEnd);
        Laya.timer.clear(this, this.skill1Off);
        Laya.timer.clear(this, this.skill1Update);
        if (this.skill1Effect)
            this.skill1Effect.destroy();
        if (!NimoForFrame.isEnd) {
            GameMgr.Event.event(EventType.Over_Game_Event, { isWin: false });
        }
    }

    //胜利结束动画
    winEnd() {
        let leftJoyStick: Laya.Image = JoystickComponentLeft.instance.owner as Laya.Image;
        let attackButton1: Laya.Image = AttackButton1.instance.owner as Laya.Image;
        let attackButton2: Laya.Image = AttackButton2.instance.owner as Laya.Image;
        let attackButton3: Laya.Image = AttackButton3.instance.owner as Laya.Image;
        let hpShow: Laya.Image = HpShow.instance.owner as Laya.Image;
        leftJoyStick.set_visible(false);
        attackButton1.set_visible(false);
        attackButton2.set_visible(false);
        attackButton3.set_visible(false);
        hpShow.set_visible(false);
        this.isAttack = true;
        this.stopMove();
        this.skill1Off();
        this.isEnd = true;
        Tools.setTransRotEulerY(this.model.transform, -180);
        let url = "shout";
        this.anim.play(url, 0, 0);
        this.anim.speed = 0.41;
        Laya.timer.once(2000, this, () => {
            this.isEnd = false;
            this.isMove = true;
            if (!this.isAttack)
                this.anim.crossFade("idle", 0.25, 0, 0);
        })
    }
    //#region 普通攻击配置
    attackOn() {
        Laya.timer.frameLoop(1, this, this.attackUpdate);
    }
    attackUpdate() {
        this.goAttack();
    }
    goAttackSpecial() {
        let lessDistance = 10000;
        let target;
        for (let i = 0; i < MachineHitCheck_AI.objs.length; i++) {
            let obj = MachineHitCheck_AI.objs[i];
            if (obj.machine.isDowning || obj.machine.isEnd) {
                continue
            }
            let hitEvent = Tools.CircleCheck_2D(obj.axis, obj.horizontal, 0, this.colliderComp.axis, this.colliderComp.horizontal, 0);
            if (hitEvent.distance < lessDistance) {
                lessDistance = hitEvent.distance;
                target = obj;
            }
        }
        if (target) {
            let way = new Laya.Vector3(target.horizontal - this.colliderComp.horizontal, 0, target.axis - this.colliderComp.axis)
            this.getWayAngle(way);
        }
    }
    attackDoSpecial() {
        // 碰撞判定
        let ais = MachineHitCheck_AI.objs;
        let nowAttackId = this.attackCombe;
        let range = this.attackRange[nowAttackId];
        for (let i = 0; i < ais.length; i++) {
            let machine = ais[i].machine;
            let hitEvent = Tools.CircleCheck_2D(this.colliderComp.axis, this.colliderComp.horizontal, range, ais[i].axis, ais[i].horizontal, ais[i].radius)
            if (hitEvent.isHit) {
                let dir = Tools.Vector3Reduce(machine.player.transform.position.clone(), this.player.transform.position.clone());
                let forward = Tools.getTransformForward(this.model.transform);
                let angle = Tools.getAngle(dir, forward);
                if (angle < 90) {
                    //伤害浮动，显示字
                    let axis = Tools.randomNumber(0.8, 1.2)
                    let attack = Math.floor(this.attack * axis);
                    let typeId = 0;
                    if (axis > 1) {
                        typeId = 1;
                    }
                    if (machine.goHurt(attack)) {
                        let pos = machine.model.transform.position.clone();
                        pos.y += 5;
                        PopupTextTool.PopText(attack, typeId, pos);
                        GameMgr.Platform.vibrateShort();
                    }
                }
            }
        }
        Data.camShake.vibrateShort(Data.mainCam);

    }
    attackOff() {
        Laya.timer.clear(this, this.attackUpdate);
    }
    //#endregion

    //#region 剑刃风暴配置
    skill1Range = 8;
    skill1Time = 2500;
    skill1CD = 6000;
    skill1Effect;
    skill1On() {
        if (!this.isGotWeapon)
            return;
        if (NimoForFrame.isEnd)
            return;
        if (this.isEnd)
            return;
        if (!this.isMove)
            return;
        if (this.isAttack)
            return;
        if (this.isDowning)
            return;
        this.isAttack = true;
        this.isInvincible = true;
        // this.stopMove();
        let url = "swordSkill";
        this.anim.play(url, 0, 0);
        Laya.timer.frameLoop(1, this, this.skill1Update);
        Laya.timer.once(this.skill1Time, this, this.skill1Off);
        AttackButton2.instance.unActive(this.skill1CD);
        setTimeout(() => {
            AttackButton2.instance.goActive();
        }, this.skill1CD);
        let hitParent = this.effect.getChildByName("SwordSpinEffect")
        this.skill1Effect = FxController.playEffectReturn("SwordSpinEffect", hitParent);
        this.anim.getControllerLayer(1).defaultWeight = 0;
        GameMgr.ConsoleLog("技能1开启")
        Data.sound.play(SoundType.SwordSkill);
    }
    skill1Update() {
        this.MoveControl();
        this.skill1Do();
    }
    skill1Do() {
        let ais = MachineHitCheck_AI.objs;
        let range = 6;
        for (let i = 0; i < ais.length; i++) {
            let machine = ais[i].machine;
            let hitEvent = Tools.CircleCheck_2D(this.colliderComp.axis, this.colliderComp.horizontal, range, ais[i].axis, ais[i].horizontal, ais[i].radius)
            if (hitEvent.isHit) {
                //伤害浮动，显示字
                let axis = Tools.randomNumber(0.8, 1.2)
                let attack = Math.floor(this.attack * axis);
                let typeId = 0;
                if (axis > 1) {
                    typeId = 1;
                }
                if (machine.goHurt(attack)) {
                    let pos = machine.model.transform.position.clone();
                    pos.y += 5;
                    PopupTextTool.PopText(attack, typeId, pos);
                    GameMgr.Platform.vibrateShort();
                }
            }

        }
    }
    skill1Off() {
        if (this.isEnd)
            return;
        this.anim.play("idle", 0, 0);
        if (this.skill1Effect)
            this.skill1Effect.destroy();
        Laya.timer.clear(this, this.skill1Off);
        Laya.timer.clear(this, this.skill1Update);
        this.isAttack = false;
        this.isInvincible = false;
        GameMgr.ConsoleLog("技能1关闭")
    }
    //#endregion

    //#region 咆哮配置
    skill2Effect: Laya.Sprite3D;
    skill2Range = 8;
    skill2DoTime = 200;
    skill2Time = 600;
    skill2CD = 6000;
    skill2On() {
        if (NimoForFrame.isEnd)
            return;
        if (this.isEnd)
            return;
        if (!this.isMove)
            return;
        if (this.isAttack)
            return;
        if (this.isDowning)
            return;
        this.isAttack = true;
        this.isInvincible = true;
        this.stopMove();
        let url = "shout";
        this.anim.play(url, 0, 0);
        Laya.timer.once(this.skill2DoTime, this, this.skill2Do);
        AttackButton3.instance.unActive(this.skill2CD);
        Laya.timer.once(this.skill2Time, this, this.skill2Off);
        setTimeout(() => {
            AttackButton3.instance.goActive();
        }, this.skill2CD);
        GameMgr.ConsoleLog("技能2开启")
        Data.sound.play(SoundType.Shout);
    }
    skill2Do() {
        let ais = MachineHitCheck_AI.objs;
        let range = 10;
        for (let i = 0; i < ais.length; i++) {
            let machine = ais[i].machine;
            let hitEvent = Tools.CircleCheck_2D(this.colliderComp.axis, this.colliderComp.horizontal, range, ais[i].axis, ais[i].horizontal, ais[i].radius)
            if (hitEvent.isHit) {
                machine.HitBack(this.colliderComp.axis, this.colliderComp.horizontal, 50);
                let axis = Tools.randomNumber(0.8, 1.2)
                let attack = Math.floor(this.attack * axis);
                let typeId = 0;
                if (axis > 1) {
                    typeId = 1;
                }
                if (machine.goHurt(attack, true)) {
                    let pos = machine.model.transform.position.clone();
                    pos.y += 5;
                    PopupTextTool.PopText(attack, typeId, pos);
                    GameMgr.Platform.vibrateShort();
                }
                machine.goDown();

            }
        }
        let parent = this.skill2Effect;
        FxController.playEffectDestroy("BoomEffect", parent);
    }
    skill2Off() {
        if (this.isEnd)
            return;
        this.isAttack = false;
        this.isInvincible = false;
        if (!this.isDowning)
            this.goMove();
        GameMgr.ConsoleLog("技能2关闭")
    }
    //#endregion


    specialInit() {
        //控制器控制
        let carControl: Laya.Image = CarControlComponent.instance.owner as Laya.Image;
        let leftJoyStick: Laya.Image = JoystickComponentLeft.instance.owner as Laya.Image;
        let rightJoyStick: Laya.Image = JoystickComponentRight.instance.owner as Laya.Image;
        let attackButton1: Laya.Image = AttackButton1.instance.owner as Laya.Image;
        let attackButton2: Laya.Image = AttackButton2.instance.owner as Laya.Image;
        let attackButton3: Laya.Image = AttackButton3.instance.owner as Laya.Image;
        let speed: Laya.Image = SpeedButton.instance.owner as Laya.Image;
        carControl.set_visible(false);
        leftJoyStick.set_visible(true);
        rightJoyStick.set_visible(false);
        attackButton1.set_visible(false);
        attackButton2.set_visible(false);
        attackButton3.set_visible(true);
        speed.set_visible(false);
        //特效
        this.skill2Effect = this.effect.getChildByName("BoomEffect") as Laya.Sprite3D;
        //镜头
        let cam = this.modelRobot.getChildByName("CamRobotPoint");
        // let camComp = cam.getComponent(CamFollowBox)
        // if (camComp)
        //     camComp.destroy();
        // cam.addComponent(CamFollowBox);
        cam.addChild(Data.mainCam);
        let realCam: Laya.Camera = Data.mainCam.getChildByName("Main Camera") as Laya.Camera;
        realCam.fieldOfView = 40
        Data.mainCam.transform.localPosition = new Laya.Vector3();
        Data.mainCam.transform.localRotationEuler = new Laya.Vector3();
        Data.mainCam.transform.localScale = new Laya.Vector3(1, 1, 1);
        //属性
        this.attack = Data.PlayerAttack;
        this.skill1CD = Data.PlayerSkill1CD;
        this.skill2CD = Data.PlayerSkill2CD;
    }
    getWeaponSpecial() {
        let attackButton1: Laya.Image = AttackButton1.instance.owner as Laya.Image;
        let attackButton2: Laya.Image = AttackButton2.instance.owner as Laya.Image;
        let attackButton3: Laya.Image = AttackButton3.instance.owner as Laya.Image;
        attackButton1.set_visible(true);
        attackButton2.set_visible(true);
        attackButton3.set_visible(true);
        GameMgr.Event.event(EventType.AirUI_Off);
    }
    getControlComp() {
        this.controlComp = JoystickComponentLeft;
    }
    getColliderComp() {
        this.colliderComp = this.player.addComponent(MachineHitCheck_Player)
    }
    MoveControl() {
        if (!this.controlComp)
            return;
        let xAxis = this.controlComp.xAxis;
        let yAxis = this.controlComp.yAxis;
        if (xAxis == 0 && yAxis == 0) {
            this.anim.getControllerLayer(1).defaultWeight = 0;
            return;
        }
        let joyAngle;
        if (yAxis == 1 && xAxis == 0) {
            joyAngle = 0
        } else if (yAxis == -1 && xAxis == 0) {
            joyAngle = 180;
        }
        else {
            let hudu = Math.atan2(xAxis, yAxis);
            joyAngle = hudu * 180 / Math.PI
        }
        let goAngle = this.camAngle + joyAngle;
        //玩家朝向角度
        if (JoystickComponentLeft.instance.isPressed) {
            //角度处理(0~360)
            let distance = goAngle - this.nowAngle;
            if (goAngle - this.nowAngle > 180) {
                distance -= 360;
            } else if (this.nowAngle - goAngle > 180) {
                distance += 360;
            }
            let lerpAxis = 0.3
            let goAxis = distance * lerpAxis;
            this.nowAngle += goAxis
            if (this.nowAngle > 180) {
                this.nowAngle -= 360;
            } else if (this.nowAngle <= -180) {
                this.nowAngle += 360;
            }
        }
        let angle = goAngle;
        let way = new Laya.Vector3(-xAxis, 0, yAxis)
        let wayAxis = Laya.Vector3.scalarLength(way);
        if (wayAxis > 1) {
            wayAxis = 1;
        }
        this.Move(angle, wayAxis);
    }
    hurtSpecial() {

        if (!NimoForFrame.isPlayerCar) {
            Data.sound.play(SoundType.PlayerHit);
            GameMgr.Platform.vibrateShort();
        }
    }
    goDownSpecial() {
        this.skill1Off();
        // this.skill2Off();
    }
    getWayAngle(way) {
        if (way.x == 0 && way.z == 0) {
            this.anim.getControllerLayer(1).defaultWeight = 0;
            return;
        }
        let joyAngle;
        if (way.z == -1 && way.x == 0) {
            joyAngle = 0
        } else if (way.z == 1 && way.x == 0) {
            joyAngle = 180;
        }
        else {
            let hudu = Math.atan2(way.x, -way.z);
            joyAngle = hudu * 180 / Math.PI
        }
        let angle = joyAngle;
        Tools.setTransRotEulerY(this.model.transform, -angle)
    }


}