import ExtensionTool from "../ExtensionTool";
import Data from "./Data";

export default class FxController {

    static effect;
    static init() {
        this.effect = Data.prefabs.getChildByName("Effect");
        for (let i = 0; i < this.effect.numChildren; i++) {
            let obj = this.effect.getChildAt(i) as Laya.Sprite3D
            this.playEffectDestroy(obj.name, Data.scene3D, 1000)
        }
    }
    static playEffectPositon(url, pos, time = 1000) {
        let prefab = this.effect.getChildByName(url) as Laya.Sprite3D;
        let obj = prefab.clone() as Laya.Sprite3D;
        Data.scene3D.addChild(obj)
        obj.transform.position = pos;
        Laya.timer.once(time, this, () => {
            obj.destroy(true);
        })
        return obj;
    }
    static playEffectDestroy(url, parent, time = 1000) {
        let prefab = this.effect.getChildByName(url) as Laya.Sprite3D;
        let obj = prefab.clone() as Laya.Sprite3D;
        parent.addChild(obj);
        obj.transform.localPosition = new Laya.Vector3(0, 0, 0);
        obj.transform.localRotationEuler = new Laya.Vector3(0, 0, 0);
        obj.transform.localScale = new Laya.Vector3(1, 1, 1);
        Laya.timer.once(time, this, () => {
            obj.destroy(true);
        })
        return obj;
    }
    static playEffectReturn(url, parent) {
        let prefab = this.effect.getChildByName(url) as Laya.Sprite3D;
        let obj = prefab.clone() as Laya.Sprite3D;
        parent.addChild(obj);
        obj.transform.localPosition = new Laya.Vector3(0, 0, 0);
        obj.transform.localRotationEuler = new Laya.Vector3(0, 0, 0);
        obj.transform.localScale = new Laya.Vector3(1, 1, 1);
        return obj;
    }

}