export default class D2Tween {
    /**透明度缓动 */
    static toAlpha(target: Laya.Sprite, startValue: number, endValue: number, duration: number, ease: Function = null, completeCB: Function = null, isReset: boolean = false, isLoop: boolean = false) {
        if (!target) return;

        var handler = null;
        target.alpha = startValue;
        alpha1();

        function alpha1() {
            handler = isLoop || isReset ? new Laya.Handler(this, alpha2) : new Laya.Handler(this, () => { completeCB && completeCB(); completeCB = null });
            Laya.Tween.to(target, { alpha: endValue }, duration, ease, handler);
        }

        function alpha2() {
            handler = isLoop ? new Laya.Handler(this, alpha1) : null;
            Laya.Tween.to(target, { alpha: startValue }, duration, ease, handler);
        }
    }

    /**缩放缓动 */
    static toScale(target: Laya.Sprite, startValue: number, endValue: number, duration: number, ease?: Function, completeCB?: Function, isReset?: boolean, isLoop: boolean = false) {
        if (!target) return;

        var handler = null;
        target.scale(startValue, startValue);
        scale1();

        function scale1() {
            handler = isLoop || isReset ? new Laya.Handler(this, scale2) : new Laya.Handler(this, () => { completeCB && completeCB(); completeCB = null });
            Laya.Tween.to(target, { scaleX: endValue, scaleY: endValue }, duration, ease, handler);
        }

        function scale2() {
            handler = isLoop ? new Laya.Handler(this, scale1) : null;
            Laya.Tween.to(target, { scaleX: startValue, scaleY: startValue }, duration, ease, handler);
        }
    }

    /**移动缓动 */
    static toPosition(target: Laya.Sprite, position: { x: number, y: number }, duration: number, ease: Function = null, completeCB: Function = null, isLoop = false, type = 0) {
        let curPos = new Laya.Vector2(target.x, target.y);
        pos1();

        function pos1() {
            if (isLoop) {
                switch (type) {
                    case 0:
                        var handler = new Laya.Handler(this, pos2);
                        break;
                    case 1:
                        handler = new Laya.Handler(this, pos3);
                        break;
                }
            }
            else {
                handler = new Laya.Handler(this, () => { completeCB && completeCB(); completeCB = null })
            }
            Laya.Tween.to(target, { x: position.x, y: position.y }, duration, ease, handler);
        }

        function pos2() {
            Laya.Tween.to(target, { x: curPos.x, y: curPos.y }, duration, ease, Laya.Handler.create(this, pos1));
        }

        function pos3() {
            target.pos(curPos.x, curPos.y);
            pos1();
        }
    }


    /**清除缓动 */
    static clearTween(target: Laya.Sprite) {
        Laya.Tween.clearAll(target);
    }
}