import Tools from "../../../extensionTools/mods_Nimo/Tools";
import TraceCircuit from "./TraceCircuit";

export default class TraceProgressTracker extends Laya.Script3D {
    public circuit: TraceCircuit;

    private lookAheadForTargetOffset: number = 1;
    private lookAheadForTargetFactor: number = 0.001;
    private lookAheadForSpeedOffset: number = 1;
    private lookAheadForSpeedFactor: number = 0.001;

    private isSmoothAlongRoute: boolean = true;

    private pointToPointThreshold: number = 4;

    /**终点 */
    public endDistance: number;

    public self: Laya.Sprite3D;
    public target: Laya.Transform3D;

    public isPass: boolean;

    /**生成点 */
    public progressDistance: number = 0;
    /**速度 */
    public speedFactor: number = 0;

    public progressNum: number = 0;
    private lastPosition: Laya.Vector3 = new Laya.Vector3();
    public speed: number = 0;

    public targetPoint: RoutePoint;
    public speedPoint: RoutePoint;
    public progressPoint: RoutePoint;



    init(distance = 50) {
        this.speed = 0;
        this.self = this.owner as Laya.Sprite3D;
        this.target = this.self.transform;
        this.reset(0, distance);
    }

    reset(num = 0, distance = 50) {
        if (!this.circuit) return;

        this.progressNum = num;
        this.progressDistance = distance;
        if (!this.isSmoothAlongRoute) {
            //Nigel New
            // this.target.position = this.circuit.waypoints[this.progressNum].position;
            // this.target.rotation = this.circuit.waypoints[this.progressNum].rotation;
        }
        else {
            this.target.position = this.circuit.getRoutePoint(this.progressDistance + this.lookAheadForTargetOffset * this.speedFactor + this.lookAheadForTargetFactor * this.speed).position;
            let rot = new Laya.Quaternion();
            let quaternion = this.circuit.getRoutePoint(this.progressDistance + this.lookAheadForSpeedOffset + this.lookAheadForSpeedFactor * this.speed).direction;
            let upDir = this.circuit.getUpDirection(this.progressDistance + this.lookAheadForSpeedOffset + this.lookAheadForSpeedFactor * this.speed);
            Laya.Quaternion.rotationLookAt(quaternion, upDir, rot);
            Laya.Quaternion.invert(rot, rot);
            this.target.rotation = rot;
            this.lastPosition = this.target.position.clone();
        }

    }
    forwardAxis = 0;
    onTrackerUpdate() {
        if (this.circuit == null) return;
        if (this.speedFactor == 0) {
            return;
        }

        if (this.progressDistance >= this.circuit.accumulateDistance - 0.5) {
            console.log("到达终点")
            return;
        }
        if (this.self == null) return;

        if (this.isSmoothAlongRoute) {
            let deltaTime = 0.02;
            if (deltaTime > 0) {
                let v3 = new Laya.Vector3();
                Laya.Vector3.subtract(this.lastPosition, this.self.transform.position.clone(), v3);
                let length = Laya.Vector3.scalarLength(v3);
                this.speed = Laya.MathUtil.lerp(this.speed, length / deltaTime, deltaTime); //===============不确定==============
            }
            this.forwardAxis = this.lookAheadForTargetOffset * this.speedFactor + this.lookAheadForTargetFactor * this.speed;
            let forwardAxis = this.forwardAxis;
            this.target.position = this.circuit.getRoutePoint(this.progressDistance + forwardAxis).position;
            let rot = new Laya.Quaternion();


            //TODO 测试注释            
            let rotOffset = this.speedFactor * 25;
            if (rotOffset < 0)
                rotOffset = 0;
            let quaternion = this.circuit.getRoutePoint(this.progressDistance + this.lookAheadForSpeedOffset + this.lookAheadForSpeedFactor * this.speed).direction;
            // let quaternion = this.getRotation(forwardAxis);
            let upDir = this.circuit.getUpDirection(this.progressDistance + this.lookAheadForSpeedOffset + this.lookAheadForSpeedFactor * this.speed);
            Laya.Quaternion.rotationLookAt(quaternion, upDir, rot);
            Laya.Quaternion.invert(rot, rot);
            Laya.Quaternion.lerp(this.target.rotation, rot, 0.2, rot); //0.2
            this.target.rotation = rot;

            this.progressPoint = this.circuit.getRoutePoint(this.progressDistance);
            var progressDelta = this.circuit.v3Subtract(this.progressPoint.position, this.self.transform.position);
            if (Laya.Vector3.dot(progressDelta, this.progressPoint.direction) > 0) {
                this.progressDistance += Laya.Vector3.scalarLength(progressDelta) * 0.5;
            }
            else if (this.progressDistance > 10) {
                this.progressDistance -= Laya.Vector3.scalarLength(progressDelta) * 0.5;
            }
            this.lastPosition = this.self.transform.position.clone();
        }
    }

    v3Subtract(a: Laya.Vector3, b: Laya.Vector3): Laya.Vector3 {
        var v = new Laya.Vector3();
        Laya.Vector3.subtract(a, b, v);

        return v;
    }
    //获得前进插值
    getNextDistance(speedFactor) {
        let nextProgressDistance = this.progressDistance;
        let forwardAxis = this.lookAheadForTargetOffset * speedFactor + this.lookAheadForTargetFactor * this.speed;
        let selfPos = this.circuit.getRoutePoint(nextProgressDistance + forwardAxis).position;
        let progressPoint = this.circuit.getRoutePoint(nextProgressDistance);
        let delta = this.circuit.v3Subtract(progressPoint.position, selfPos);
        if (Laya.Vector3.dot(delta, progressPoint.direction) > 0) {
            nextProgressDistance += Laya.Vector3.scalarLength(delta) * 0.5;
        }
        else if (nextProgressDistance > 10) {
            nextProgressDistance -= Laya.Vector3.scalarLength(delta) * 0.5;
        }
        return nextProgressDistance;
    }
    //获取转弯表现插值
    getHorizontalOffset(speed) {
        if (speed < 0) {
            return 0;
        }
        speed *= 15;
        let base = 0.1;
        var p1 = this.circuit.getRountePosition(this.progressDistance);
        var p2 = this.circuit.getRountePosition(this.progressDistance + base); //+ 0.1
        var p3 = this.circuit.getRountePosition(this.progressDistance + base + speed); //+ 0.1
        p1.y = 0;
        p2.y = 0;
        p3.y = 0;

        let dir1 = this.v3Subtract(p1, p2);
        let dir2 = this.v3Subtract(p1, p3);
        let up = new Laya.Vector3(0, 1, 0);

        let angle = Tools.getAngle(dir1, dir2)
        let crossNoraml = new Laya.Vector3();
        Laya.Vector3.cross(dir1, dir2, crossNoraml);
        if (Tools.getAngle(up, crossNoraml) > 90) {
            angle *= -1;
        }
        return angle;
    }
    getRotation(speed) {
        speed *= 15;
        speed += 5;
        var p1 = this.circuit.getRountePosition(this.progressDistance);
        var p2 = this.circuit.getRountePosition(this.progressDistance + speed); //+ 0.1
        var p3 = this.circuit.getRountePosition(this.progressDistance + 5);
        let dir1 = this.v3Subtract(p1, p2);
        let dir2 = this.v3Subtract(p1, p3);
        let up = Tools.getTransformUp(this.target);
        let dir3 = this.getProjection(dir1, up);
        let forward = Tools.getTransformForward(this.target);
        let angle = Tools.getAngle(dir3, forward)
        if (angle < 3 && angle > 90) {
            return dir2;
        }
        return dir1;
    }
    //获得法线对应平面投影
    getProjection(dir, normal) {
        let upProject = Tools.Vector3Axis(normal, Laya.Vector3.dot(dir, normal));
        let downProject = Tools.Vector3Reduce(dir, upProject);
        return downProject;
    }
}

class RoutePoint {
    public position: Laya.Vector3;
    public direction: Laya.Vector3;

    constructor(position, direction) {
        this.position = position;
        this.direction = direction;
    }
}
