
import CarComponent from "../CarComponent";
import TraceProgressTracker from "../Move/TraceProgressTracker";

export default class CarHitCheck extends Laya.Script3D {
    tracer: TraceProgressTracker;
    carComp: CarComponent;

    axis;
    horizontal;

    widthAxis = 3;
    rangeAxis = 7;

    widthHorizontal = 3.5;
    rangeHorizontal = 5;

    active = false;

    onEnable() {
        this.tracer = this.owner.getComponent(TraceProgressTracker);
        this.carComp = this.owner.getComponent(CarComponent);
        this.axis = this.tracer.progressDistance;
        this.horizontal = this.carComp.horizontal;
        this.active = true;
        this.init();
    }
    onDisable() {
        Laya.timer.clearAll(this);
    }

    onUpdate() {
        if (!this.active)
            return;
        this.axis = this.tracer.progressDistance
        this.horizontal = this.carComp.horizontal;
        this.CarCheckUpdate();
    }

    //获取计算点，和保存要输出的数值
    forwardSpeed;
    horizontalPos;
    colliderGetAxis(forwardSpeed, horizontalPos) {
        if (!this.active)
            return;
        this.axis = this.tracer.getNextDistance(forwardSpeed);
        this.horizontal = horizontalPos.x;
        this.forwardSpeed = forwardSpeed;
        this.horizontalPos = horizontalPos;
    }
    colliderCheck() {
        this.CarCheckUpdate();
    }

    init() {

    }
    CarCheckUpdate() {

    }

}