import { EventType } from "../../configs/EnumType";
import GameMgr from "../../managers/GameMgr";
import Platform, { NativeAd } from "../Platform";

/**vivo平台 */
export default class VivoPlatform extends Platform {
    /******************** 广告 ********************/
    initAd() {
        this.canSubPackage = true;
        this.platformApi = Laya.Browser.window.qg;

        this.bannerId = '';
        this.videoId = '46f0bb21385841d7a492eb8c9f0c45ac';
        this.nativeId = '77eade16821f45349e58fc6f3796f3c0';
        this.boxPortalId = '';

        // this.createBannerAd();
        this.createVideoAd();
        // this.createBoxProtalAd();
    }

    //#region Banner广告
    createBannerAd(isShow: boolean = false) {
        // style内无需设置任何字段，banner会在屏幕底部居中显示，
        // 没有style字段，banner会在上边显示
        // 1053以及以上版本支持属性设置
        if (this.getPlatformVersionCode < 1031) {
            console.warn("版本过低暂不支持Banner广告组件")
            return;
        }

        if (this.bannerAd) {
            this.destroyBannerAd();
        }

        this.bannerAd = Laya.Browser.window.qg.createBannerAd({
            posId: this.bannerId,
            style: {},//left:0,top:0
            adIntervals: 30
        });

        this.bannerAd.onLoad(() => {
            if (isShow) {
                this.bannerAd.show().then(() => { }).catch(err => {
                    this.createBannerAd();
                });
            }
            console.log("Banner广告加载成功");
        });

        this.bannerAd.onError(err => {
            this.bannerAd = null;
            console.error("Banner广告加载失败", JSON.stringify(err));
        });

        this.bannerAd.onClose(() => {
            this.createBannerAd();
        });

        this.bannerAd.onSize(res => {
            // console.log(res.width, res.height);
        });
    }

    showBannerAd() {
        if (!this.bannerAd) {
            this.createBannerAd(true);
            return;
        }

        this.bannerAd.show().then(() => { }).catch((err) => {
            this.createBannerAd(true);
        });
    }

    hideBannerAd() {
        if (!this.bannerAd) return;
        this.bannerAd.hide();
    }

    destroyBannerAd() {
        if (!this.bannerAd) return;
        this.bannerAd.destroy();
        this.bannerAd = null;
    }
    //#endregion

    //#region 激励视频广告
    createVideoAd() {
        if (this.getPlatformVersionCode < 1041) {
            console.warn("版本过低暂不支持激励视频广告组件")
            return;
        }

        if (this.videoAd) {
            this.destroyVideoAd();
        }

        this.videoAd = Laya.Browser.window.qg.createRewardedVideoAd({
            posId: this.videoId
        });

        this.videoAd.onLoad(res => {
            console.log("激励视频广告加载成功");
        });

        this.videoAd.onError(err => {
            this.videoAd = null;
            console.error("激励视频广告加载失败", JSON.stringify(err));
        });

        this.videoAd.onClose((res) => {
            if (res) {
                if (res.isEnded) {
                    this.videoFinishCallback && this.videoFinishCallback();
                }
                else {
                    this.videoCancelCallback && this.videoCancelCallback();
                }

                this.videoFinishCallback = null;
                this.videoCancelCallback = null;
                this.videoAd.load();

                GameMgr.Event.event(EventType.Close_Video_Event, { isEnded: res.isEnded });
                Laya.SoundManager.muted = false;
            }
        })
    }

    showVideoAd(finishCB?: Function, cancelCB?: Function) {
        if (!this.videoAd) {
            this.createVideoAd();
            finishCB && finishCB();
            return;
        }

        this.videoFinishCallback = finishCB;
        this.videoCancelCallback = cancelCB;

        //vivo播放视频需要中断游戏音效
        Laya.SoundManager.muted = true;
        this.videoAd.show().then(() => {
            // //恢复游戏音效
            // Laya.SoundManager.muted = false;
        }).catch(err => {
            finishCB && finishCB();
            this.createVideoAd();
            this.videoFinishCallback = null;
            this.videoCancelCallback = null;
            //恢复游戏音效
            Laya.SoundManager.muted = false;
        })

        GameMgr.Event.event(EventType.Show_Video_Event);
    }

    destroyVideoAd() {
        if (!this.videoAd) return;
        this.videoAd = null;
    }
    //#endregion

    //#region 九宫格广告
    createBoxProtalAd() {
        if (!Laya.Browser.window.qg.createBoxPortalAd) {
            console.warn("版本过低暂不支持九宫格广告组件");
            return;
        }

        this.boxPortalAd = Laya.Browser.window.qg.createBoxPortalAd({
            posId: this.boxPortalId,
            marginTop: this.getSystemInfoSync().screenHeight * 0.4
        });
        this.boxPortalAd.onLoad(() => {
            console.log("九宫格广告加载成功");
        });
        this.boxPortalAd.onError(err => {
            this.boxPortalAd = null;
            console.error("九宫格广告加载失败", JSON.stringify(err));
        });
        this.boxPortalAd.onClose(() => {
            this.boxPortalCloseCallback && this.boxPortalCloseCallback()
            this.boxPortalCloseCallback = null
        });
    }

    showBoxProtalAd(closeCB?: Function) {
        if (!this.boxPortalAd) {
            closeCB && closeCB();
            this.createBoxProtalAd();
            return;
        }

        this.boxPortalAd.show().then(() => {
            this.boxPortalCloseCallback = closeCB;
        }).catch(err => {
            this.destroyBoxProtalAd();
            closeCB && closeCB();
        })
    }

    destroyBoxProtalAd() {
        if (!this.boxPortalAd) return;
        this.boxPortalAd.destroy();
        this.boxPortalAd = null;
        this.createBoxProtalAd();
    }
    //#endregion

    //#region 原生广告
    createNativeAd(id?: string, cb?: Function): NativeAd {
        if (this.getPlatformVersionCode < 1053) {
            console.warn("版本过低暂不支持原生广告组件");
            return;
        }
        var ad = new NativeAd;

        var nativeAd = Laya.Browser.window.qg.createNativeAd({
            posId: id ? id : this.nativeId,
        });
        nativeAd.onLoad((res) => {
            if (res && res.adList) {
                ad.nativeAd = nativeAd;
                let curAd = res.adList.pop();
                ad.title = curAd.title;
                ad.adId = curAd.adId.toString();
                ad.imgUrl = curAd.imgUrlList[0];
                ad.desc = curAd.desc;
                console.log("原生广告加载成功");
                ad.nativeAd.reportAdShow({ adId: ad.adId });
                if (cb) {
                    cb(ad);
                    cb = null;
                }
            }
        });

        nativeAd.onError(err => {
            console.error("原生广告加载失败", JSON.stringify(err));
            if (cb) {
                cb(null);
                cb = null;
            }
        });

        return ad;
    }

    showNativeAd(ad?: NativeAd, cb?: Function) {
        if (ad && ad.nativeAd) {
            // ad.nativeAd.reportAdShow({ adId: ad.adId });
            ad.nativeAd.reportAdClick({ adId: ad.adId });
            cb && cb(true);
        }
        cb && cb(false)
    }

    loadNativeAd(ad?: NativeAd, cb?: Function) {
        if (ad && ad.nativeAd) {
            ad.nativeAd.load().then(() => {
                cb && cb(ad);
            }).catch(err => {
                cb && cb(null);
            });
        }
        else {
            this.createNativeAd(null, cb);
        }
    }

    destroyNativeAd(ad?: NativeAd) {
        ad = null;
    }
    //#endregion

    /******************** 平台API ********************/
    /**当前平台版本号 */
    get getPlatformVersionCode() {
        return this.getSystemInfoSync().platformVersionCode;
    }

    /**
     * 弹窗
     * @param msg 文本
     * @param duration 0为短时，1为长时 
     */
    showToast(msg: string, duration: number = 0) {
        Laya.Browser.window.qg.showToast({
            message: msg,//提示文字
            duration: duration,//显示时长
        })
    }

    /**创建桌面图标 */
    createShortcut(cb?: Function) {
        this.hasShortcut((res) => {
            // 判断图标未存在时，创建图标
            if (res == false) {
                Laya.Browser.window.qg.installShortcut({
                    success: function () {
                        // 执行用户创建图标奖励
                        cb && cb();
                    },
                    fail: function (err) { },
                    complete: function () { }
                })
            }
        });
    }
    /**是否已创建桌面图标 */
    hasShortcut(cb?: Function) {
        Laya.Browser.window.qg.hasShortcutInstalled({
            success: function (res) {
                cb && cb(res);
            },
            fail: function (err) { },
        })
    }
}