
import D3Tween from "../../extensionTools/D3Tween";
import Data from "../../extensionTools/mods_Nimo/Data";
import FxController from "../../extensionTools/mods_Nimo/FxController";
import NimoForFrame from "../../extensionTools/mods_Nimo/NimoForFrame";
import Tools from "../../extensionTools/mods_Nimo/Tools";
import GameMgr from "../../managers/GameMgr";
import CarComponent from "./CarComponent";
import Car_NPC from "./Car_NPC";
import CarHitCheck_NPC from "./HitCheck/CarHitCheck_NPC";
import TraceProgressTracker from "./Move/TraceProgressTracker";

export default class CarComponent_NPC extends CarComponent {

    npcId = 0;
    maxSpeed = 100;

    getControlComp() {
        if (!this.controlComp)
            this.controlComp = this.user.addComponent(Car_NPC);
        else
            this.controlComp = this.user.getComponent(Car_NPC);



    }
    getColliderComp() {
        this.colliderComp = this.user.addComponent(CarHitCheck_NPC);
    }
    gameSpecialEnd() {

    }

    init(circuit, distance = 50, horizontal = 0, skinId = 1) {
        //获取玩家
        this.user = this.owner as Laya.Sprite3D;
        //#region 轨迹初始化
        let tracerComp = this.user.getComponent(TraceProgressTracker)
        if (tracerComp)
            tracerComp.destroy();
        let tracer = this.user.addComponent(TraceProgressTracker);
        tracer.circuit = circuit;
        tracer.init(distance);
        this.circuit = circuit
        this.tracker = tracer;
        //#endregion

        //#region 模型初始化
        let modelCar = this.user.getChildByName("ModelCar") as Laya.Sprite3D;
        this.modelCar = modelCar;
        this.modelRobot = this.user.getChildByName("ModelRobot") as Laya.Sprite3D;
        modelCar.active = true;
        // this.modelRobot.active = true;
        // this.modelRobot.transform.localScale = new Laya.Vector3(0, 0, 0);
        // let modelIndex = skinId
        this.skinId = skinId



        //model赋给
        if (this.model) {
            this.model.destroy();
        }
        let modelUrl = "Model" + skinId.toString();
        let prefab = Data.prefabs.getChildByName("Car").getChildByName(modelUrl) as Laya.Sprite3D;
        this.model = prefab.clone() as Laya.Sprite3D;
        modelCar.addChild(this.model);
        this.model.transform.localPosition = new Laya.Vector3();
        this.model.transform.localRotationEuler = new Laya.Vector3();
        this.model.transform.localScale = new Laya.Vector3(100, 100, 100);

        //特效
        this.carHitEffect = this.model.getChildByName("CarHit") as Laya.Sprite3D;
        this.carPushEffect = this.model.getChildByName("PushEffect") as Laya.Sprite3D;
        //轮胎
        let car = this.model.getChildAt(0) as Laya.MeshSprite3D;
        this.car = car;
        this.wheels = [];
        for (let i = 0; i < car.numChildren; i++) {
            let wheel = car.getChildAt(i) as Laya.Sprite3D;
            this.wheels.push(wheel);
        }
        //#endregion

        let mat = car.meshRenderer.material.clone()
        mat.albedoColorR = Tools.randomNumber(0, 1);
        mat.albedoColorG = Tools.randomNumber(0, 1);
        mat.albedoColorB = Tools.randomNumber(0, 1);
        car.meshRenderer.material = mat;
        //位置初始化
        this.horizontal = horizontal;
        this.model.transform.localPosition = new Laya.Vector3(-this.horizontal, 0, 0);
        //状态初始化
        this.isControl = true;
        this.isStart = false;
        Tools.setTransRotEulerY(this.model.transform, 0)
        // Laya.timer.loop(1000, this, () => {
        //     FxController.playEffectPositon("Expolsion", this.model.transform.position.clone(), 1000);
        //     GameMgr.ConsoleLog(this.model.transform.position.clone())
        // })
    }
    CarSpecialChange() {
        // this.user.addComponent(MachineComponent_AI);
        // this.user.getComponent(MachineComponent_AI).playerId = this.playerId;
        // this.user.getComponent(MachineComponent_AI).init(this.skinId);
    }
    isGone = false
    CarStartSpecial() {
        FxController.playEffectPositon("Expolsion", this.model.transform.position.clone(), 1000);
        this.colliderComp.active = false;
        this.colliderComp.destroy()
        this.controlComp = null;
        this.tracker.destroy();
        this.circuit.destroy();
        this.destroy();
        this.model.destroy();
        this.isGone = true;
    }

    specialUpdate() {
        let comp = Data.player.getComponent(CarComponent);
        if (comp && !this.isGone) {
            if (comp.tracker.progressDistance > this.tracker.progressDistance + 100) {
                this.reset();
            }
        }
    }




    //重置位置，并初始化
    reset() {
        this.controlComp = null;
        this.colliderComp.destroy();
        Laya.timer.once(1500, this, () => {
            if (Data.player.getComponent(CarComponent) && !this.isGone) {
                let nowRange = this.tracker.progressDistance;
                for (let i = 0; i < NimoForFrame.npcs.length; i++) {
                    let comp = NimoForFrame.npcs[i].getComponent(CarComponent_NPC);
                    if (comp) {
                        if (comp.tracker.progressDistance > nowRange) {
                            nowRange = comp.tracker.progressDistance
                        }
                    }
                }
                if (nowRange == this.tracker.progressDistance) {
                    GameMgr.ConsoleLog("障碍物回收")
                    this.colliderComp.active = false;
                    this.colliderComp.destroy()
                    this.controlComp = null;
                    this.tracker.destroy();
                    this.circuit.destroy();
                    this.destroy();
                    this.model.destroy();
                    this.isGone = true;
                    return
                }
                nowRange += Tools.randomNumber(250, 350);
                let targetRange = this.circuit.accumulateDistance - 1000;
                if (nowRange >= targetRange) {
                    GameMgr.ConsoleLog("障碍物回收")
                    this.colliderComp.active = false;
                    this.colliderComp.destroy()
                    this.controlComp = null;
                    this.tracker.destroy();
                    this.circuit.destroy();
                    this.destroy();
                    this.model.destroy();
                    this.isGone = true;
                    return
                } else {
                    this.init(this.circuit, nowRange, this.horizontal, this.skinId)
                    this.getControlComp();
                    this.getColliderComp();
                }
            } else {
                GameMgr.ConsoleLog("障碍物回收")
                this.colliderComp.active = false;
                this.colliderComp.destroy()
                this.controlComp = null;
                this.tracker.destroy();
                this.circuit.destroy();
                this.destroy();
                this.model.destroy();
                this.isGone = true;
            }
        })
    }
    HitFlyEndSpecial() {
        this.reset();
    }
}