import { EventType } from "../../configs/EnumType";
import Data from "../../extensionTools/mods_Nimo/Data";
import NimoForFrame from "../../extensionTools/mods_Nimo/NimoForFrame";
import GameMgr from "../../managers/GameMgr";

export default class HpShow extends Laya.Script {

    static instance: HpShow;

    hpUI: Laya.Image[] = [];
    hpUIMasks: Laya.Image[] = [];

    valueMaskWidth: number = 280;

    onEnable() {
        HpShow.instance = this;
        for (let i = 0; i < this.owner.numChildren; i++) {
            let player = this.owner.getChildAt(i) as Laya.Image;
            this.hpUI.push(player);
            let value: Laya.Image = player.getChildByName("valueBack").getChildByName("value") as Laya.Image;
            let mask = value.mask as Laya.Image;
            this.hpUIMasks.push(mask);
            this.hpChange(new hpData(i, NimoForFrame.maxHpValues[i],true))
        }
        GameMgr.Event.on(EventType.Update_HpUI_Event, this, this.hpChange)
    }

    hpChange(data: hpData) {
        if (!data.close) {
            let image = this.hpUI[data.playerId]
            image.set_visible(true);
            let axis = data.hpValue / NimoForFrame.maxHpValues[data.playerId] * this.valueMaskWidth
            this.hpUIMasks[data.playerId].x = -260 + axis;
        } else {
            let image = this.hpUI[data.playerId]
            image.set_visible(false);
        }

    }
}
export class hpData {
    close = false;
    playerId;
    hpValue
    constructor(playerId, hpValue, close = false) {
        this.playerId = playerId;
        this.hpValue = hpValue;
        this.close = close;
    }
}