export default class BitMapFntTool {
    private static fntRes = ["Fnt/Attack.fnt","Fnt/BigAttack.fnt"];
    public static fntName = ["Attack","BigAttack"]; //与路径个数一致
    public static bitMapFnts = new Array<Laya.BitmapFont>();
    private static count = 0;

    public static LoadFnt()  {
        for (var i = 0; i < this.fntRes.length; i++)  {
            var bitmapfnt = new Laya.BitmapFont();
            this.bitMapFnts.push(bitmapfnt);
            bitmapfnt.loadFont(this.fntRes[i], new Laya.Handler(this, this.OnLoaded, [i]))
        }
    }

    private static OnLoaded(index: number)  {
        Laya.Text.registerBitmapFont(this.fntName[index], this.bitMapFnts[index]);
        this.count++;
        console.log("加载字体完成 " + this.fntName[index]);
    }
    
}